import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import BaseService from "../../../../services/BaseService";
import "./style.scss";
import BannerService from "../../../../services/BannerService";
import { AvField, AvForm } from "availity-reactstrap-validation";

const Banner = () => {
	const [formData, setFormData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [deleteLoading, setDeleteLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const bannerService = new BannerService();
	useEffect(() => {
		bannerService.getList().then((res) => {
			if (res?.status) {
				if (!!res?.data?.data?.length) {
					setFormData(res?.data?.data);
				} else {
					setFormData([{ image: "", textAr: "", textEn: "" }]);
				}
			}
		});
	}, [shouldUpdate]);

	const onSubmit = (banner) => {
		let data = {
			banners: [
				{
					image: banner?.image,
					textAr: banner?.textAr,
					textEn: banner?.textEn,
				},
			],
		};

		if (!!banner?.id) {
			setLoading(true);
			bannerService.update(data).then((response) => {
				if (response?.status === 200) {
					toast.success(Translate[lang].updated_successfully);
					setShouldUpdate((prev) => !prev);
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			bannerService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(
						`${Translate[lang].added} ${Translate[lang].successfully}`
					);
					setShouldUpdate((prev) => !prev);
				}
				setLoading(false);
			});
		}
	};

	const deleteBanner = (banner) => {
		setDeleteLoading(true);
		bannerService.remove(banner?.id).then((response) => {
			if (response?.status) {
				toast.success(response?.data?.message);
				setShouldUpdate((prev) => !prev);
			}
			setDeleteLoading(false);
		});
	};

	const fileHandler = (e, index) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		if (filesData?.length) {
			setLoading(true);
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData((prev) =>
							prev?.map((response, ind) => {
								if (ind === index) {
									return {
										...response,
										image: res?.data?.data?.uploadedFiles[0]?.url,
									};
								} else {
									return response;
								}
							})
						);
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<>
			<div style={{ margin: "12px 0 32px" }}>
				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{Translate[lang]?.banners}
				</label>
			</div>
			<Row>
				<Col md={10}>
					<div>
						{formData?.map((banner, index) => {
							return (
								<AvForm onValidSubmit={() => onSubmit(banner)} key={index}>
									<div
										className="row mb-3 mx-2"
										style={{
											border: "1px solid rgb(205, 206, 215)",
											padding: "8px 0",
											borderRadius: "16px",
										}}>
										<div className="col-lg-12 col-sm-12">
											<div className="form-group position-relative w-100">
												<i
													className="la la-times remove-banner"
													onClick={() => {
														if (banner?.id) {
															deleteBanner(banner);
														} else {
															let update = formData?.filter(
																(_, ind) => ind !== index
															);
															setFormData(update);
														}
													}}
												/>
												<div className="banner">
													<div className="banner-img">
														{!banner?.image && (
															<svg
																width="24"
																height="24"
																viewBox="0 0 24 24"
																fill="none"
																xmlns="http://www.w3.org/2000/svg">
																<path
																	d="M9 22H15C20 22 22 20 22 15V9C22 4 20 2 15 2H9C4 2 2 4 2 9V15C2 20 4 22 9 22Z"
																	stroke="#004094"
																	strokeWidth="1.5"
																	strokeLinecap="round"
																	strokeLinejoin="round"
																/>
																<path
																	d="M9 10C10.1046 10 11 9.10457 11 8C11 6.89543 10.1046 6 9 6C7.89543 6 7 6.89543 7 8C7 9.10457 7.89543 10 9 10Z"
																	stroke="#004094"
																	strokeWidth="1.5"
																	strokeLinecap="round"
																	strokeLinejoin="round"
																/>
																<path
																	d="M2.66992 18.9486L7.59992 15.6386C8.38992 15.1086 9.52992 15.1686 10.2399 15.7786L10.5699 16.0686C11.3499 16.7386 12.6099 16.7386 13.3899 16.0686L17.5499 12.4986C18.3299 11.8286 19.5899 11.8286 20.3699 12.4986L21.9999 13.8986"
																	stroke="#004094"
																	strokeWidth="1.5"
																	strokeLinecap="round"
																	strokeLinejoin="round"
																/>
															</svg>
														)}
														{!!banner?.image && (
															<a
																href={banner?.image}
																target="_blank"
																rel="noreferrer"
																className="w-100 h-100">
																<img
																	src={banner?.image}
																	alt="banner"
																	className="w-100 h-100"
																/>
															</a>
														)}
													</div>
													<div>
														<span className="max">
															{Translate[lang]?.max_size} 100{" "}
															{Translate[lang]?.mbs} / 1440*585
														</span>
														<div
															className="d-flex mt-4"
															style={{
																gap: "24px",
																opacity: loading ? ".8" : "1",
															}}>
															{!banner?.image && (
																<label
																	className="custum-file-upload"
																	for="file">
																	<div className="icon">
																		<svg
																			width="25"
																			height="25"
																			viewBox="0 0 25 25"
																			fill="none"
																			xmlns="http://www.w3.org/2000/svg">
																			<path
																				d="M16.9405 9.39844C20.5405 9.70844 22.0105 11.5584 22.0105 15.6084V15.7384C22.0105 20.2084 20.2205 21.9984 15.7505 21.9984H9.24047C4.77047 21.9984 2.98047 20.2084 2.98047 15.7384V15.6084C2.98047 11.5884 4.43047 9.73844 7.97047 9.40844"
																				stroke="#FDFDFE"
																				strokeWidth="1.5"
																				strokeLinecap="round"
																				strokeLinejoin="round"
																			/>
																			<path
																				d="M12.5 15.5011V4.12109"
																				stroke="#FDFDFE"
																				strokeWidth="1.5"
																				strokeLinecap="round"
																				strokeLinejoin="round"
																			/>
																			<path
																				d="M15.8504 6.35L12.5004 3L9.15039 6.35"
																				stroke="#FDFDFE"
																				strokeWidth="1.5"
																				strokeLinecap="round"
																				strokeLinejoin="round"
																			/>
																		</svg>
																	</div>

																	<div>
																		<span className="text">
																			{Translate[lang].upload_image}
																		</span>
																	</div>
																	{!loading && (
																		<input
																			type="file"
																			accept="image/*"
																			onChange={(e) => {
																				if (!banner?.image)
																					fileHandler(e, index);
																			}}
																			className="upload"
																			name="Upload"
																		/>
																	)}
																</label>
															)}

															{!!banner?.image && (
																<button
																	disabled={deleteLoading}
																	className="delete-img"
																	type="button"
																	onClick={() => {
																		let update = formData?.map((res, ind) => {
																			if (ind === index) {
																				return {
																					...res,
																					image: "",
																				};
																			} else {
																				return res;
																			}
																		});
																		setFormData(update);
																	}}>
																	<i className="la la-trash" />
																	{Translate[lang].delete}
																</button>
															)}
														</div>
													</div>
												</div>
											</div>
										</div>
										<div className="col-lg-6 col-md-6 col-12">
											<AvField
												label={`${Translate[lang]?.english_title}`}
												name={`english_title${index}`}
												type="text"
												value={banner?.textEn}
												validate={{
													required: {
														value: true,
														errorMessage: Translate[lang].field_required,
													},
												}}
												placeholder={`${Translate[lang]?.english_title}`}
												onChange={(e) => {
													let update = formData?.map((res) => {
														return {
															...res,
															textEn: e.target?.value,
														};
													});
													setFormData(update);
												}}
											/>
										</div>
										<div className="col-lg-6 col-md-6 col-12">
											<AvField
												label={`${Translate[lang]?.arabic_title}`}
												name={`arabic_title${index}`}
												type="text"
												value={banner?.textAr}
												validate={{
													required: {
														value: true,
														errorMessage: Translate[lang].field_required,
													},
												}}
												placeholder={`${Translate[lang]?.arabic_title}`}
												onChange={(e) => {
													let update = formData?.map((res) => {
														return {
															...res,
															textAr: e.target?.value,
														};
													});
													setFormData(update);
												}}
											/>
										</div>
										<div className="col-12 text-right">
											<Button variant="primary" type="submit">
												{Translate[lang].submit}
											</Button>
										</div>
									</div>
								</AvForm>
							);
						})}
						<div className="mx-2">
							<Button
								variant="outline-primary"
								onClick={() =>
									setFormData((prev) => [
										...prev,
										{ image: "", textAr: "", textEn: "" },
									])
								}>
								{Translate[lang].add} {Translate[lang].banner}
							</Button>
						</div>
					</div>
				</Col>
			</Row>
		</>
	);
};

export default Banner;
