import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import OrganizationService from "../../../../services/OrganizationService";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import {
	ageGroupOptions,
	convertDate,
	daysOptions,
	daysValuesOptions,
	gendersUpOptions,
} from "../../../Enums/Fixed";
import EntertainmentSubjectsService from "../../../../services/EntertainmentSubjectsService";
import BranchesService from "../../../../services/BranchesService";
import BaseService from "../../../../services/BaseService";
import EntertainmentProgramsService from "../../../../services/EntertainmentProgramsService";
import HallService from "../../../../services/HallService";
import "../style.scss";
const AddEntertainmentProgram = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name: "",
		nameAr: "",
		age_group: "",
		subjects: "",
		branch: "",
		gender: "MALE",
		program_start_date: "",
		program_end_date: "",
		halls: "",
		time_from: "",
		time_to: "",
		illustration: "",
		accepted_years: "",
		image: "",
		dayPrice: "",
		weekPrice: "",
		monthPrice: "",
		group1: "",
		group2: "",
		hallDetails: [],
	});
	const [loading, setLoading] = useState(false);
	const [selectHall, setSelectHall] = useState("");
	const [activeHall, setActiveHall] = useState("");
	const [id, setId] = useState(null);
	const [daySelected, setDaySelected] = useState(null);
	const [subjectsOptions, setSubjectsOptions] = useState([]);
	const [branchesOptions, setBranchesOptions] = useState([]);
	const [hallsOptions, setHallsOptions] = useState([]);
	const [view, setView] = useState(false);
	const navigate = useNavigate();
	const entertainmentSubjectsService = new EntertainmentSubjectsService();
	const entertainmentProgramsService = new EntertainmentProgramsService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		new BranchesService().getList().then((res) => {
			if (res?.status) {
				let info = res?.data?.data?.map((sub) => {
					return {
						label: sub?.name,
						value: sub?.id,
					};
				});
				setBranchesOptions(info);
			}
		});
	}, []);

	useEffect(() => {
		if (!!formData?.age_group?.value) {
			let params = {
				gender: formData?.gender,
				ageGroup: formData?.age_group?.value,
				page: 1,
				limit: 10000,
			};
			entertainmentSubjectsService.getList(params).then((res) => {
				if (res?.status) {
					let info = res?.data?.data?.data
						?.filter((sub) => sub?.isActive)
						?.map((sub) => {
							return {
								label: sub?.name,
								value: sub?.id,
							};
						});
					setSubjectsOptions(info);
				}
			});
		}
	}, [formData?.gender, formData?.age_group]);

	useEffect(() => {
		if (!!formData?.branch?.value) {
			let params = {
				gender: formData?.gender,
				branchId: formData?.branch?.value,
				page: 1,
				limit: 10000,
			};
			new HallService().getEntertainmentHalls(params).then((res) => {
				if (res?.status) {
					let info = res?.data?.data?.map((sub) => {
						return {
							...sub,
							label: sub?.name,
							value: sub?.id,
						};
					});
					setHallsOptions(info);

					setSelectHall(
						info?.map((hall) => {
							return {
								hall: hall,
								subject: "",
							};
						})
					);
					setActiveHall(info[0]);
				}
			});
		}
	}, [formData?.gender, formData?.branch]);

	useEffect(() => {
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			let item = location.state;
			setId(item?.id);
			// entertainmentProgramsService.find(item?.id).then((res) => {
			// 	if (res?.status) {
			// 	}
			// });
			// setFormData({
			// })
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		// let assignments = [];
		// formData.hallDetails?.map((res) => {
		// 	res?.hallClasses?.map((info) => {
		// 		if (!!info?.subject?.value) {
		// 			assignments?.push({
		// 				entertainmentSubjectId: info?.subject?.value,
		// 				day: daysOptions
		// 					?.find((opt) => opt?.value === res?.day)
		// 					?.label?.slice(0, 3)
		// 					?.toLocaleUpperCase(),
		// 				hallClassId: info?.id,
		// 			});
		// 		}
		// 	});
		// });
		let data = {
			name: formData?.name,
			nameAr: formData?.nameAr,
			illustration: formData?.illustration,
			image: formData?.image,
			ageGroup: formData?.age_group?.value,
			gender: formData?.gender,
			branchId: formData?.branch?.value,
			hallId: selectHall[0]?.hall?.value,
			startDate: convertDate(formData?.program_start_date),
			endDate: convertDate(formData?.program_end_date),
			dayGroups: [
				{
					groupNumber: 1,
					days: formData?.group1?.map((res) =>
						res?.label?.slice(0, 3)?.toLocaleUpperCase()
					),
				},
				{
					groupNumber: 2,
					days: formData?.group2?.map((res) =>
						res?.label?.slice(0, 3)?.toLocaleUpperCase()
					),
				},
			],
			hallSubjects: selectHall
				?.filter((res) => res?.subject?.value)
				?.map((res) => {
					return {
						hallId: res?.hall?.value,
						subjectId: res?.subject?.value,
					};
				}),
			//  [
			// 	{
			// 		entertainmentSubjectId: "dfb17ed1-97e9-4e1c-88b5-92f215d8de0d",
			// 		day: "MON",
			// 		hallClassId: "ece54857-74e7-4003-ac72-20f959ba541d",
			// 	},
			// ],
			dayPrice: Number(formData?.dayPrice),
			weekPrice: Number(formData?.weekPrice),
			monthPrice: Number(formData?.monthPrice),
			// translations: [
			// 	{
			// 		languageId: "ar",

			// 	},
			// ],
		};

		if (!!id) {
			setLoading(true);
			entertainmentProgramsService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(Translate[lang].admin_updated_successfully);
					navigate("/entertainment", { state: "programs" });
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			entertainmentProgramsService.create(data).then((response) => {
				if (!!response?.status) {
					toast.success(
						`${Translate[lang].added} ${Translate[lang].admin} ${Translate[lang].successfully}`
					);
					navigate("/entertainment", { state: "programs" });
				}
				setLoading(false);
			});
		}
	};

	const fileHandler = (e, name) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							[name]: res?.data?.data?.uploadedFiles[0]?.url,
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/entertainment", { state: "programs" })}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{view
						? Translate[lang]?.view
						: !!id
						? Translate[lang]?.edit
						: Translate[lang]?.add}{" "}
					{Translate[lang]?.program}
				</label>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_name} (${Translate[lang]?.english})`}
									name="name"
									type="text"
									value={formData?.name}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_name} (${Translate[lang]?.arabic})`}
									name="nameAr"
									type="text"
									value={formData?.nameAr}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 add-gallery mb-3">
								<label className="m-0">{Translate[lang]?.illustration}</label>
								<div className="image-placeholder">
									<div className="avatar-edit">
										<input
											type="file"
											multiple
											onChange={(e) => fileHandler(e, "illustration")}
											id={`imageUpload0`}
										/>
										<label htmlFor={`imageUpload0`} name=""></label>
									</div>
									<div className="avatar-preview2 m-auto">
										<div id={`imagePreview`} className="images">
											{
												<>
													<i className="la la-plus-circle" />
													<span style={{ fontSize: "18px", fontWeight: "600" }}>
														{Translate[lang]?.add} {Translate[lang]?.image}
													</span>
												</>
											}
										</div>
									</div>
								</div>
								{!!formData?.illustration && (
									<div className="banner_image mt-2 mb-3 w-50">
										<img
											src={formData?.illustration}
											alt="subjectImage"
											className="w-100"
										/>
										<a
											href={formData?.illustration}
											target="_blank"
											rel="noreferrer">
											<i className="la la-eye" />
										</a>
										<i
											className="la la-trash"
											onClick={() => {
												setFormData({
													...formData,
													illustration: "",
												});
											}}
										/>
									</div>
								)}
							</div>
							<div className="col-lg-6 col-sm-6 add-gallery mb-3">
								<label className="m-0">{Translate[lang]?.image}</label>
								<div className="image-placeholder">
									<div className="avatar-edit">
										<input
											type="file"
											multiple
											onChange={(e) => fileHandler(e, "image")}
											id={`imageUpload1`}
										/>
										<label htmlFor={`imageUpload1`} name=""></label>
									</div>
									<div className="avatar-preview2 m-auto">
										<div className="images">
											{
												<>
													<i className="la la-plus-circle" />
													<span style={{ fontSize: "18px", fontWeight: "600" }}>
														{Translate[lang]?.add} {Translate[lang]?.image}
													</span>
												</>
											}
										</div>
									</div>
								</div>
								{!!formData?.image && (
									<div className="banner_image mt-2 mb-3 w-50">
										<img
											src={formData?.image}
											alt="subjectImage"
											className="w-100"
										/>
										<a href={formData?.image} target="_blank" rel="noreferrer">
											<i className="la la-eye" />
										</a>
										<i
											className="la la-trash"
											onClick={() => {
												setFormData({
													...formData,
													image: "",
												});
											}}
										/>
									</div>
								)}
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">
									{Translate[lang]?.age_group}
								</label>
								<Select
									value={formData?.age_group}
									name="age_group"
									placeholder={Translate[lang]?.select}
									options={ageGroupOptions}
									onChange={(e) =>
										setFormData({ ...formData, age_group: e, subjects: "" })
									}
								/>
							</div>
							{/* Gender */}
							<div className="col-lg-6 col-sm-6 ">
								<label className="d-block">{Translate[lang].gender}</label>
								<div className="d-flex gap-16">
									{gendersUpOptions?.map((res) => {
										return (
											<label className="d-flex gap-8 mt-2 cursor-pointer">
												<input
													type="radio"
													name="gender"
													checked={formData?.gender === res?.value}
													onChange={() =>
														setFormData({
															...formData,
															gender: res?.value,
															subjects: "",
														})
													}
												/>
												{res?.label}
											</label>
										);
									})}
								</div>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_start_date}`}
									name="program_start_date"
									type="date"
									value={formData?.program_start_date}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_start_date}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_end_date}`}
									name="program_end_date"
									type="date"
									value={formData?.program_end_date}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_end_date}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							{/* Subjects */}
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">
									{Translate[lang]?.subjects} ({Translate[lang]?.multi_select})
								</label>
								<Select
									value={formData?.subjects}
									name="subjects"
									placeholder={Translate[lang]?.select}
									options={!formData?.age_group ? [] : subjectsOptions}
									isMulti
									noOptionsMessage={() =>
										!formData?.age_group ? "Select Age Group First" : ""
									}
									onChange={(e) => setFormData({ ...formData, subjects: e })}
								/>
							</div>
							{/* Branch */}
							<div className="col-lg-6 col-sm-6 ">
								<label className="text-label">{Translate[lang]?.branch}</label>
								<Select
									value={formData?.branch}
									name="branch"
									placeholder={Translate[lang]?.select}
									options={branchesOptions}
									onChange={(e) => setFormData({ ...formData, branch: e })}
								/>
							</div>
							{/* Accepted Years */}
							{formData?.age_group?.value === "THREE_TO_FIVE" && (
								<div className="col-lg-6 col-sm-6 ">
									<label className="text-label">
										{Translate[lang]?.accepted_years}
									</label>
									<Select
										value={formData?.accepted_years}
										name="accepted_years"
										placeholder={Translate[lang]?.select}
										isMulti
										options={Array.from({ length: 41 }, (_, i) => {
											const year = 2000 + i;
											return { label: year, value: year };
										})}
										onChange={(e) =>
											setFormData({ ...formData, accepted_years: e })
										}
									/>
								</div>
							)}
							{formData?.age_group?.value === "THREE_TO_FIVE" && (
								<div className="col-6"></div>
							)}
							{/* Hall */}
							<div className="col-lg-12 col-sm-12 mb-3">
								{!!selectHall?.length && (
									<label className="text-label">{Translate[lang]?.halls}</label>
								)}

								<div className="tabs custom-grid">
									{!!selectHall?.length
										? selectHall?.map((hallSub) => {
												return (
													<p
														key={hallSub?.hall?.value}
														onClick={() => setActiveHall(hallSub?.hall)}
														className={
															hallSub?.hall?.value === activeHall?.value
																? "active"
																: ""
														}>
														{hallSub?.hall?.label}
													</p>
												);
										  })
										: ""}
								</div>
								{!!activeHall?.value && (
									<div>
										<label>{Translate[lang].assign_subject_to_hall}</label>
										<Select
											value={
												selectHall?.find(
													(res) => res?.hall?.value === activeHall?.value
												)?.subject
											}
											name={`hall_subject`}
											placeholder={Translate[lang]?.select}
											className="w-50"
											isMulti={formData?.age_group?.value === "THREE_TO_FIVE"}
											options={
												formData?.age_group?.value === "THREE_TO_FIVE"
													? formData?.subjects
													: !!formData?.subjects?.length
													? formData?.subjects?.filter(
															(item) =>
																!new Set(
																	selectHall.map((item) => item?.subject.value)
																).has(item.value)
													  )
													: []
											}
											onChange={(e) => {
												let update = selectHall?.map((res) => {
													if (res?.hall?.value === activeHall?.value) {
														return {
															...res,
															subject: e,
														};
													} else {
														return res;
													}
												});
												setSelectHall(update);
											}}
										/>
									</div>
								)}
							</div>
							{!!selectHall?.length && (
								<div
									className={
										formData?.age_group?.value === "THREE_TO_FIVE"
											? "col-9 mt-5"
											: "col-6 mt-5"
									}>
									<div>
										<Table responsive>
											<thead>
												<tr className="text-center">
													<th>
														<strong>{Translate[lang]?.hall}</strong>
													</th>
													<th>
														<strong>{Translate[lang]?.subject}</strong>
													</th>
													{formData?.age_group?.value === "THREE_TO_FIVE" && (
														<th>
															<strong>{Translate[lang]?.coach}</strong>
														</th>
													)}
												</tr>
											</thead>
											<tbody className="table-body">
												{selectHall?.map((item, index) => {
													return (
														<tr key={index} className="text-center">
															<td>{item?.hall?.label}</td>
															<td>{item?.subject?.label || "-"}</td>
															{formData?.age_group?.value ===
																"THREE_TO_FIVE" && (
																<td>{item?.hall?.coach || "-"}</td>
															)}
														</tr>
													);
												})}
											</tbody>
										</Table>
									</div>
								</div>
							)}

							{/* {!!formData?.hallDetails?.length && (
								<>
									<div className="col-12 mt-3">
										<p className="fs-20">
											{Translate[lang].select} {Translate[lang].days}
										</p>
										<div className="tabs">
											{formData?.hallDetails?.map((day, index) => {
												return (
													<p
														key={index}
														className={`${
															day?.day === daySelected ? "active" : ""
														} tab`}
														onClick={() => setDaySelected(day?.day)}>
														{
															daysOptions?.find(
																(res) => res?.value === day?.day
															)?.label
														}
													</p>
												);
											})}
										</div>
										<div className="hallClasses">
											{formData?.hallDetails
												?.find((res) => res?.day === daySelected)
												?.hallClasses?.map((hallClass, index) => {
													return (
														<div className="hallClass">
															<p className="details">
																<span>
																	{Translate[lang].class} {index + 1}
																</span>
																<span>
																	<i className="la la-clock"></i>
																	{Translate[lang].from} {hallClass?.timeFrom}
																</span>
																<span>
																	<i className="la la-clock"></i>
																	{Translate[lang].to} {hallClass?.timeTo}
																</span>
															</p>
															<div style={{ width: "15rem" }}>
																<Select
																	value={hallClass?.subject}
																	name={`hall_subject${index}`}
																	placeholder={Translate[lang]?.select}
																	options={formData?.subjects}
																	onChange={(e) => {
																		let update = formData?.hallDetails?.map(
																			(info) => {
																				if (info?.day === daySelected) {
																					return {
																						...info,
																						hallClasses: info?.hallClasses?.map(
																							(classInfo, ind) => {
																								if (index === ind) {
																									return {
																										...classInfo,
																										subject: e,
																									};
																								} else {
																									return classInfo;
																								}
																							}
																						),
																					};
																				} else {
																					return info;
																				}
																			}
																		);
																		setFormData({
																			...formData,
																			hallDetails: update,
																		});
																	}}
																/>
															</div>
														</div>
													);
												})}
										</div>
									</div>
								</>
							)} */}

							<div className="col-12">
								<hr />
							</div>
							<div className="col-lg-6 col-sm-6">
								<label className="text-label">{Translate[lang]?.group} 1</label>
								<Select
									value={formData?.group1}
									name="group1"
									placeholder={Translate[lang]?.select}
									options={
										!!formData?.group2?.length
											? daysValuesOptions?.filter(
													(item) =>
														!new Set(
															formData?.group2?.map((item) => item?.value)
														)?.has(item?.value)
											  )
											: daysValuesOptions
									}
									isMulti
									onChange={(e) => setFormData({ ...formData, group1: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<label className="text-label">{Translate[lang]?.group} 2</label>
								<Select
									value={formData?.group2}
									name="group2"
									placeholder={Translate[lang]?.select}
									options={
										!!formData?.group1?.length
											? daysValuesOptions?.filter(
													(item) =>
														!new Set(
															formData?.group1?.map((item) => item?.value)
														)?.has(item?.value)
											  )
											: daysValuesOptions
									}
									isMulti
									onChange={(e) => setFormData({ ...formData, group2: e })}
								/>
							</div>

							<div className="col-12 mt-4">
								<hr />
								<p>{Translate[lang].package_price}</p>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.day_price}`}
									name="dayPrice"
									type="number"
									value={formData?.dayPrice}
									disabled={view}
									min={0}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.day_price}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.week_price}`}
									name="weekPrice"
									type="number"
									value={formData?.weekPrice}
									disabled={view}
									min={0}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.week_price}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.month_price}`}
									name="monthPrice"
									type="number"
									value={formData?.monthPrice}
									disabled={view}
									min={0}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.month_price}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-6"></div>

							{/* Buttons */}
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								{view && (
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/halls")}>
										{Translate[lang]?.cancel}
									</Button>
								)}
								{!view && (
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								)}
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddEntertainmentProgram;
