import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../../common/DeleteModal";
import { Translate } from "../../../../Enums/Tranlate";
import { Link, useNavigate } from "react-router-dom";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import viewIcon from "../../../../../images/icons/eye.svg";
import HallService from "../../../../../services/HallService";
import POSService from "../../../../../services/POSService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setView,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const hallService = new HallService();
	const navigate = useNavigate();

	return (
		<tr key={index}>
			<td>{item?.name}</td>
			<td>{item?.email}</td>
			<td>{item?.branchName}</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item?.isActive ? "success" : "danger"} light`}>
					{item?.isActive ? Translate[lang].active : Translate[lang].deactive}
				</Badge>
			</td>
			<td>
				<div className="d-flex justify-content-center" style={{ gap: "20px" }}>
					<Link
						to={`/pos/edit`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img
							src={editIcon}
							className="cursor-pointer"
							alt="edit"
							width={24}
						/>
					</Link>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={new POSService()}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
