import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import { Link, useNavigate } from "react-router-dom";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import viewIcon from "../../../../../images/icons/eye.svg";
import DeleteModal from "../../../../common/DeleteModal";
import TeacherManagementService from "../../../../../services/TeacherManagementService";

const CardItem = ({ item, index, setShouldUpdate }) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const teacherManagementService = new TeacherManagementService();
	const navigate = useNavigate();

	return (
		<tr key={index}>
			<td>{lang === "en" ? item?.name : item?.nameAr}</td>
			<td>
				{item?.zones
					?.map((res) => res?.zoneToZoneGroup?.zoneGroup?.name)
					.join(", ")}
			</td>
			<td>{item?.subjects?.map((res) => res?.subject?.name).join(", ")}</td>
			<td>{item?.grades?.join(", ")}</td>
			<td>
				<div className="d-flex justify-content-center" style={{ gap: "20px" }}>
					<Link
						to={(`/teachers/view`, { state: item })}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img src={viewIcon} alt="view" width={24} />
					</Link>
					<Link
						to={`/teachers/edit`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img
							src={editIcon}
							className="cursor-pointer"
							alt="edit"
							width={24}
						/>
					</Link>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={teacherManagementService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
