import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row, Table } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import EntertainmentSubjectsService from "../../../../services/EntertainmentSubjectsService";
import "./style.scss";
import SubjectsService from "../../../../services/SubjectsService";
import {
	ageGroupOptions,
	daysOptions,
	daysValuesOptions,
} from "../../../Enums/Fixed";
import EntertainmentCategoriesService from "../../../../services/EntertainmentCategoriesService";

const TableModal = ({ addModal, setAddModal, item, setShouldUpdate }) => {
	const [formData, setFormData] = useState({});
	const [activeTab, setActiveTab] = useState(1);
	const [activeTable, setActiveTable] = useState([]);
	const lang = useSelector((state) => state.auth.lang);
	const entertainmentSubjectsService = new EntertainmentSubjectsService();

	useEffect(() => {
		entertainmentSubjectsService.getTable(item?.id).then((res) => {
			if (res?.status) {
				setFormData(res?.data.data);
				setActiveTable(res?.data.data?.dayGroup1Tables);
			}
		});
	}, [item]);

	const submit = (e) => {};

	return (
		<Modal
			className={lang === "en" ? "en fade table-modal" : "ar fade table-modal"}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-3">
							<div className="d-flex justify-content-between">
								<p className="mb-0">{formData?.program?.name}</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="tabs">
								<p
									className={activeTab === 1 ? "active" : ""}
									onClick={() => {
										setActiveTab(1);
										setActiveTable(formData?.dayGroup1Tables);
									}}>
									Group 1
								</p>
								<p
									className={activeTab === 2 ? "active" : ""}
									onClick={() => {
										setActiveTab(2);
										setActiveTable(formData?.dayGroup2Tables);
									}}>
									Group 2
								</p>
							</div>
						</Col>
						<Col md={12}>
							{activeTable?.map((actTable) => {
								return (
									<Table responsive className="mb-5">
										<thead>
											<th className="days-th">{Translate[lang]?.days}</th>
											{actTable?.days[0]?.schedule?.map((res) => {
												if (res?.classNumber !== -1) {
													return (
														<>
															<th className="class-th">
																<span
																	style={{ fontWeight: "500" }}
																	className="d-block text-center text-black">
																	Class {res?.classNumber}
																</span>
																<span
																	style={{ fontWeight: "500" }}
																	className="d-block text-center text-black">
																	{res?.hall?.name}
																</span>
																<span
																	style={{ fontWeight: "500" }}
																	className="d-block text-center text-black">
																	{res?.timeFrom} - {res?.timeTo}
																</span>
															</th>
															{res?.classNumber ===
																Number(
																	actTable?.days[0]?.schedule?.find(
																		(sc) => sc?.isBreak
																	)?.hall?.hallNo
																) && (
																<th className="break-th">
																	<strong>{Translate[lang]?.break}</strong>
																</th>
															)}
														</>
													);
												} else {
													return <></>;
												}
											})}
										</thead>
										<tbody>
											{actTable?.days?.map((res) => {
												return (
													<tr className="text-center">
														<td className="days">
															{
																daysValuesOptions?.find(
																	(da) => da?.value === res?.day
																)?.label
															}
														</td>
														{res?.schedule?.map((schedule) => {
															if (schedule?.classNumber !== -1) {
																return (
																	<>
																		<td className="subjects">
																			{schedule?.subject?.name}
																		</td>
																		{schedule?.classNumber ===
																			Number(
																				res?.schedule?.find((sc) => sc?.isBreak)
																					?.hall?.hallNo
																			) && (
																			<td className="break">
																				{
																					res?.schedule?.find(
																						(sc) => sc?.isBreak
																					).timeTo
																				}
																			</td>
																		)}
																	</>
																);
															} else {
																return <></>;
															}
														})}
													</tr>
												);
											})}
										</tbody>
									</Table>
								);
							})}
						</Col>
					</Row>
				</Modal.Body>
				{/* <Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{Translate[lang].category}
					</Button>
				</Modal.Footer> */}
			</AvForm>
		</Modal>
	);
};

export default TableModal;
