import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import EntertainmentSubjectsService from "../../../../services/EntertainmentSubjectsService";
import { ageGroupOptions, gendersUpOptions } from "../../../Enums/Fixed";
import "../style.scss";

const AddSubjectsModal = ({ addModal, setAddModal, item, setShouldUpdate }) => {
	const [formData, setFormData] = useState({
		age_group: [],
		name: "",
		nameAr: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const entertainmentSubjectsService = new EntertainmentSubjectsService();

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				name: item?.name,
				isActive: item?.isActive,
				nameAr: item?.translations?.find((res) => res?.languageId === "ar")
					.name,
				age_group: ageGroupOptions?.find(
					(res) => res?.value === item?.ageGroups
				),
				gender: gendersUpOptions?.find((res) => res?.value === item?.gender),
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			ageGroups: formData?.age_group?.value,
			gender: formData?.gender?.value,
			isActive: formData?.isActive,
			translations: [
				{
					languageId: "ar",
					name: formData?.nameAr,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			entertainmentSubjectsService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`Subject Added Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			entertainmentSubjectsService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`Subject Updated Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.subject}
								</p>

								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>
						<Col md={6}>
							<AvField
								label={`${Translate[lang]?.name} (${Translate[lang].english})`}
								name="name"
								type="text"
								value={formData?.name}
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								placeholder={`${Translate[lang]?.name}`}
								onChange={(e) =>
									setFormData({ ...formData, name: e.target.value })
								}
							/>
						</Col>
						<Col md={6}>
							<AvField
								label={`${Translate[lang]?.name} (${Translate[lang].arabic})`}
								name="nameAr"
								type="text"
								value={formData?.nameAr}
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								placeholder={`${Translate[lang]?.name}`}
								onChange={(e) =>
									setFormData({ ...formData, nameAr: e.target.value })
								}
							/>
						</Col>

						<Col md={6} className="mb-3">
							<label className="text-label">{Translate[lang]?.gender}</label>
							<Select
								value={formData?.gender}
								name="gender"
								placeholder={Translate[lang]?.select}
								options={gendersUpOptions}
								onChange={(e) => setFormData({ ...formData, gender: e })}
							/>
						</Col>
						<Col md={12} className="mt-1">
							<label className="text-label">{Translate[lang]?.age_group}</label>
							<div className="d-flex gap-32">
								{ageGroupOptions?.map((res) => {
									return (
										<label className="d-flex gap-8">
											<input
												type="checkbox"
												name="age_group"
												checked={formData?.age_group?.includes(res?.value)}
												onChange={() => {
													if (formData?.age_group?.includes(res?.value)) {
														setFormData({
															...formData,
															age_group: formData?.age_group?.filter(
																(group) => group !== res?.value
															),
														});
													} else {
														setFormData({
															...formData,
															age_group: [...formData?.age_group, res?.value],
														});
													}
												}}
											/>
											{res?.label}
										</label>
									);
								})}
							</div>
							{/* <Select
								value={formData?.age_group}
								name="age_group"
								placeholder={Translate[lang]?.select}
								options={ageGroupOptions}
								onChange={(e) => setFormData({ ...formData, age_group: e })}
							/> */}
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang].subject}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddSubjectsModal;
