import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import { convertDate, daysOptions } from "../../../../Enums/Fixed";
import ExecutivePlanService from "../../../../../services/ExecutivePlanService";
import kuwait from "../../../../../images/kwait.svg";
import usa from "../../../../../images/usa.svg";
import uk from "../../../../../images/uk.svg";

const AddExecutivePlan = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		education_type: "kwt",
		number_of_semester: "",
		academic_year: "",
		semesters: [],
		semester1: {
			number_of_semester: "",
			start_date: "",
			end_date: "",
			exam_start_date: "",
			exam_end_date: "",
			final_start_date: "",
			final_end_date: "",
		},
		semester2: {
			number_of_semester: "",
			start_date: "",
			end_date: "",
			exam_start_date: "",
			exam_end_date: "",
			final_start_date: "",
			final_end_date: "",
		},
		semester3: {
			number_of_semester: "",
			start_date: "",
			end_date: "",
			exam_start_date: "",
			exam_end_date: "",
			final_start_date: "",
			final_end_date: "",
		},
	});
	const [type, setType] = useState(0);
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const [branchesOptions, setBranchesOptions] = useState([]);
	const [coachesOptions, setCoachesOptions] = useState([]);
	const navigate = useNavigate();
	const executivePlanService = new ExecutivePlanService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			let item = location.state;
			// setFormData({
			// 	id: item?.id,
			// 	name: response?.name,
			// 	hall_no: response?.hallNo,
			// 	program_type: programType?.find(
			// 		(res) => res?.value === item?.programType
			// 	),
			// 	coach: !!response?.coach?.id
			// 		? {
			// 				label: response?.coach?.name,
			// 				value: response?.coach?.id,
			// 		  }
			// 		: "",
			// 	branch: {
			// 		label: response?.branch?.name,
			// 		value: response?.branch?.id,
			// 	},
			// 	gender: genderOptions?.find(
			// 		(res) => res?.value === response?.gender
			// 	),
			// 	number_of_classes: response?.hallClasses?.length,
			// 	time_from: response?.timeFrom,
			// 	time_to: response?.timeTo,
			// 	enable_capacity: response?.enableCapacity,
			// 	capacity: response?.capacity,
			// 	capacity_assumption: response?.capacityAssumption || "",
			// 	days: response?.days?.map((day) =>
			// 		daysOptions?.find((res) => res?.value === day)
			// 	),
			// 	hall_classification: response?.classification,
			// 	isActive: response?.status === "ACTIVE",
			// 	classes: response?.hallClasses?.map((res) => ({
			// 		from: res?.timeFrom,
			// 		to: res?.timeTo,
			// 		interval: res?.intervalBetweenClass,
			// 	})),
			// });
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		let data = {
			educationType: formData?.education_type?.toLocaleUpperCase(),
			numberOfSemesters: Number(formData?.number_of_semester),
			year: formData?.academic_year,
			semesters: formData?.semesters?.map((res, index) => {
				return {
					semesterNumber: Number(index + 1),
					nameEn: res?.name_of_semester,
					nameAr: res?.name_of_semester_ar,
					startDate: convertDate(res?.start_date),
					endDate: convertDate(res?.end_date),
					examNightsStartDate: convertDate(res?.exam_start_date),
					examNightsEndDate: convertDate(res?.exam_end_date),
					finalRevisionStartDate: convertDate(res?.final_start_date),
					finalRevisionEndDate: convertDate(res?.final_end_date),
				};
			}),
		};

		if (!!id) {
			setLoading(true);
			executivePlanService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response.data?.message);
					navigate("/teacher/executive-plan");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			executivePlanService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response.data?.message);
					navigate("/teacher/executive-plan");
				}
				setLoading(false);
			});
		}
	};

	const handleSemesters = (e, nameType, index) => {
		let update = formData?.semesters?.map((res, ind) => {
			if (index === ind) {
				return {
					...res,
					[nameType]: e.target?.value,
				};
			} else {
				return res;
			}
		});
		setFormData({
			...formData,
			semesters: update,
		});
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/executive-plan")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{!!id ? Translate[lang]?.edit : Translate[lang]?.add}{" "}
					{Translate[lang]?.academic_year}
				</label>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-12 mb-4">
								<p className="fs-20 mb-4 text-black fw-500">
									{Translate[lang]?.choose_education_type}
								</p>
								<div className="row">
									<div className="col-4">
										<div
											onClick={() =>
												setFormData({ ...formData, education_type: "kwt" })
											}
											className={`${
												formData?.education_type === "kwt" ? "active" : ""
											} border-gray br-10 p-3 cursor-pointer text-center d-flex gap-16 align-items-center justify-content-center`}>
											<img
												src={kuwait}
												width={64}
												className="br-50"
												alt="icon"
											/>
											<p className="fs-20 fw-500 text-black m-0">
												{Translate[lang].kuwait}
											</p>
										</div>
									</div>
									<div className="col-4">
										<div
											onClick={() =>
												setFormData({ ...formData, education_type: "british" })
											}
											className={`${
												formData?.education_type === "british" ? "active" : ""
											} border-gray br-10 p-3 cursor-pointer text-center d-flex gap-16 align-items-center justify-content-center`}>
											<img src={uk} width={64} className="br-50" alt="icon" />
											<p className="fs-20 fw-500 text-black m-0">
												{Translate[lang].british}
											</p>
										</div>
									</div>
									<div className="col-4">
										<div
											onClick={() =>
												setFormData({ ...formData, education_type: "american" })
											}
											className={`${
												formData?.education_type === "american" ? "active" : ""
											} border-gray br-10 p-3 cursor-pointer text-center d-flex gap-16 align-items-center justify-content-center`}>
											<img src={usa} width={64} className="br-50" alt="icon" />
											<p className="fs-20 fw-500 text-black m-0">
												{Translate[lang].american}
											</p>
										</div>
									</div>
								</div>
							</div>
							<div className="col-12">
								<p className="fs-18 mb-4 text-black fw-500">
									{Translate[lang]?.semester_info}
								</p>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.number_of_semester}`}
									name="number_of_semester"
									type="text"
									value={formData?.number_of_semester}
									max={8}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.number_of_semester}`}
									onChange={(e) => {
										if (e.target.value < 8) {
											setFormData({
												...formData,
												number_of_semester: e.target.value,
												semesters: Array(Number(e.target?.value))
													.fill("")
													.map((_) => {
														return {
															name_of_semester: "",
															name_of_semester_ar: "",
															start_date: "",
															end_date: "",
															exam_start_date: "",
															exam_end_date: "",
															final_start_date: "",
															final_end_date: "",
														};
													}),
											});
										}
									}}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.academic_year}`}
									name="academic_year"
									type="number"
									value={formData?.academic_year}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.academic_year}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							{!!formData?.number_of_semester && (
								<div className="col-12 mt-3 mb-2">
									<p className="fs-18 m-0 text-black fw-500">
										{Translate[lang]?.semester_details}
									</p>
								</div>
							)}
							{!!formData?.number_of_semester && (
								<div className="col-12">
									<div className="tabs-div">
										{formData?.semesters?.map((_, index) => {
											return (
												<p
													onClick={() => setType(index)}
													className={type === index ? "activeP" : ""}>
													{Translate[lang].semeseter} {index + 1}
												</p>
											);
										})}
									</div>
								</div>
							)}
							{!!formData?.number_of_semester && (
								<React.Fragment>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.name_of_semester} (${Translate[lang]?.english})`}
											name={`name_${type}`}
											type="text"
											value={formData?.semesters[type]?.name_of_semester}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.name_of_semester}`}
											onChange={(e) =>
												handleSemesters(e, "name_of_semester", type)
											}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.name_of_semester} (${Translate[lang]?.arabic})`}
											name={`nameAr_${type}`}
											type="text"
											value={formData?.semesters[type]?.name_of_semester_ar}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.name_of_semester}`}
											onChange={(e) =>
												handleSemesters(e, "name_of_semester_ar", type)
											}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.start_date}`}
											name={`start_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.start_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.start_date}`}
											onChange={(e) => handleSemesters(e, "start_date", type)}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.end_date}`}
											name={`end_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.end_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											min={formData?.semesters[type]?.start_date}
											onChange={(e) => handleSemesters(e, "end_date", type)}
										/>
									</div>
									<div className="col-12 mb-2">
										<p className="fs-18 m-0 fw-500">
											{Translate[lang]?.exam_nights}
										</p>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.start_date}`}
											name={`exam_start_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.exam_start_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											onChange={(e) =>
												handleSemesters(e, "exam_start_date", type)
											}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.end_date}`}
											name={`exam_end_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.exam_end_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											min={formData?.semesters[type]?.exam_start_date}
											onChange={(e) =>
												handleSemesters(e, "exam_end_date", type)
											}
										/>
									</div>
									<div className="col-12 mb-2">
										<p className="fs-18 m-0 fw-500">
											{Translate[lang]?.final_revision}
										</p>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.start_date}`}
											name={`final_start_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.final_start_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											onChange={(e) =>
												handleSemesters(e, "final_start_date", type)
											}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.end_date}`}
											name={`final_end_date_${type}`}
											type="date"
											value={formData?.semesters[type]?.final_end_date}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											min={formData?.semesters[type]?.final_start_date}
											onChange={(e) =>
												handleSemesters(e, "final_end_date", type)
											}
										/>
									</div>
								</React.Fragment>
							)}

							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								<Button
									variant="primary"
									type="submit"
									className="w-100 fs-14"
									disabled={loading}>
									{Translate[lang]?.save}
								</Button>
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddExecutivePlan;
