import BaseService from "./BaseService";
import { API_BASE_URL_ENV } from "../jsx/common/common";
import http from "./HttpService";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/teacher/academic-year";

export default class ExecutivePlanService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}
	startAcademicYear(id, data) {
		return http.post(`${apiEndpoint}/${id}/start`, data);
	}
	finishAcademicYear(id, data) {
		return http.post(`${apiEndpoint}/${id}/finish`, data);
	}
}
