import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import DepartmentService from "../../../../../services/DepartmentService";
import DeleteModal from "../../../../common/DeleteModal";
import { Translate } from "../../../../Enums/Tranlate";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setViewModal,
	setAssign,
	type,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const departmentService = new DepartmentService();

	return (
		<tr key={index} className="text-center">
			<td>{item?.name}</td>
			<td>
				{type === "departments" ? (
					<button
						className="btn btn-outline-primary d-flex align-items-center"
						onClick={() => {
							setAssign(true);
							setAddModal(true);
							setItem(item);
						}}
						style={{ gap: "8px" }}>
						<i className="la la-plus" />
						{Translate[lang].assign_manager}
					</button>
				) : (
					"Mohanned"
				)}
			</td>
			<td>{item?.organization?.name}</td>
			<td>
				{type === "units" ? (
					<button
						onClick={() => {
							setAssign(true);
							setAddModal(true);
							setItem(item);
						}}
						className="btn btn-outline-primary d-flex align-items-center"
						style={{ gap: "8px" }}>
						<i className="la la-plus" />
						{Translate[lang].assign_manager}
					</button>
				) : (
					0
				)}
			</td>
			<td>{item.number_of_people || 0}</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item.isActive ? "success" : "danger"} light`}>
					{item?.isActive ? Translate[lang].active : Translate[lang].deactive}
				</Badge>
			</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<img
						src={editIcon}
						onClick={() => {
							setItem(item);
							setAddModal(true);
							setAssign(false);
						}}
						className="cursor-pointer"
						alt="edit"
						width={24}
					/>
					<img
						src={trashIcon}
						onClick={() => setDeleteModal(true)}
						className="cursor-pointer"
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={departmentService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
