import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import ProductService from "../../../../../services/ProductService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import "../style.scss";

const Service = ({ isActive, setIsActive }) => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name_en: "",
		name_ar: "",
		cost: "",
		fees: "",
		type: "With QTY",
		city: "",
		add_on_group: false,
		isActive: true,
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const navigate = useNavigate();
	const productService = new ProductService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;

		if (location?.pathname.includes("edit")) {
			setId(item?.id);
			productService.find(location?.state?.id).then((res) => {
				if (res?.status === 200) {
					const response = res?.data?.data;
					setIsActive(response?.isActive);
					setId(location?.state?.id);
					setFormData({
						name_en: response?.name,
						name_ar: response?.translations?.find(
							(trans) => trans?.languageId === "ar"
						)?.name,
						cost: response?.cost,
						fees: response?.fees,
						type: response?.withQty ? "With QTY" : "Free of QTY",
						qty: response?.qty,
						add_on_group: response?.addOnGroup,
					});
				}
			});
		} else {
			setId(null);
		}
	}, [location]);

	const onDigitalSubmit = (e) => {
		e.preventDefault();

		let data = {
			type: "SERVICE",
			name: formData?.name_en,
			description: "description",
			addOnGroup: formData?.add_on_group,
			isActive: isActive,
			cost: Number(formData?.cost),
			fees: Number(formData?.fees),
			withQty: formData?.type === "With QTY",
			translations: [
				{
					languageId: "ar", // Arabic language ID
					name: formData?.name_ar,
					description: "تفاصيل",
				},
			],
		};
		if (formData?.type === "With QTY") data["qty"] = Number(formData?.qty);

		if (!!id) {
			setLoading(true);
			productService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			productService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		}
	};

	return (
		<AvForm onValidSubmit={onDigitalSubmit}>
			<div className="row">
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.service_name} (${Translate[lang]?.english})`}
						name={`name_en`}
						type="text"
						value={formData?.name_en}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.service_name}
						onChange={(e) => {
							setFormData({ ...formData, name_en: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.service_name} (${Translate[lang]?.arabic})`}
						name={`name_ar`}
						type="text"
						value={formData?.name_ar}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.service_name}
						onChange={(e) => {
							setFormData({ ...formData, name_ar: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.cost}
						name={`cost`}
						type="number"
						value={formData?.cost}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.cost}
						onChange={(e) => {
							setFormData({ ...formData, cost: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.price} (${Translate[lang]?.fees})`}
						name={`fees`}
						type="number"
						value={formData?.fees}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.fees}
						onChange={(e) => {
							setFormData({ ...formData, fees: e.target.value });
						}}
					/>
				</div>
				<div className="col-12 d-flex gap-16 my-3">
					<label className="d-flex gap-8 cursor-pointer">
						<input
							type="radio"
							name="type"
							checked={formData?.type === "With QTY"}
							onChange={() => setFormData({ ...formData, type: "With QTY" })}
						/>
						{Translate[lang].with_qty}
					</label>
					<label className="d-flex gap-8 cursor-pointer">
						<input
							type="radio"
							name="type"
							checked={formData?.type === "Free of QTY"}
							onChange={() => setFormData({ ...formData, type: "Free of QTY" })}
						/>
						{Translate[lang].free_of_qty}
					</label>
				</div>
				{formData?.type === "With QTY" && (
					<div className="col-lg-6 col-sm-6">
						<AvField
							label={Translate[lang]?.qty}
							name={`qty`}
							type="number"
							value={formData?.qty}
							validate={{
								required: {
									value: true,
									errorMessage: Translate[lang].field_required,
								},
							}}
							placeholder={Translate[lang]?.qty}
							onChange={(e) => {
								setFormData({ ...formData, qty: e.target.value });
							}}
						/>
					</div>
				)}
				<div className="col-12">
					<hr />
				</div>
				<div className="col-12 mb-3">
					<label className="d-flex gap-8">
						<input
							type="checkbox"
							name="add_on_group"
							checked={formData?.add_on_group}
							onChange={(e) =>
								setFormData({ ...formData, add_on_group: e.target?.checked })
							}
							className="add-product-check"
						/>
						{Translate[lang].add_on_group}
					</label>
				</div>

				{!!id && (
					<div className="col-lg-6 col-sm-6 mt-2 mb-5">
						<Button
							variant="secondary"
							type="button"
							className="w-100 fs-14"
							onClick={() => navigate("/products")}>
							{Translate[lang]?.cancel}
						</Button>
					</div>
				)}
				<div className="col-lg-6 col-sm-6 mt-2 mb-5">
					<Button
						variant="primary"
						type="submit"
						className="w-100 fs-14"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</div>
			</div>
		</AvForm>
	);
};

export default Service;
