import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import POSService from "../../../../../services/POSService";
import EmployeesService from "../../../../../services/EmployeesService";
import { daysOptions } from "../../../../Enums/Fixed";
import BranchesService from "../../../../../services/BranchesService";
import PhoneInput from "react-phone-input-2";
import BaseService from "../../../../../services/BaseService";
import Loader from "../../../../common/Loader";

const AddPOS = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name: "",
		nameAr: "",
		email: "",
		phone: "",
		branch: "",
		image: "",
		number_of_employees: "",
		employees: [],
		isActive: true,
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const [branchesOptions, setBranchesOptions] = useState([]);
	const [employeesOptions, setEmployeesOptions] = useState([]);
	const navigate = useNavigate();
	const pOSService = new POSService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		new BranchesService().getList().then((res) => {
			if (res?.status) {
				let data = res?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setBranchesOptions(data);
			}
		});
		new EmployeesService().getList().then((res) => {
			if (res?.status) {
				let data = res?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setEmployeesOptions(data);
			}
		});
	}, []);

	useEffect(() => {
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			let item = location.state;
			pOSService.find(item?.id).then((res) => {
				if (res?.status) {
					let response = res?.data?.data;
					setId(item?.id);
					setFormData({
						id: item?.id,
						isActive: response?.isActive,
						name: response?.name,
						nameAr: response?.translations?.find(
							(trans) => trans?.languageId === "ar"
						)?.name,
						email: response?.email,
						phone: response?.phoneNumber,
						image: response?.imageUrl,
						branch: {
							label: response?.branch?.name,
							value: response?.branch?.id,
						},
						number_of_employees: response?.numberOfEmployees,
						employees: response?.employees?.map((emp) => {
							return {
								employee: {
									label: emp?.name,
									value: emp?.employeeId,
								},
								userName: emp?.username,
								password: emp?.password,
							};
						}),
					});
				}
			});
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		if (!formData?.branch) {
			return;
		}
		e.preventDefault();

		let data = {
			name: formData?.name,
			email: formData?.email,
			phoneNumber: formData?.phone,
			branchId: formData?.branch?.value,
			imageUrl: formData?.image,
			numberOfEmployees: Number(formData?.number_of_employees),
			isActive: formData?.isActive,
			translations: [
				{
					languageId: "ar",
					name: formData?.nameAr,
				},
			],
			employees: formData?.employees?.map((res) => {
				return {
					employeeId: res?.employee?.value,
					username: res?.userName,
					password: res?.password,
				};
			}),
		};

		if (!!id) {
			setLoading(true);
			pOSService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response.data?.message);
					navigate("/pos");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			pOSService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response.data?.message);
					navigate("/pos");
				}
				setLoading(false);
			});
		}
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							image: res?.data?.data?.uploadedFiles[0]?.url,
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/pos")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{view
						? Translate[lang]?.view
						: !!id
						? Translate[lang]?.edit
						: Translate[lang]?.add}{" "}
					{Translate[lang]?.pos}
				</label>

				<Form.Check
					type="switch"
					id="custom-switch"
					checked={formData?.isActive}
					onChange={(e) =>
						setFormData({ ...formData, isActive: e.target.checked })
					}
				/>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.name} (${Translate[lang].english})`}
									name="name"
									type="text"
									value={formData?.name}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.name} (${Translate[lang].arabic})`}
									name="nameAr"
									type="text"
									value={formData?.nameAr}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.email}`}
									name="email"
									type="email"
									value={formData?.email}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.email}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label>{Translate[lang]?.phone}</label>
								<PhoneInput
									country={"kw"}
									enableSearch={true}
									value={formData?.phone}
									onChange={(e) => setFormData({ ...formData, phone: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">{Translate[lang]?.branch}</label>
								<Select
									value={formData?.branch}
									name="branch"
									placeholder={Translate[lang]?.select}
									options={branchesOptions}
									onChange={(e) => setFormData({ ...formData, branch: e })}
								/>
							</div>
							<div className="col-lg-12 col-sm-12 mb-3">
								<div className="form-group w-100 add-gallery">
									<label className="m-0">{Translate[lang]?.image}</label>
									<div className="image-placeholder">
										<div className="avatar-edit">
											<input
												type="file"
												multiple
												onChange={(e) => fileHandler(e)}
												id={`imageUpload1`}
											/>
											<label htmlFor={`imageUpload1`} name=""></label>
										</div>
										<div className="avatar-preview2 m-auto">
											<div id={`imagePreview`} className="images">
												<>
													<i className="la la-plus-circle" />
													<span style={{ fontSize: "18px", fontWeight: "600" }}>
														{Translate[lang]?.add} {Translate[lang]?.image}
													</span>
												</>
												{loading && <Loader />}
											</div>
										</div>
									</div>
									{!!formData?.image && (
										<div className="row">
											<div className="banner_image w-auto">
												<img
													src={formData?.image}
													alt="bannerImage"
													className="w-100"
												/>
												<a
													href={formData?.image}
													target="_blank"
													rel="noreferrer">
													<i className="la la-eye" />
												</a>
												<i
													className="la la-trash"
													onClick={() => {
														setFormData({
															...formData,
															image: "",
														});
													}}
												/>
											</div>
										</div>
									)}
								</div>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.number_of_employees}`}
									name="number_of_employees"
									type="number"
									value={formData?.number_of_employees}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.number_of_employees}`}
									onChange={(e) => {
										let newNumber = parseInt(e.target.value);
										if (!newNumber) {
											return;
										}
										setFormData((prevArray) => {
											if (newNumber > prevArray?.employees?.length) {
												// Add new objects if the number is greater
												const newObjects = Array.from(
													{
														length: newNumber - prevArray?.employees?.length,
													},
													(_) => ({
														employee: "",
														userName: "",
														password: "",
														showPassword: false,
													})
												);
												return {
													...prevArray,
													number_of_employees: e.target?.value,
													employees: [...prevArray?.employees, ...newObjects],
												};
											} else {
												return {
													...prevArray,
													number_of_employees: e.target?.value,
													employees: prevArray?.employees?.slice(0, newNumber),
												};
											}
										});
									}}
								/>
							</div>
							<div className="col-12">
								{formData?.employees?.map((employee, index) => {
									return (
										<div className="row employee" key={index}>
											<div className="col-lg-4 col-sm-4">
												<label className="text-label">
													{Translate[lang]?.employee}
												</label>
												<Select
													value={employee?.employee}
													name="employee"
													placeholder={Translate[lang]?.select}
													options={employeesOptions}
													onChange={(e) => {
														let update = formData?.employees?.map(
															(res, ind) => {
																if (index === ind) {
																	return {
																		...res,
																		employee: e,
																	};
																} else {
																	return res;
																}
															}
														);
														setFormData({ ...formData, employees: update });
													}}
												/>
											</div>
											<div className="col-lg-4 col-sm-4">
												<AvField
													label={`${Translate[lang]?.user_name}`}
													name={`userName${index}`}
													type="text"
													value={employee?.userName}
													disabled={view}
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													placeholder={`${Translate[lang]?.user_name}`}
													onChange={(e) => {
														let update = formData?.employees?.map(
															(res, ind) => {
																if (index === ind) {
																	return {
																		...res,
																		userName: e?.target?.value,
																	};
																} else {
																	return res;
																}
															}
														);
														setFormData({ ...formData, employees: update });
													}}
												/>
											</div>
											<div className="col-lg-4 col-sm-4">
												<div className="position-relative">
													<label>{Translate[lang].password}</label>
													<AvField
														name={`password${index}`}
														type={employee?.showPassword ? "text" : "password"}
														value={employee?.password}
														className="radius-10 h-45"
														validate={{
															required: {
																value: true,
																errorMessage: Translate[lang].field_required,
															},
														}}
														placeholder={Translate[lang].password}
														onChange={(e) => {
															let update = formData?.employees?.map(
																(res, ind) => {
																	if (index === ind) {
																		return {
																			...res,
																			password: e?.target?.value,
																		};
																	} else {
																		return res;
																	}
																}
															);
															setFormData({ ...formData, employees: update });
														}}
													/>
													{employee?.showPassword ? (
														<i
															className="la la-eye-slash position-absolute cursor-pointer"
															style={{
																top: "44px",
																right: lang === "en" ? "8%" : "auto",
																left: lang === "ar" ? "8%" : "auto",
															}}
															onClick={() => {
																let update = formData?.employees?.map(
																	(res, ind) => {
																		if (index === ind) {
																			return {
																				...res,
																				showPassword: false,
																			};
																		} else {
																			return res;
																		}
																	}
																);
																setFormData({ ...formData, employees: update });
															}}></i>
													) : (
														<i
															className="la la-eye position-absolute cursor-pointer"
															style={{
																top: "44px",
																right: lang === "en" ? "8%" : "auto",
																left: lang === "ar" ? "8%" : "auto",
															}}
															onClick={() => {
																let update = formData?.employees?.map(
																	(res, ind) => {
																		if (index === ind) {
																			return {
																				...res,
																				showPassword: true,
																			};
																		} else {
																			return res;
																		}
																	}
																);
																setFormData({ ...formData, employees: update });
															}}></i>
													)}
												</div>
											</div>
										</div>
									);
								})}
							</div>
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								{view && (
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/pos")}>
										{Translate[lang]?.cancel}
									</Button>
								)}
								{!view && (
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								)}
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddPOS;
