import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../../common/DeleteModal";
import { Translate } from "../../../../Enums/Tranlate";
import { Link, useNavigate } from "react-router-dom";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import viewIcon from "../../../../../images/icons/eye.svg";
import HallService from "../../../../../services/HallService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setView,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const hallService = new HallService();
	const navigate = useNavigate();

	return (
		<tr key={index}>
			<td>
				<Link
					to={`/halls/view`}
					state={item}
					style={{
						color: "#0F50A6",
						fontWeight: "500",
					}}>
					{item?.name}
				</Link>
			</td>
			<td>{item?.hallNo}</td>
			<td style={{ textWrap: "nowrap" }}>{item?.branch?.name}</td>
			<td>{Translate[lang][item?.programType?.toLowerCase()]}</td>
			<td>{item?.capacity}</td>
			<td>{item?.capacityAssumption || "-"}</td>
			<td>{Translate[lang][item?.gender?.toLowerCase()]}</td>
			<td style={{ textWrap: "nowrap" }}>{item?.coach?.name || "-"}</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item?.status === "ACTIVE" ? "success" : "danger"} light`}>
					{Translate[lang][item?.status?.toLowerCase()]}
				</Badge>
			</td>
			<td>
				<div className="d-flex justify-content-center" style={{ gap: "20px" }}>
					<Link
						to={`/halls/view`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img src={viewIcon} alt="view" width={24} />
					</Link>
					<Link
						to={`/halls/edit`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img
							src={editIcon}
							className="cursor-pointer"
							alt="edit"
							width={24}
						/>
					</Link>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={hallService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
