import { Button, Card, Col, Modal, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Loader from "../../../../common/Loader";
import { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import DeleteModal from "../../../../common/DeleteModal";
import HallService from "../../../../../services/HallService";
import { daysOptions } from "../../../../Enums/Fixed";

const ViewHall = () => {
	const [deleteModal, setDeleteModal] = useState(false);
	const [item, setItem] = useState({});
	const [loading, setLoading] = useState(false);
	const location = useLocation();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		new HallService().find(location?.state?.id).then((res) => {
			if (res?.status) {
				setItem(res?.data?.data);
			}
		});
	}, []);

	return (
		<div className="department view-details">
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div className="d-flex align-items-center" style={{ gap: "8px" }}>
						<Link to="/halls">
							<i
								className={
									lang === "en" ? "la la-arrow-left" : "la la-arrow-right"
								}
								style={{
									color: "#000",
									fontSize: "24px",
								}}></i>
						</Link>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].hall_details}
						</span>
					</div>
					<div className="d-flex" style={{ gap: "20px" }}>
						<Link to={`/halls/edit`} state={item}>
							<img
								src={editIcon}
								className="cursor-pointer"
								alt="edit"
								width={24}
							/>
						</Link>
						{/* <img
							src={trashIcon}
							className="cursor-pointer"
							onClick={() => setDeleteModal(true)}
							alt="trash"
							width={24}
						/> */}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<div
						style={{
							border: "1px solid #CDCED7",
							borderRadius: "10px",
							padding: "32px 24px",
						}}>
						{loading && (
							<div style={{ height: "400px" }}>
								<Loader />
							</div>
						)}

						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].hall_name}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.name}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].hall_no}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.hallNo}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].branch}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.branch?.name}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].gender}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{Translate[lang][item?.gender?.toLowerCase()]}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].coach}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.coach?.name || "-"}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].program_type}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{Translate[lang][item?.programType?.toLowerCase()]}
							</span>
						</div>

						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].number_of_classes}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.numberOfClasses}
							</span>
						</div>

						{item?.hallClasses?.map((hallClass, index) => {
							return (
								<div
									key={index}
									className="d-flex mb-3"
									style={{ gap: "16px" }}>
									<label className="mb-0 fs-18">
										{Translate[lang].class} {hallClass?.classNumber}{" "}
										{Translate[lang].time}:
									</label>
									<span className="text-black" style={{ fontWeight: "500" }}>
										<span className="time-style px-3 mx-2">
											<i
												className="la la-clock"
												style={{ color: "#FFCABA", fontSize: "18px" }}></i>{" "}
											{hallClass?.timeFrom}
										</span>
										<i
											className="la la-arrow-right mx-1"
											style={{
												color: "#000",
												fontSize: "20px",
											}}></i>
										<span className="time-style px-3 mx-2">
											<i
												className="la la-clock"
												style={{ color: "#FFCABA", fontSize: "18px" }}></i>{" "}
											{hallClass?.timeTo}
										</span>
										{/* <i
											className="la la-arrow-right mx-1"
											style={{
												color: "#000",
												fontSize: "20px",
											}}></i>
										<span className="time-style px-3 mx-2">
											<label className="mb-0 mx-1">
												{Translate[lang].interval}:
											</label>
											{hallClass?.intervalBetweenClass}
											min
										</span> */}
									</span>
								</div>
							);
						})}

						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].break}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.hasBreak ? Translate[lang].yes : Translate[lang].no}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].break_duration}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.breakDuration}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].capacity}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.capacity}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].days}:</label>
							{item?.days?.map((day, index) => {
								return (
									<span key={index} className="time-style px-3 mx-2">
										{daysOptions[day - 1]?.label}
									</span>
								);
							})}
						</div>
						<div className="d-flex" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].hall_classification}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{Translate[lang][item?.classification?.toLowerCase()]}
							</span>
						</div>
					</div>
				</Col>
			</Row>
		</div>
	);
};

export default ViewHall;
