import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import EntertainmentSubjectsService from "../../../../services/EntertainmentSubjectsService";
import "../style.scss";
import SubjectsService from "../../../../services/SubjectsService";
import { ageGroupOptions } from "../../../Enums/Fixed";
import EntertainmentCategoriesService from "../../../../services/EntertainmentCategoriesService";
import BaseService from "../../../../services/BaseService";
import Loader from "../../../common/Loader";

const AddCategoriesModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		subjects: "",
		category: "",
		categoryAr: "",
		image: "",
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const [subjectsOptions, setSubjectsOptions] = useState([]);
	const lang = useSelector((state) => state.auth.lang);
	const entertainmentSubjectsService = new EntertainmentSubjectsService();
	const entertainmentCategoriesService = new EntertainmentCategoriesService();

	useEffect(() => {
		let params = {
			page: 1,
			limit: 10000,
		};
		entertainmentSubjectsService.getList(params).then((res) => {
			if (res?.status) {
				let info = res?.data?.data?.data
					?.filter((sub) => sub?.isActive)
					?.map((sub) => {
						return {
							label: sub?.name,
							value: sub?.id,
						};
					});
				setSubjectsOptions(info);
			}
		});
	}, []);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				category: item?.name,
				categoryAr: item?.translations?.find((res) => res?.languageId === "ar")
					?.name,
				subjects: item?.entertainmentSubjects?.map((res) => {
					return {
						label: res?.name,
						value: res?.id,
					};
				}),
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.category,
			entertainmentSubjectIds: formData?.subjects?.map((res) => res?.value),
			translations: [
				{
					languageId: "ar",
					name: formData?.categoryAr,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			entertainmentCategoriesService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`Category Added Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			entertainmentCategoriesService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`Category Updated Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							image: res?.data?.data?.uploadedFiles[0]?.url,
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0">
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang].category}
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>

						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.category} (${Translate[lang].english})`}
								name="category"
								type="text"
								value={formData?.category}
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								placeholder={`${Translate[lang]?.category}`}
								onChange={(e) =>
									setFormData({ ...formData, category: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.category} (${Translate[lang].arabic})`}
								name="categoryAr"
								type="text"
								value={formData?.categoryAr}
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								placeholder={`${Translate[lang]?.category}`}
								onChange={(e) =>
									setFormData({ ...formData, categoryAr: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<label className="text-label">
								{Translate[lang]?.subjects}{" "}
								<span>({Translate[lang]?.multi_select})</span>
							</label>
							<Select
								value={formData?.subjects}
								name="subjects"
								placeholder={Translate[lang]?.select}
								options={subjectsOptions}
								isMulti
								onChange={(e) => setFormData({ ...formData, subjects: e })}
							/>
						</Col>
						<Col md={12} className="add-gallery mt-3">
							<label className="m-0">{Translate[lang]?.image}</label>
							<div className="image-placeholder">
								<div className="avatar-edit">
									<input
										type="file"
										multiple
										onChange={(e) => fileHandler(e)}
										id={`imageUpload1`}
									/>
									<label htmlFor={`imageUpload1`} name=""></label>
								</div>
								<div className="avatar-preview2 m-auto">
									<div className="images">
										<i className="la la-plus-circle" />
										<span style={{ fontSize: "18px", fontWeight: "600" }}>
											{Translate[lang]?.add} {Translate[lang]?.image}
										</span>
										{loading && <Loader />}
									</div>
								</div>
							</div>
							{!!formData?.image && (
								<div className="row">
									<div className="col-md-6 -col-12 mb-3">
										<div className="banner_image w-auto">
											<img
												src={formData?.image}
												alt="subjectImage"
												className="w-100"
											/>
											<a
												href={formData?.image}
												target="_blank"
												rel="noreferrer">
												<i className="la la-eye" />
											</a>
											<i
												className="la la-trash"
												onClick={() => {
													setFormData({
														...formData,
														image: "",
													});
												}}
											/>
										</div>
									</div>
								</div>
							)}
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang].category}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddCategoriesModal;
