import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint =
	API_BASE_URL_ENV() + "/admin/program/entertainment-subjects";
const apiTableEndpoint =
	API_BASE_URL_ENV() + "/admin/program/entertainment/program-tables";

export default class EntertainmentSubjectsService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	getTable(id) {
		return http.get(`${apiTableEndpoint}/${id}`);
	}
}
