import React, { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import ZoneGroupService from "../../../../../services/ZoneGroupService";
import ZoneService from "../../../../../services/ZoneService";

const AddZonesModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
	Service,
}) => {
	const [formData, setFormData] = useState([
		{
			zone: "",
			group: "",
		},
	]);
	const [isAdd, setIsAdd] = useState(false);
	const [id, setId] = useState("");
	const [zonesOptions, setZonesOptions] = useState([]);
	const [zonesGroupOptions, setZonesGroupOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		let promises = [
			new ZoneGroupService().getList(),
			new ZoneService().getZonesList(),
		];
		Promise.all(promises).then((res) => {
			if (res[0]?.status === 200) {
				let data = res[0]?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setZonesGroupOptions(data);
			}
			if (res[1]?.status === 200) {
				let data = res[1]?.data?.data?.zones?.map((info) => {
					return {
						label: lang === "en" ? info?.nameEn : info?.nameAr,
						value: info?.id,
					};
				});
				setZonesOptions(data);
			}

			if (Object.keys(item)?.length === 0) {
				setIsAdd(true);
				setId("");
			} else {
				setIsAdd(false);
				setId(item?.id);
				setFormData([
					{
						id: item?.id,
						zone: item?.zoneIds?.map((res, index) => {
							return {
								value: res,
								label:
									lang === "en"
										? item?.zoneNames[index]
										: item?.zoneNamesAr[index],
							};
						}),
						group: {
							label:
								lang === "en" ? item?.zoneGroupName : item?.zoneGroupNameAr,
							value: item?.id,
						},
					},
				]);
			}
		});
	}, []);

	const submit = (e) => {
		e.preventDefault();
		let data = formData
			?.filter((res) => !!res?.group && !!res?.zone?.length)
			?.map((res) => ({
				zoneGroupId: res?.group?.value,
				zoneIds: res?.zone?.map((zone) => zone?.value),
			}));

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(id, data[0])
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.zone}
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>

						{formData?.map((zone, index) => {
							return (
								<React.Fragment key={index}>
									<Col md={6} className="mb-3">
										<label className="text-label">
											{Translate[lang]?.zones_name}
										</label>
										<Select
											value={zone?.zone}
											name={`zone${index}`}
											isMulti
											placeholder={Translate[lang]?.select}
											options={zonesOptions}
											onChange={(e) => {
												let update = formData?.map((res, ind) => {
													if (index === ind) {
														return {
															...res,
															zone: e,
														};
													} else {
														return res;
													}
												});
												setFormData(update);
											}}
										/>
									</Col>
									<Col md={6} className="mb-3">
										<label className="text-label">
											{Translate[lang]?.zone_groups}
										</label>
										<Select
											value={zone?.group}
											name={`group${index}`}
											placeholder={Translate[lang]?.select}
											options={zonesGroupOptions}
											onChange={(e) => {
												let update = formData?.map((res, ind) => {
													if (index === ind) {
														return {
															...res,
															group: e,
														};
													} else {
														return res;
													}
												});
												setFormData(update);
											}}
										/>
									</Col>
								</React.Fragment>
							);
						})}

						{isAdd && (
							<Col md={12} className="mb-0">
								<Button
									onClick={() => {
										setFormData([
											...formData,
											{
												zone: "",
												group: "",
											},
										]);
									}}
									className="w-100"
									variant="outline-primary">
									<i className="la la-plus"></i>
									{Translate[lang]?.add}
									{Translate[lang]?.zone}
								</Button>
							</Col>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.zone}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddZonesModal;
