import { Button, Card, Col, Modal, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Loader from "../../../common/Loader";
import { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import DeleteModal from "../../../common/DeleteModal";
import ProgramsService from "../../../../services/ProgramsService";

const ViewEducation = () => {
	const [deleteModal, setDeleteModal] = useState(false);
	const [item, setItem] = useState({});
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const location = useLocation();
	const programsService = new ProgramsService();

	useEffect(() => {
		setLoading(true);
		programsService
			?.find(location?.state?.id)
			.then((res) => {
				if (res?.status === 200) {
					setItem(res?.data?.data);
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	}, []);

	if (loading) {
		return;
	}
	return (
		<div className="department view-details">
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div className="d-flex align-items-center" style={{ gap: "8px" }}>
						<Link to="/education" state={"programs"}>
							<i
								className={
									lang === "en" ? "la la-arrow-left" : "la la-arrow-right"
								}
								style={{
									color: "#000",
									fontSize: "24px",
								}}></i>
						</Link>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].program_details}
						</span>
					</div>
					<div className="d-flex" style={{ gap: "20px" }}>
						<Link to={`/education/edit-program`} state={item} style={{}}>
							<img
								src={editIcon}
								className="cursor-pointer"
								alt="edit"
								width={24}
							/>
						</Link>
						{/* <img
							src={trashIcon}
							className="cursor-pointer"
							onClick={() => setDeleteModal(true)}
							alt="trash"
							width={24}
						/> */}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<div
						style={{
							border: "1px solid #CDCED7",
							borderRadius: "10px",
							padding: "32px 24px",
						}}>
						{loading && (
							<div style={{ height: "400px" }}>
								<Loader />
							</div>
						)}

						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].name}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.name}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].type}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{Translate[lang][item?.type?.toLocaleLowerCase()]}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].education_type}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{Translate[lang][item?.educationType?.toLocaleLowerCase()]}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].grade}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.grade?.join(", ")}
							</span>
						</div>
						{item?.hallId && (
							<div className="d-flex mb-3" style={{ gap: "16px" }}>
								<label className="mb-0 fs-18">{Translate[lang].hall}:</label>
								<span
									className="text-black fs-18"
									style={{ fontWeight: "500" }}>
									{item?.hall?.name}
								</span>
							</div>
						)}
						{item?.branchId && (
							<div className="d-flex mb-3" style={{ gap: "16px" }}>
								<label className="mb-0 fs-18">{Translate[lang].branch}:</label>
								<span
									className="text-black fs-18"
									style={{ fontWeight: "500" }}>
									{item?.branch?.name}
								</span>
							</div>
						)}
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].start_day}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.startDate?.split("T")[0]}
							</span>
						</div>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">{Translate[lang].end_day}:</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.endDate?.split("T")[0]}
							</span>
						</div>
						{!!item?.programType && (
							<div className="d-flex mb-3" style={{ gap: "16px" }}>
								<label className="mb-0 fs-18">
									{Translate[lang].program_type}:
								</label>
								<span
									className="text-black fs-18"
									style={{ fontWeight: "500" }}>
									{Translate[lang][item?.programType?.toLocaleLowerCase()]}
								</span>
							</div>
						)}
						{!!item?.classification && (
							<div className="d-flex mb-3" style={{ gap: "16px" }}>
								<label className="mb-0 fs-18">
									{Translate[lang].program_classification}:
								</label>
								<span
									className="text-black fs-18"
									style={{ fontWeight: "500" }}>
									{Translate[lang][item?.classification?.toLocaleLowerCase()]}
								</span>
							</div>
						)}
						{!!item?.lessonsPerWeek && (
							<div className="d-flex" style={{ gap: "16px" }}>
								<label className="mb-0 fs-18">
									{Translate[lang].lessons_frequency_week}:
								</label>
								<span
									className="text-black fs-18"
									style={{ fontWeight: "500" }}>
									{item?.lessonsPerWeek}
								</span>
							</div>
						)}
					</div>
				</Col>
			</Row>
		</div>
	);
};

export default ViewEducation;
