import { useState } from "react";
import { useSelector } from "react-redux";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import viewIcon from "../../../../images/icons/eye.svg";
import { useNavigate } from "react-router-dom";
import SubjectsService from "../../../../services/SubjectsService";
import ProgramsService from "../../../../services/ProgramsService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	type,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const programsService = new ProgramsService();
	const subjectsService = new SubjectsService();
	const navigate = useNavigate();

	return (
		<tr key={index} className="text-center">
			<td>{item?.name}</td>
			<td>
				{type === "subjects"
					? Translate[lang][item?.educationType?.toLowerCase()]
					: Translate[lang][item?.type?.toLowerCase()]}
			</td>
			<td>
				{type === "subjects"
					? item?.grades?.join(", ")
					: item?.grade?.join(", ")}
			</td>
			{type === "programs" && (
				<td>{item?.subjects?.map((res) => res?.name)?.join(", ")}</td>
			)}
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					{type === "programs" && (
						<img
							onClick={() =>
								navigate(`/education/${item?.id}`, { state: item })
							}
							src={viewIcon}
							alt="viewIcon"
							className="cursor-pointer"
							width={24}
						/>
					)}
					<img
						src={editIcon}
						onClick={() => {
							if (type === "programs") {
								navigate(`/education/edit-program`, { state: item });
								return;
							} else {
								setItem(item);
								setAddModal(true);
							}
						}}
						alt="edit"
						className="cursor-pointer"
						width={24}
					/>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={type === "subjects" ? subjectsService : programsService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
