import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import viewIcon from "../../../../images/icons/eye.svg";
import tableIcon from "../../../../images/icons/table.svg";
import { useNavigate } from "react-router-dom";
import { ageGroupOptions, gendersUpOptions } from "../../../Enums/Fixed";
import EntertainmentSubjectsService from "../../../../services/EntertainmentSubjectsService";
import EntertainmentCategoriesService from "../../../../services/EntertainmentCategoriesService";
import EntertainmentProgramsService from "../../../../services/EntertainmentProgramsService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setTableModal,
	type,
	setAddCategoryModal,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const navigate = useNavigate();

	return (
		<tr key={index} className="text-center">
			<td>{item?.name}</td>
			{type === "programs" && (
				<>
					<td>{item?.branch?.name}</td>
					{/* <td>{item?.hall?.name}</td> */}
					{/* <td>
						<span style={{ width: "10rem" }}>
							{item?.subjects?.map((res) => res?.name).join(", ")}
						</span>
					</td> */}
					<td>
						{
							ageGroupOptions?.find((res) => res?.value === item?.ageGroup)
								?.label
						}
					</td>
					<td>{item?.startDate?.split("T")[0]}</td>
					<td>{item?.endDate?.split("T")[0]}</td>
				</>
			)}
			{type === "subjects" && (
				<>
					<td>
						{
							ageGroupOptions?.find((res) => res?.value === item?.ageGroups)
								?.label
						}
					</td>
					<td>
						{
							gendersUpOptions?.find((res) => res?.value === item?.gender)
								?.label
						}
					</td>
					<td>
						<Badge
							className="text-capitalize"
							variant={`${item.isActive ? "success" : "danger"} light`}>
							{item?.isActive
								? Translate[lang].active
								: Translate[lang].deactive}
						</Badge>
					</td>
				</>
			)}
			{type === "categories" && (
				<>
					<td>
						{item.entertainmentSubjects?.map((res) => res?.name)?.join(", ")}
					</td>
				</>
			)}
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					{type === "programs" && (
						<img
							onClick={() => navigate(`/entertainment/${item?.id}`)}
							src={viewIcon}
							alt="viewIcon"
							className="cursor-pointer"
							width={24}
						/>
					)}
					<img
						src={editIcon}
						onClick={() => {
							if (type === "programs") {
								navigate(`/entertainment/edit-program`, { state: item });
								return;
							} else {
								setItem(item);
								if (type === "subjects") setAddModal(true);
								if (type === "categories") setAddCategoryModal(true);
							}
						}}
						alt="edit"
						className="cursor-pointer"
						width={24}
					/>
					{type === "programs" && (
						<img
							src={tableIcon}
							onClick={() => {
								setItem(item);
								setTableModal(true);
							}}
							alt="edit"
							className="cursor-pointer"
							width={24}
						/>
					)}
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={
						type === "subjects"
							? new EntertainmentSubjectsService()
							: type === "categories"
							? new EntertainmentCategoriesService()
							: new EntertainmentProgramsService()
					}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
