import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";
import OrganizationService from "../../../../../services/OrganizationService";
import EmployeesService from "../../../../../services/EmployeesService";
import DepartmentService from "../../../../../services/DepartmentService";

const AddDepartmentsModal = ({
	addModal,
	setAddModal,
	item,
	assign,
	setShouldUpdate,
	type,
	Service,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		manager: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [options, setOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const [managerOptions, setManagerOptions] = useState([]);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		let promises = assign
			? [new EmployeesService().getManager("MANAGER")]
			: [
					new EmployeesService().getManager("MANAGER"),
					type === "departments"
						? new OrganizationService().getList()
						: new DepartmentService().getList(),
			  ];
		Promise.all(promises).then((res) => {
			let managersOptions = [];
			let orgOptions = [];
			if (res[0]?.status === 200) {
				let data = res[0]?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				managersOptions = data;
				setManagerOptions(data);
			}
			if (!assign && res[1]?.status === 200) {
				let data = res[1]?.data?.data?.map((info) => {
					return {
						label: type === "departments" ? info?.legalName : info?.name,
						value: info?.id,
					};
				});
				orgOptions = data;
				setOptions(data);
			}

			if (Object.keys(item)?.length === 0) {
				setIsAdd(true);
			} else {
				setIsAdd(false);
				setFormData({
					id: item?.id,
					name: item?.name,
					isActive: item?.isActive,
					manager:
						managersOptions?.find((res) => res?.value === item?.managerId) ||
						"",
					type: orgOptions?.find(
						(res) => res?.value === item?.organization?.id
					),
				});
			}
		});
	}, []);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			isActive: formData?.isActive,
		};
		if (type === "departments") data["organizationId"] = formData?.type?.value;
		// if (!!formData?.manager?.value)
		// 	data["managerId"] = formData?.manager?.value;

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{assign
										? Translate[lang]?.assign_manager
										: isAdd
										? Translate[lang]?.add
										: Translate[lang]?.edit}{" "}
									{assign ? "" : Translate[lang]?.department}
								</p>
								{!assign && (
									<Form.Check
										type="switch"
										id="custom-switch"
										checked={formData?.isActive}
										onChange={(e) =>
											setFormData({ ...formData, isActive: e.target.checked })
										}
									/>
								)}
							</div>
						</Col>

						{!assign && (
							<>
								<Col md={12}>
									<AvField
										label={
											type === "departments"
												? Translate[lang]?.department_name
												: Translate[lang]?.unit_name
										}
										type="text"
										placeholder={
											type === "departments"
												? Translate[lang]?.department_name
												: Translate[lang]?.unit_name
										}
										bsSize="lg"
										name="name"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.name}
										onChange={(e) =>
											setFormData({
												...formData,
												name: e.target.value,
											})
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label className="text-label">
										{type === "departments"
											? Translate[lang]?.organization
											: Translate[lang]?.department}
									</label>
									<Select
										value={formData?.type}
										name="type"
										placeholder={Translate[lang]?.select}
										options={options}
										onChange={(e) => setFormData({ ...formData, type: e })}
									/>
								</Col>
							</>
						)}

						{(!isAdd || assign) && (
							<Col md={12}>
								<label className="text-label">
									{type === "units"
										? Translate[lang]?.unit_manager
										: Translate[lang]?.department_manager}
								</label>
								<Select
									value={formData?.manager}
									name="manager"
									placeholder={Translate[lang]?.select}
									options={managerOptions}
									onChange={(e) => setFormData({ ...formData, manager: e })}
								/>
							</Col>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{assign
							? Translate[lang]?.assign_manager
							: isAdd
							? Translate[lang]?.add
							: Translate[lang]?.edit}{" "}
						{assign
							? ""
							: type === "departments"
							? Translate[lang]?.department
							: Translate[lang]?.unit}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddDepartmentsModal;
