import { useState } from "react";
import { Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Loader from "../../../common/Loader";
import NoData from "../../../common/NoData";
import Pagination from "../../../common/Pagination/Pagination";
import { Translate } from "../../../Enums/Tranlate";
import CardItem from "./CardItem";
import ProductService from "../../../../services/ProductService";
import filterIcon from "../../../../images/icons/filter.svg";
import FilterModal from "./FilterModal";

const Products = () => {
	const [data, setData] = useState([]);
	const [hasData, setHasData] = useState(null);
	const [filterModal, setFilterModal] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const [loading, setLoading] = useState(false);
	const [params, setParams] = useState("");
	const productService = new ProductService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const lang = useSelector((state) => state.auth?.lang);

	return (
		<div className="organization">
			<div className="mb-4">
				<div className="assets-div d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].products}
						</span>
					</div>
					<div
						className="add-assets d-flex cursor-pointer"
						style={{ gap: "16px" }}>
						<img
							src={filterIcon}
							width={42}
							height={42}
							alt="filterIcon"
							onClick={() => setFilterModal(true)}
						/>
						{/* isExist("add_organization") &&  */}
						{
							<Link to="/products/add" className="btn btn-primary h-75">
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add} {Translate[lang]?.product}
							</Link>
						}
					</div>
				</div>
			</div>
			<Card>
				<div className={`${hasData === 0 ? "text-center" : "table-style"}`}>
					{loading && (
						<div style={{ height: "300px" }}>
							<Loader />
						</div>
					)}
					{hasData === 1 && !loading && (
						<Table responsive>
							<thead>
								<tr>
									<th>
										<strong>{Translate[lang]?.product}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.sku}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.type}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.price}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.status}</strong>
									</th>
									<th className="text-center">
										<strong>{Translate[lang]?.action}</strong>
									</th>
								</tr>
							</thead>
							<tbody>
								{data?.map((item, index) => {
									return (
										<CardItem
											key={index}
											index={index}
											item={item}
											setShouldUpdate={setShouldUpdate}
										/>
									);
								})}
							</tbody>
						</Table>
					)}
					{hasData === 0 && <NoData />}
					<Pagination
						setData={setData}
						service={productService}
						shouldUpdate={shouldUpdate}
						setHasData={setHasData}
						setLoading={setLoading}
						type={!!params ? params?.value : ""}
					/>
				</div>
			</Card>
			{filterModal && (
				<FilterModal
					filterModal={filterModal}
					setFilterModal={setFilterModal}
					setShouldUpdate={setShouldUpdate}
					setParams={setParams}
					params={params}
				/>
			)}
		</div>
	);
};
export default Products;
