import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/ecommerce/variant";

export default class VariantsService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	getVariantsByCategory(id) {
		return http.get(`${apiEndpoint}/category/${id}`);
	}
}
