import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import ProductService from "../../../../../services/ProductService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import BaseService from "../../../../../services/BaseService";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw, ContentState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import { gradesOptions } from "../../../../Enums/Fixed";
import "../style.scss";
import SubjectsService from "../../../../../services/SubjectsService";
import Loader from "../../../../common/Loader";

const Packages = ({ isActive, setIsActive }) => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name_en: "",
		name_ar: "",
		subject: "",
		price: "",
		discount: "",
		grade: "",
		image: "",
		features: EditorState.createEmpty(),
		features_ar: EditorState.createEmpty(),
		description: EditorState.createEmpty(),
		description_ar: EditorState.createEmpty(),
		addOnGroup: false,
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [subjectOptions, setSubjectOptions] = useState([]);
	const navigate = useNavigate();
	const productService = new ProductService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;

		let promisesFun = [new SubjectsService().getList()];
		if (location?.pathname.includes("edit"))
			promisesFun.push(productService?.find(item?.id));
		Promise.all(promisesFun).then((res) => {
			let info = [];
			if (res[0]?.status) {
				info = res[0]?.data?.data?.data?.map((cat) => {
					return {
						label:
							lang === "en"
								? cat?.name
								: cat?.translations?.find((trans) => trans?.languageId === "ar")
										?.name,
						value: cat?.id,
					};
				});
				setSubjectOptions(info);
			}
			if (location?.pathname.includes("edit")) {
				if (res[1]?.status) {
					setId(item?.id);
					productService?.find(item?.id).then((res) => {
						if (res?.status) {
							let product = res?.data?.data;
							setIsActive(product?.isActive);
							setFormData({
								name_en: product?.name,
								name_ar: product?.translations?.find(
									(info) => info?.languageId === "ar"
								).name,
								subject: product?.subjects?.map((sub) =>
									info?.find((subInf) => sub === subInf?.value)
								),
								price: product?.price,
								discount: product?.discount,
								grade: product?.grades?.map((grade) =>
									gradesOptions?.find((grd) => grd.value === grade)
								),
								image: product?.packageImage,
								features: EditorState.createWithContent(
									ContentState.createFromBlockArray(
										htmlToDraft(product?.features?.replaceAll("&lt;", "<"))
									)
								),
								features_ar: EditorState.createWithContent(
									ContentState.createFromBlockArray(
										htmlToDraft(
											product?.translations
												?.find((info) => info?.languageId === "ar")
												.features?.replaceAll("&lt;", "<")
										)
									)
								),
								description: EditorState.createWithContent(
									ContentState.createFromBlockArray(
										htmlToDraft(product?.description?.replaceAll("&lt;", "<"))
									)
								),
								description_ar: EditorState.createWithContent(
									ContentState.createFromBlockArray(
										htmlToDraft(
											product?.translations
												?.find((info) => info?.languageId === "ar")
												?.description?.replaceAll("&lt;", "<")
										)
									)
								),
								addOnGroup: product?.addOnGroup,
							});
						}
					});
				}
			}
		});
		if (!location?.pathname.includes("edit")) {
			setId(null);
		}
	}, [location]);

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);

		if (!filesData?.length) {
			return;
		}
		setLoading(true);
		new BaseService()
			.postUpload(filesData[0])
			.then((res) => {
				if (res?.status === 201) {
					let imgsUrls = res?.data?.data?.uploadedFiles?.map((img) => img?.url);
					setFormData({
						...formData,
						image: imgsUrls[0],
					});
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	const onFeaturesSubmit = (e) => {
		e.preventDefault();

		let data = {
			type: "PACKAGE",
			name: formData?.name_en,
			price: Number(formData?.price),
			discount: Number(formData?.discount),
			description: draftToHtml(
				convertToRaw(formData?.description_ar.getCurrentContent())
			),
			addOnGroup: formData?.addOnGroup,
			isActive: isActive,
			subjects: formData?.subject?.map((res) => res?.value),
			grades: formData?.grade?.map((res) => res?.value),
			features: draftToHtml(
				convertToRaw(formData?.features.getCurrentContent())
			),
			packageImage: formData?.image,
			translations: [
				{
					languageId: "ar", // Arabic language ID
					name: formData?.name_ar,
					description: draftToHtml(
						convertToRaw(formData?.description_ar.getCurrentContent())
					),
					features: draftToHtml(
						convertToRaw(formData?.features_ar.getCurrentContent())
					),
				},
			],
		};

		if (!!id) {
			setLoading(true);
			productService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			productService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		}
	};

	return (
		<AvForm onValidSubmit={onFeaturesSubmit}>
			<div className="row">
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.package_name} (${Translate[lang]?.english})`}
						name={`name_en`}
						type="text"
						value={formData?.name_en}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.package_name}
						onChange={(e) => {
							setFormData({ ...formData, name_en: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.package_name} (${Translate[lang]?.arabic})`}
						name={`name_ar`}
						type="text"
						value={formData?.name_ar}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.package_name}
						onChange={(e) => {
							setFormData({ ...formData, name_ar: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3">
					<label>{Translate[lang]?.subject}</label>
					<Select
						value={formData?.subject}
						options={subjectOptions}
						isMulti
						placeholder={`${Translate[lang]?.subject}`}
						onChange={(e) => setFormData({ ...formData, subject: e })}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3">
					<label>{Translate[lang]?.grade}</label>
					<Select
						value={formData?.grade}
						options={gradesOptions}
						isMulti
						placeholder={`${Translate[lang]?.grade}`}
						onChange={(e) => setFormData({ ...formData, grade: e })}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.price}
						name={`price`}
						type="number"
						value={formData?.price}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.price}
						onChange={(e) => {
							setFormData({ ...formData, price: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.discount} %`}
						name={`discount`}
						type="number"
						value={formData?.discount}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.percentage}
						onChange={(e) => {
							setFormData({ ...formData, discount: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3 add-gallery">
					<div className="form-group w-100">
						<label className="m-0">{Translate[lang]?.upload_image}</label>
						<div className="image-placeholder">
							<div className="avatar-edit">
								<input
									type="file"
									onChange={(e) => fileHandler(e)}
									id={`imageUpload1`}
								/>
								<label htmlFor={`imageUpload1`} name=""></label>
							</div>
							<div className="avatar-preview2 m-auto">
								<div id={`imagePreview`} className="images">
									<>
										<i className="la la-plus-circle" />
										<span style={{ fontSize: "18px", fontWeight: "600" }}>
											{Translate[lang]?.add} {Translate[lang]?.image}
										</span>
									</>
									{loading && <Loader />}
								</div>
							</div>
						</div>
						{!!formData?.image && (
							<div className="about_image">
								<img src={formData?.image} alt="banner_image" />
								<a href={formData?.image} target="_blank" rel="noreferrer">
									<i className="la la-eye" />
								</a>
								<i
									className="la la-trash"
									onClick={() => setFormData({ ...formData, image: "" })}
								/>
							</div>
						)}
					</div>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>
						{Translate[lang].description} ({Translate[lang].english})
					</label>
					<Editor
						editorState={formData.description}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description: e })
						}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>
						{Translate[lang].description} ({Translate[lang].english})
					</label>
					<Editor
						editorState={formData.description_ar}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description_ar: e })
						}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>
						{Translate[lang].features} ({Translate[lang].english})
					</label>
					<Editor
						editorState={formData.features}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, features: e })
						}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>
						{Translate[lang].features} ({Translate[lang].arabic})
					</label>
					<Editor
						editorState={formData.features_ar}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, features_ar: e })
						}
					/>
				</div>
				<div className="col-12">
					<hr />
				</div>
				<div className="col-12 mb-3">
					<label className="d-flex gap-8">
						<input
							type="checkbox"
							name="add_on_group"
							className="add-product-check"
							checked={formData?.addOnGroup}
							onChange={(e) =>
								setFormData({ ...formData, addOnGroup: e.target?.checked })
							}
						/>
						{Translate[lang].add_on_group}
					</label>
				</div>

				{!!id && (
					<div className="col-lg-6 col-sm-6 mt-2 mb-5">
						<Button
							variant="secondary"
							type="button"
							className="w-100 fs-14"
							onClick={() => navigate("/products")}>
							{Translate[lang]?.cancel}
						</Button>
					</div>
				)}
				<div className="col-lg-6 col-sm-6 mt-2 mb-5">
					<Button
						variant="primary"
						type="submit"
						className="w-100 fs-14"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</div>
			</div>
		</AvForm>
	);
};

export default Packages;
