import React, { useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../../common/Loader";
import NoData from "../../../common/NoData";
import { Translate } from "../../../Enums/Tranlate";
import CardItem from "./CardItem";
import "./style.scss";
import AddOnGroupService from "../../../../services/AddOnGroupService";
import AssignModal from "./AssignModal";

const AddOnGroup = () => {
	const [data, setData] = useState([]);
	const [assignModal, setAssignModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(null);
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const addOnGroupService = new AddOnGroupService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);

	useEffect(() => {
		setLoading(true);
		addOnGroupService
			.getList()
			.then((res) => {
				if (res?.status) {
					setData(res?.data?.data);
					if (!!res?.data?.data?.length) {
						setHasData(1);
					} else {
						setHasData(0);
					}
				}
				setLoading(false);
			})
			.catch((e) => setLoading(false));
	}, [shouldUpdate]);

	return (
		<div className="department">
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].on_group}
						</span>
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>{Translate[lang]?.group_product_name}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.products}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setItem={setItem}
													setAssignModal={setAssignModal}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{/* <Pagination
								setData={setData}
								service={addOnGroupService}
								shouldUpdate={shouldUpdate}
								setHasData={setHasData}
								setLoading={setLoading}
							/> */}
						</div>
					</Card>
				</Col>
			</Row>

			{setAssignModal && (
				<AssignModal
					item={item}
					addModal={assignModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAssignModal(false)}
				/>
			)}
		</div>
	);
};

export default AddOnGroup;
