import { useEffect, useState } from "react";
import { Button, Col, Modal, Row } from "react-bootstrap";
import { AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import Select from "react-select";
import "../style.scss";
import { Translate } from "../../../../Enums/Tranlate";

const FilterModal = ({
	filterModal,
	setFilterModal,
	setParams,
	setShouldUpdate,
	params,
}) => {
	const [formData, setFormData] = useState({
		type: "",
	});
	const lang = useSelector((state) => state.auth.lang);
	const typesOptions = [
		{ label: "Package", value: "PACKAGE" },
		{ label: "Service", value: "SERVICE" },
		{ label: "Physical", value: "PHYSICAL" },
		{ label: "Digital", value: "DIGITAL" },
	];

	useEffect(() => setFormData({ type: params }), []);

	const submit = (e) => {
		if (!formData?.type) {
			return;
		}
		e.preventDefault();
		setParams(formData?.type);
		setShouldUpdate((prev) => !prev);
		setFilterModal(false);
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={filterModal}
			onHide={() => {
				setFilterModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0">{Translate[lang]?.filter}</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setFilterModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">{Translate[lang]?.type}</label>
							<Select
								value={formData?.type}
								name="type"
								placeholder={Translate[lang]?.select}
								options={typesOptions}
								onChange={(e) => setFormData({ ...formData, type: e })}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{!!formData?.type?.value && (
						<Button
							variant="danger"
							type="button"
							onClick={() => {
								setParams("");
								setShouldUpdate((prev) => !prev);
								setFilterModal(false);
							}}
							className="mb-3 w-100">
							{Translate[lang]?.clear_filter}
						</Button>
					)}
					<Button variant="primary" type="submit" className="w-100">
						{Translate[lang]?.apply_filter}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default FilterModal;
