import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import HallService from "../../../../../services/HallService";
import EmployeesService from "../../../../../services/EmployeesService";
import { daysOptions } from "../../../../Enums/Fixed";
import BranchesService from "../../../../../services/BranchesService";
import adjustClassTimes from "./classBreak";

const AddHall = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		branch: "",
		name: "",
		hall_no: "",
		program_type: "",
		coach: "",
		gender: "",
		time_from: "",
		time_to: "",
		number_of_classes: "",
		classes: [],
		capacity_assumption: "",
		capacity: "",
		days: "",
		enable_capacity: false,
		break: false,
		time_in_minutes: "",
		after_number_of_classes: "",
		isActive: true,
		hall_classification: "PRIVATE",
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const [editTime, setEditTime] = useState(false);
	const [branchesOptions, setBranchesOptions] = useState([]);
	const [coachesOptions, setCoachesOptions] = useState([]);
	const navigate = useNavigate();
	const hallService = new HallService();
	const lang = useSelector((state) => state.auth?.lang);
	const programType = [
		{ label: Translate[lang].education, value: "EDUCATION" },
		{ label: Translate[lang].entertainment, value: "ENTERTAINMENT" },
	];
	const genderOptions = [
		{ label: Translate[lang].female, value: "FEMALE" },
		{ label: Translate[lang].male, value: "MALE" },
		{ label: Translate[lang].mixed, value: "MIXED" },
	];

	useEffect(() => {
		new BranchesService().getList().then((res) => {
			if (res?.status) {
				let data = res?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setBranchesOptions(data);
			}
		});
		new EmployeesService().getManager("COACH").then((res) => {
			if (res?.status) {
				let data = res?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setCoachesOptions(data);
			}
		});
	}, []);

	useEffect(() => {
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			let item = location.state;
			hallService.find(item?.id).then((res) => {
				if (res?.status) {
					let response = res?.data?.data;
					setId(item?.id);
					setFormData({
						id: item?.id,
						name: response?.name,
						hall_no: response?.hallNo,
						program_type: programType?.find(
							(res) => res?.value === item?.programType
						),
						coach: !!response?.coach?.id
							? {
									label: response?.coach?.name,
									value: response?.coach?.id,
							  }
							: "",
						branch: {
							label: response?.branch?.name,
							value: response?.branch?.id,
						},
						gender: genderOptions?.find(
							(res) => res?.value === response?.gender
						),
						number_of_classes: response?.hallClasses?.length,
						time_from: response?.timeFrom,
						time_to: response?.timeTo,
						enable_capacity: response?.enableCapacity,
						capacity: response?.capacity,
						capacity_assumption: response?.capacityAssumption || "",
						days: response?.days?.map((day) =>
							daysOptions?.find((res) => res?.value === day)
						),
						hall_classification: response?.classification,
						isActive: response?.status === "ACTIVE",
						break: response?.hasBreak,
						time_in_minutes: response?.breakDuration,
						after_number_of_classes: response?.breakAfterClass,
						classes: response?.hallClasses
							?.filter((res) => res?.classNumber !== -1)
							?.map((res) => ({
								from: res?.timeFrom,
								to: res?.timeTo,
							})),
					});
				}
			});
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		if (
			!formData?.days?.length ||
			!formData?.branch ||
			!formData?.gender ||
			!formData?.program_type
		) {
			return;
		}
		e.preventDefault();

		let data = {
			name: formData?.name,
			hallNo: formData?.hall_no,
			branchId: formData?.branch?.value,
			gender: formData?.gender?.value,
			numberOfClasses: formData?.classes?.length,
			timeFrom: formData?.time_from,
			timeTo: formData?.time_to,
			programType: formData?.program_type?.value,
			enableCapacity: formData?.enable_capacity,
			capacity: Number(formData?.capacity),
			days: formData?.days?.map((day) => day?.value),
			classification: formData?.hall_classification,
			hasBreak: formData?.break,
			breakDuration: Number(formData?.time_in_minutes),
			breakAfterClass: Number(formData?.after_number_of_classes),
			status: formData?.isActive ? "ACTIVE" : "DEACTIVE",
			hallClasses: formData?.break
				? adjustClassTimes(
						formData?.classes,
						Number(formData?.after_number_of_classes),
						Number(formData?.time_in_minutes)
				  )
				: formData?.classes?.map((res, index) => ({
						classNumber: index + 1,
						timeFrom: res?.from,
						timeTo: res?.to,
				  })),
		};
		if (formData?.program_type?.value === "ENTERTAINMENT")
			data["coachId"] = formData?.coach?.value;
		if (formData?.enable_capacity)
			data["capacityAssumption"] = Number(formData?.capacity_assumption);

		if (!!id) {
			data["hallClasses"] = editTime
				? adjustClassTimes(
						formData?.classes,
						Number(formData?.after_number_of_classes),
						Number(formData?.time_in_minutes)
				  )
				: formData?.classes?.map((res, index) => ({
						classNumber: index + 1,
						timeFrom: res?.from,
						timeTo: res?.to,
				  }));
			setLoading(true);
			hallService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response.data?.message);
					navigate("/halls");
				}
				setLoading(false);
			});
		} else {
			data["hallClasses"] = formData?.break
				? adjustClassTimes(
						formData?.classes,
						Number(formData?.after_number_of_classes),
						Number(formData?.time_in_minutes)
				  )
				: formData?.classes?.map((res, index) => ({
						classNumber: index + 1,
						timeFrom: res?.from,
						timeTo: res?.to,
				  }));
			setLoading(true);
			hallService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response.data?.message);
					navigate("/halls");
				}
				setLoading(false);
			});
		}
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/halls")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{view
						? Translate[lang]?.view
						: !!id
						? Translate[lang]?.edit
						: Translate[lang]?.add}{" "}
					{Translate[lang]?.hall}
				</label>

				<Form.Check
					type="switch"
					id="custom-switch"
					checked={formData?.isActive}
					onChange={(e) =>
						setFormData({ ...formData, isActive: e.target.checked })
					}
				/>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6">
								<label className="text-label">{Translate[lang]?.branch}</label>
								<Select
									value={formData?.branch}
									name="branch"
									placeholder={Translate[lang]?.select}
									options={branchesOptions}
									onChange={(e) => setFormData({ ...formData, branch: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.hall_name}`}
									name="name"
									type="text"
									value={formData?.name}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.hall_name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.hall_no}`}
									name="hall_no"
									type="text"
									value={formData?.hall_no}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.hall_no}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<label className="text-label">
									{Translate[lang]?.program_type}
								</label>
								<Select
									value={formData?.program_type}
									name="program_type"
									placeholder={Translate[lang]?.select}
									options={programType}
									onChange={(e) =>
										setFormData({ ...formData, program_type: e, coach: "" })
									}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-4">
								<label className="text-label">{Translate[lang]?.coach}</label>
								<Select
									value={formData?.coach}
									name="coach"
									isDisabled={formData?.program_type?.value === "EDUCATION"}
									placeholder={Translate[lang]?.select}
									options={coachesOptions}
									onChange={(e) => setFormData({ ...formData, coach: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-4">
								<label className="text-label">{Translate[lang]?.gender}</label>
								<Select
									value={formData?.gender}
									name="gender"
									placeholder={Translate[lang]?.select}
									options={genderOptions}
									onChange={(e) => setFormData({ ...formData, gender: e })}
								/>
							</div>
							<div className="col-lg-12 col-sm-12">
								<Row className="table-style py-2 w-100 m-auto">
									<div className="col-lg-4 col-sm-4">
										<AvField
											label={`${Translate[lang]?.time_from}`}
											name="time_from"
											type="time"
											value={formData?.time_from}
											disabled={view}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.time_from}`}
											onChange={(e) => inputHandler(e)}
										/>
									</div>
									<div className="col-lg-4 col-sm-4">
										<AvField
											label={`${Translate[lang]?.time_to}`}
											name="time_to"
											type="time"
											value={formData?.time_to}
											disabled={view}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.time_to}`}
											onChange={(e) => inputHandler(e)}
										/>
									</div>
									<div className="col-lg-4 col-sm-4">
										<AvField
											label={`${Translate[lang]?.number_of_classes}`}
											name="number_of_classes"
											type="number"
											value={formData?.number_of_classes}
											disabled={view}
											min={0}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.number_of_classes}`}
											onChange={(e) => {
												let newNumber = parseInt(e.target.value);
												if (!newNumber) {
													return;
												}
												setFormData((prevArray) => {
													if (newNumber > prevArray?.classes?.length) {
														// Add new objects if the number is greater
														const newObjects = Array.from(
															{
																length: newNumber - prevArray?.classes?.length,
															},
															(_) => ({
																from: "",
																to: "",
															})
														);
														return {
															...prevArray,
															classes: [...prevArray?.classes, ...newObjects],
														};
													} else {
														return {
															...prevArray,
															classes: prevArray?.classes?.slice(0, newNumber),
														};
													}
												});
											}}
										/>
									</div>
									{formData?.classes?.map((classItem, index) => {
										return (
											<React.Fragment key={index}>
												<div className="col-md-4 col-6">
													<AvField
														label={`${Translate[lang]?.class} ${index + 1} ${
															Translate[lang]?.from
														}`}
														name={`from${index}`}
														type="time"
														value={classItem.from}
														disabled={view}
														validate={{
															required: {
																value: true,
																errorMessage: Translate[lang].field_required,
															},
														}}
														placeholder={`${Translate[lang]?.from}`}
														onChange={(e) => {
															if (id) {
																setEditTime(true);
															}
															setFormData((prevData) => ({
																...prevData,
																classes: prevData.classes?.map((item, ind) =>
																	ind === index
																		? { ...item, from: e.target.value }
																		: item
																),
															}));
														}}
													/>
												</div>
												<div className="col-md-4 col-6">
													<AvField
														label={`${Translate[lang]?.class} ${index + 1} ${
															Translate[lang]?.to
														}`}
														name={`to${index}`}
														type="time"
														value={classItem.to}
														disabled={view}
														validate={{
															required: {
																value: true,
																errorMessage: Translate[lang].field_required,
															},
														}}
														placeholder={`${Translate[lang]?.to}`}
														onChange={(e) => {
															if (id) {
																setEditTime(true);
															}
															setFormData((prevData) => ({
																...prevData,
																classes: prevData.classes?.map((item, ind) =>
																	ind === index
																		? { ...item, to: e.target.value }
																		: item
																),
															}));
														}}
													/>
												</div>
												<div className="col-md-4 col-12"></div>
											</React.Fragment>
										);
									})}
								</Row>
							</div>
							<div
								className="col-lg-12 col-sm-6 d-flex mt-4 mb-3"
								style={{ gap: "8px" }}>
								<label className="d-flex gap-8 cursor-pointer">
									<Form.Check
										type="switch"
										id="custom-switch-enable"
										checked={formData?.enable_capacity}
										onChange={(e) =>
											setFormData({
												...formData,
												enable_capacity: e.target.checked,
											})
										}
									/>
									{Translate[lang].enable_capacity_assumption}
								</label>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.capacity}`}
									name="capacity"
									type="text"
									value={formData?.capacity}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.capacity}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.capacity_assumption}`}
									name="capacity_assumption"
									type="number"
									min="1"
									value={formData?.capacity_assumption}
									disabled={view || !formData?.enable_capacity}
									validate={{
										required: {
											value: formData?.enable_capacity,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.capacity_assumption}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								<label className="text-label">
									{Translate[lang]?.days}{" "}
									<span
										style={{ color: "rgba(98, 99, 108, 1)", fontWeight: 400 }}>
										({Translate[lang]?.multi_select})
									</span>
								</label>
								<Select
									value={formData?.days}
									name="days"
									placeholder={Translate[lang]?.select}
									options={daysOptions}
									isMulti
									onChange={(e) => setFormData({ ...formData, days: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6">
								{formData?.program_type?.value !== "ENTERTAINMENT" && (
									<>
										<label className="text-label">
											{Translate[lang]?.hall_classification}
										</label>
										<div
											className="d-flex align-items-center"
											style={{ gap: "16px", height: "40px" }}>
											<label className="mb-0 d-flex" style={{ gap: "8px" }}>
												<input
													type="radio"
													name="private"
													checked={formData?.hall_classification === "PRIVATE"}
													onChange={() =>
														setFormData({
															...formData,
															hall_classification: "PRIVATE",
														})
													}
												/>
												<span>{Translate[lang].private}</span>
											</label>
											<label className="mb-0 d-flex" style={{ gap: "8px" }}>
												<input
													type="radio"
													name="group"
													checked={formData?.hall_classification === "GROUP"}
													onChange={() =>
														setFormData({
															...formData,
															hall_classification: "GROUP",
														})
													}
												/>
												<span>{Translate[lang].group}</span>
											</label>
										</div>
									</>
								)}
							</div>
							<div
								className="col-lg-12 col-sm-6 d-flex mt-4 mb-3"
								style={{ gap: "8px" }}>
								<label className="d-flex gap-8 cursor-pointer">
									<Form.Check
										type="switch"
										id="custom-switch-break"
										checked={formData?.break}
										onChange={(e) => {
											if (id) {
												setEditTime(true);
											}
											setFormData({
												...formData,
												break: e.target.checked,
											});
										}}
									/>
									{Translate[lang].hall_break}
								</label>
							</div>
							{formData?.break && (
								<>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.time_in_minutes}`}
											name="time_in_minutes"
											type="number"
											min="1"
											value={formData?.time_in_minutes}
											disabled={view}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.time_in_minutes}`}
											onChange={(e) => {
												if (id) {
													setEditTime(true);
												}
												inputHandler(e);
											}}
										/>
									</div>
									<div className="col-lg-6 col-sm-6">
										<AvField
											label={`${Translate[lang]?.after_number_of_classes}`}
											name="after_number_of_classes"
											type="number"
											min="1"
											value={formData?.after_number_of_classes}
											disabled={view}
											validate={{
												required: {
													value: true,
													errorMessage: Translate[lang].field_required,
												},
											}}
											placeholder={`${Translate[lang]?.after_number_of_classes}`}
											onChange={(e) => {
												if (id) {
													setEditTime(true);
												}
												inputHandler(e);
											}}
										/>
									</div>
								</>
							)}
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								{view && (
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/halls")}>
										{Translate[lang]?.cancel}
									</Button>
								)}
								{!view && (
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								)}
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddHall;
