import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/program/group-setting";

export default class GroupSettingsService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}
	create(type, data) {
		return http.post(`${apiEndpoint}/${type}`, data);
	}
	getList(type) {
		return http.get(`${apiEndpoint}/${type}`);
	}
	update(type, id, data) {
		return http.put(`${apiEndpoint}/${type}/${id}`, data);
	}
}
