import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";
import { gradesOptions, subjectsOptions } from "../../../Enums/Fixed";
import Loader from "../../../common/Loader";
import BaseService from "../../../../services/BaseService";

const AddSubjectsModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
	Service,
}) => {
	const [formData, setFormData] = useState({
		subjects: "",
		type: "",
		grade: "",
		subjectsAr: "",
		image: "",
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				subjects: item?.name,
				type: subjectsOptions?.find(
					(res) => res?.value === item?.educationType
				),
				grade: item?.grades?.map((res) => ({
					label: res,
					value: res,
				})),
				image: item?.image,
				subjectsAr: item?.translations?.find((res) => res?.languageId === "ar")
					?.name,
			});
		}
	}, [item]);

	const submit = (e) => {
		if (!formData?.type || !formData?.grade || !formData?.image) {
			return;
		}
		e.preventDefault();
		let data = {
			name: formData?.subjects,
			educationType: formData?.type?.value,
			grades: formData?.grade?.map((res) => res?.value),
			image: formData?.image,
			translations: [
				{
					languageId: "ar",
					name: formData?.subjectsAr,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`Subject Added Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`Subject Updated Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							image: res?.data?.data?.uploadedFiles[0]?.url,
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};
	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0">
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.subject}
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={6}>
							<AvField
								label={`${Translate[lang]?.subject} (${Translate[lang]?.english})`}
								type="text"
								placeholder={Translate[lang]?.subjects}
								bsSize="lg"
								name="subjects"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.subjects}
								onChange={(e) =>
									setFormData({
										...formData,
										subjects: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={6}>
							<AvField
								label={`${Translate[lang]?.subject} (${Translate[lang]?.arabic})`}
								type="text"
								placeholder={Translate[lang]?.subjects}
								bsSize="lg"
								name="subjectsAr"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.subjectsAr}
								onChange={(e) =>
									setFormData({
										...formData,
										subjectsAr: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">
								{Translate[lang]?.education_type}
							</label>
							<Select
								value={formData?.type}
								name="type"
								placeholder={Translate[lang]?.select}
								options={subjectsOptions}
								onChange={(e) => setFormData({ ...formData, type: e })}
							/>
						</Col>
						<Col md={12}>
							<label className="text-label">
								{Translate[lang]?.grade}{" "}
								<span>({Translate[lang]?.multi_select})</span>
							</label>
							<Select
								value={formData?.grade}
								name="grade"
								placeholder={Translate[lang]?.select}
								options={gradesOptions}
								isMulti
								onChange={(e) => setFormData({ ...formData, grade: e })}
							/>
						</Col>
						<Col md={12} className="add-gallery mt-3">
							<label className="m-0">{Translate[lang]?.image}</label>
							<div className="image-placeholder">
								<div className="avatar-edit">
									<input
										type="file"
										multiple
										onChange={(e) => fileHandler(e)}
										id={`imageUpload1`}
									/>
									<label htmlFor={`imageUpload1`} name=""></label>
								</div>
								<div className="avatar-preview2 m-auto">
									<div id={`imagePreview`} className="images">
										{
											<>
												<i className="la la-plus-circle" />
												<span style={{ fontSize: "18px", fontWeight: "600" }}>
													{Translate[lang]?.add} {Translate[lang]?.image}
												</span>
											</>
										}
										{loading && <Loader />}
									</div>
								</div>
							</div>
							{!!formData?.image && (
								<div className="row">
									<div className="col-md-6 -col-12 mb-3">
										<div className="banner_image w-auto">
											<img
												src={formData?.image}
												alt="subjectImage"
												className="w-100"
											/>
											<a
												href={formData?.image}
												target="_blank"
												rel="noreferrer">
												<i className="la la-eye" />
											</a>
											<i
												className="la la-trash"
												onClick={() => {
													setFormData({
														...formData,
														image: "",
													});
												}}
											/>
										</div>
									</div>
								</div>
							)}
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.subject}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddSubjectsModal;
