import { useEffect, useState } from "react";
import { Button, Col, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";
import BranchesService from "../../../../../services/BranchesService";

const AddFacilitiesModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
	type,
	Service,
}) => {
	const [formData, setFormData] = useState({
		facility_type: "",
		facility_type_ar: "",
		facility_plan: "",
		scheduled_time: "",
		assigned_branches: "",
	});
	let options = [
		{ label: "Weekly", value: "weekly" },
		{ label: "Monthly", value: "monthly" },
		{ label: "Daily", value: "daily" },
	];
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const [branchesOptions, setBranchesOptions] = useState([]);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		new BranchesService().getList().then((res) => {
			if (res?.status) {
				let info = res?.data?.data.map((branch) => {
					return {
						label: branch?.name,
						value: branch?.id,
					};
				});
				setBranchesOptions(info);
			}
		});
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				facility_type: item?.type,
				facility_type_ar: item?.translations?.find(
					(res) => res?.languageId === "ar"
				)?.type,
				facility_plan: options?.find(
					(res) => res?.value === item?.plan?.toLocaleLowerCase()
				),
				scheduled_time: item?.scheduledTime,
				assigned_branches: item?.branches?.map((res) => {
					return {
						label: res?.name,
						value: res?.id,
					};
				}),
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			type: formData.facility_type,
			plan: formData?.facility_plan?.value?.toLocaleUpperCase(),
			scheduledTime: formData?.scheduled_time,
			isActive: true,
			branchIds: formData?.assigned_branches?.map((res) => res?.value),
			translations: [
				{
					languageId: "ar",
					type: formData.facility_type_ar,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`Added Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`Updated Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Header>
					<Modal.Title>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.facility}
					</Modal.Title>
					<Button
						variant=""
						className="close"
						style={{
							right: lang === "en" ? "0" : "auto",
							left: lang === "ar" ? "0" : "auto",
						}}
						onClick={() => {
							setAddModal();
						}}>
						<span>&times;</span>
					</Button>
				</Modal.Header>
				<Modal.Body>
					<Row>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.facility_type} (${Translate[lang].english})`}
								type="text"
								placeholder={Translate[lang]?.facility_type}
								bsSize="lg"
								name="facility_type"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.facility_type}
								onChange={(e) =>
									setFormData({ ...formData, facility_type: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.facility_type} (${Translate[lang].arabic})`}
								type="text"
								placeholder={Translate[lang]?.facility_type}
								bsSize="lg"
								name="facility_type_ar"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.facility_type_ar}
								onChange={(e) =>
									setFormData({ ...formData, facility_type_ar: e.target.value })
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">
								{Translate[lang]?.facility_plan}
							</label>
							<Select
								value={formData?.facility_plan}
								name="facility_plan"
								placeholder={Translate[lang]?.select}
								options={options}
								onChange={(e) => setFormData({ ...formData, facility_plan: e })}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.scheduled_time}
								type="date"
								placeholder={Translate[lang]?.scheduled_time}
								bsSize="lg"
								name="scheduled_time"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.scheduled_time}
								onChange={(e) =>
									setFormData({ ...formData, scheduled_time: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<label className="text-label">
								{Translate[lang]?.assigned_branches}
							</label>
							<Select
								value={formData?.assigned_branches}
								name="department_manager"
								placeholder={Translate[lang]?.select}
								options={branchesOptions}
								isMulti
								onChange={(e) =>
									setFormData({ ...formData, assigned_branches: e })
								}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.facility}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddFacilitiesModal;
