import React from "react";
import { Routes, Route, Outlet } from "react-router-dom";
/// Css
import "./index.css";
import "./chart.css";
import "./step.css";

/// Layout
import Nav from "./layouts/nav";

/// Pages
import Error404 from "./common/Error404";

//Scroll To Top
import ScrollToTop from "./layouts/ScrollToTop";

import Dashboard from "./pages/Dashboard";
// import Admins from "./pages/Admin";
// import AddAdmin from "./pages/Admin/AddAdmin";
// import Profile from "./pages/Profile";
// import Projects from "./pages/Projects";
import Employees from "./pages/Employees";
import EmployeeProfile from "./pages/Employees/EmployeeProfile";
// import Assets from "./pages/Assets";
// import Payment from "./pages/Payment";
// import PaymentLink from "./pages/PaymentLink";
// import Financial from "./pages/Financial";
// import Leads from "./pages/Leads";
// import Certificate from "./pages/Certificate";
// import Attendance from "./pages/Attendance";
// import Allowances from "./pages/Allowances";
// import Notification from "./pages/Notification";
// import Holiday from "./pages/Holiday";
// import LoanManagement from "./pages/LoanManagement";

// AdminSetting
import Branches from "./pages/AdminSetting/Branches";
import ViewBranch from "./pages/AdminSetting/Branches/ViewBranch";
import Permission from "./pages/AdminSetting/CustomRules";
import AddPermission from "./pages/AdminSetting/CustomRules/AddRole";
import Departments from "./pages/AdminSetting/Departments";
import Facilities from "./pages/AdminSetting/Facilities";
import Hall from "./pages/AdminSetting/Hall";
import AddHall from "./pages/AdminSetting/Hall/AddHall";
import ViewHall from "./pages/AdminSetting/Hall/ViewHall";
import JobTitle from "./pages/AdminSetting/JobTitle";
import Organization from "./pages/AdminSetting/Organization";
import AddOrganization from "./pages/AdminSetting/Organization/AddOrganization";

// CMS
import About from "./pages/CMS/About";
import Requests from "./pages/CMS/Requests";
import Gallery from "./pages/CMS/Gallery";
import AddGallery from "./pages/CMS/Gallery/AddGallery";
import Banner from "./pages/CMS/Banner";
import Blogs from "./pages/CMS/Blogs";
import AddBlog from "./pages/CMS/Blogs/AddBlog";
import Contact from "./pages/CMS/Contact";
import PlatformSocial from "./pages/CMS/PlatformSocial";
import Static from "./pages/CMS/Static";

import Education from "./pages/Education";
import AddProgram from "./pages/Education/AddProgram";
import Entertainment from "./pages/Entertainment";
import ViewEducation from "./pages/Education/ViewEducation";
import AddEntertainmentProgram from "./pages/Entertainment/AddEntertainmentProgram";
import ViewEntertainment from "./pages/Entertainment/ViewEntertainment";
import WorkShifts from "./pages/WorkShifts";
import ContractTypes from "./pages/ContractTypes";
import PaymentMethod from "./pages/PaymentMethod";
import AddEmployees from "./pages/Employees/Active/AddEmployees";
import Categories from "./pages/Ecommerce/Categories";
import Variants from "./pages/Ecommerce/Variants";
import AddVariant from "./pages/Ecommerce/Variants/AddVariant";
import Products from "./pages/Ecommerce/Products";
import AddProduct from "./pages/Ecommerce/Products/AddProduct";
import ZoneGroup from "./pages/TeacherManagement/ZoneGroup";
import Zones from "./pages/TeacherManagement/Zones";
import ExecutivePlan from "./pages/TeacherManagement/ExecutivePlan";
import AddExecutivePlan from "./pages/TeacherManagement/ExecutivePlan/AddExecutivePlan";
import Teachers from "./pages/TeacherManagement/Teachers";
import AddTeachers from "./pages/TeacherManagement/Teachers/AddTeachers";
import Admins from "./pages/Admin";
import Users from "./pages/Users";
import EducationBooking from "./pages/Booking/Education";
import POS from "./pages/POSManagement/POS";
import AddPOS from "./pages/POSManagement/POS/AddPOS";
import EffectiveUsers from "./pages/POSManagement/EffectiveUsers";
import ViewEffectiveUsers from "./pages/POSManagement/EffectiveUsers/ViewEffectiveUsers";
import Supervisor from "./pages/ProgramManagement/Supervisor";
import SupervisorType from "./pages/ProgramManagement/SupervisorType";
import EntertainmentBooking from "./pages/Booking/Entertainment";
import GroupSettings from "./pages/ProgramManagement/GroupSettings";
import AddOnGroup from "./pages/Ecommerce/AddOnGroup";
import ViewOnGroup from "./pages/Ecommerce/AddOnGroup/ViewOnGroup";
import JoinCard from "./pages/CMS/JoinCard";

const Markup = () => {
	const allroutes = [
		// Dashboard
		{ url: "/", component: <Dashboard /> },

		// Admin Settings
		{ url: "organization", component: <Organization /> },
		{ url: "organization/:type", component: <AddOrganization /> },
		{ url: "halls", component: <Hall /> },
		{ url: "halls/:type", component: <AddHall /> },
		{ url: "halls/view", component: <ViewHall /> },
		{ url: "branches", component: <Branches /> },
		{ url: "branches/:id", component: <ViewBranch /> },
		{ url: "facilities", component: <Facilities /> },
		{ url: "departments", component: <Departments /> },
		{ url: "job_titles", component: <JobTitle /> },
		{ url: "roles", component: <Permission /> },
		{ url: "roles/add_role", component: <AddPermission /> },
		{ url: "roles/:id", component: <AddPermission /> },

		// Teacher Management
		{ url: "teacher/zone-group", component: <ZoneGroup /> },
		{ url: "teacher/zones", component: <Zones /> },
		{ url: "teacher/executive-plan", component: <ExecutivePlan /> },
		{ url: "teacher/executive-plan/add", component: <AddExecutivePlan /> },
		{ url: "teachers", component: <Teachers /> },
		{ url: "teachers/:type", component: <AddTeachers /> },

		//CMS
		{ url: "join-card", component: <JoinCard /> },
		{ url: "about", component: <About /> },
		{ url: "gallery", component: <Gallery /> },
		{ url: "gallery/:type", component: <AddGallery /> },
		{ url: "banner", component: <Banner /> },
		{ url: "blogs", component: <Blogs /> },
		{ url: "blogs/:type", component: <AddBlog /> },
		{ url: "contact", component: <Contact /> },
		{ url: "requests", component: <Requests /> },
		{ url: "platformSocial", component: <PlatformSocial /> },
		{ url: "static", component: <Static /> },

		// Program Manament
		{ url: "education", component: <Education /> },
		{ url: "education/add-program", component: <AddProgram /> },
		{ url: "education/edit-program", component: <AddProgram /> },
		{ url: "education/:id", component: <ViewEducation /> },
		{ url: "entertainment", component: <Entertainment /> },
		{ url: "entertainment/:id", component: <ViewEntertainment /> },
		{ url: "supervisors", component: <Supervisor /> },
		{ url: "supervisors-type", component: <SupervisorType /> },
		{ url: "group-settings", component: <GroupSettings /> },
		{
			url: "entertainment/add-program",
			component: <AddEntertainmentProgram />,
		},
		{
			url: "entertainment/edit-program",
			component: <AddEntertainmentProgram />,
		},

		// Work Shifts
		{ url: "work-shifts", component: <WorkShifts /> },
		{ url: "contract-types", component: <ContractTypes /> },
		{ url: "payment-methods", component: <PaymentMethod /> },

		// HR
		{ url: "employees", component: <Employees /> },
		{ url: "employees/add-employee", component: <AddEmployees /> },
		{ url: "employees/profile/:id", component: <EmployeeProfile /> },

		// E-Commerce
		{ url: "categories", component: <Categories /> },
		{ url: "variants", component: <Variants /> },
		{ url: "variants/:type", component: <AddVariant /> },
		{ url: "products", component: <Products /> },
		{ url: "products/:type", component: <AddProduct /> },
		{ url: "add-on-group", component: <AddOnGroup /> },
		{ url: "add-on-group/:id", component: <ViewOnGroup /> },

		// Users
		{ url: "/users", component: <Users /> },

		// POS Management
		{ url: "/pos", component: <POS /> },
		{ url: "/pos/:type", component: <AddPOS /> },
		{ url: "/effective-users", component: <EffectiveUsers /> },
		{ url: "/effective-users/view", component: <ViewEffectiveUsers /> },

		// Booking
		{ url: "/booking/education", component: <EducationBooking /> },
		{ url: "/booking/entertainment", component: <EntertainmentBooking /> },

		// Admins
		{ url: "/admins", component: <Admins /> },
		// { url: "add-admins", component: <AddAdmin /> },
		// { url: "edit-admin/:id/:name", component: <AddAdmin /> },

		// Assets
		// { url: "assets", component: <Assets /> },

		// Attendance
		// { url: "attendance", component: <Attendance /> },

		// Financial
		// { url: "financial", component: <Financial /> },

		// Employees
		// { url: "employees", component: <Employees /> },
		// { url: "employees/profile/:id", component: <EmployeeProfile /> },

		// Payment Link
		// { url: "payment-link", component: <PaymentLink /> },

		// Holiday
		// { url: "holiday", component: <Holiday /> },

		// Projects
		// { url: "projects", component: <Projects /> },

		// Leads
		// { url: "leads", component: <Leads /> },

		// Deduction
		// { url: "deduction", component: <Deduction /> },

		// Payment
		// { url: "payment", component: <Payment /> },

		// Profile
		// { url: "profile", component: <Profile /> },

		// Allowances
		// { url: "allowances", component: <Allowances /> },

		// Requests
		// { url: "requests", component: <Requests /> },

		// Loans
		// { url: "loans", component: <LoanManagement /> },

		// Notification
		// { url: "notification", component: <Notification /> },

		// Certificate
		// { url: "employee-letters", component: <Certificate /> },

		// Error
		{ url: "*", component: <Error404 /> },
	];

	return (
		<>
			<Routes>
				<Route path="page-error-404" element={<Error404 />} />
				<Route element={<MainLayout />}>
					{allroutes.map((data, i) => (
						<Route
							key={i}
							exact
							path={`${data.url}`}
							element={data.component}
						/>
					))}
				</Route>
			</Routes>
			<ScrollToTop />
		</>
	);
};

function MainLayout() {
	return (
		<div id="main-wrapper overflow-auto" className={`show `}>
			<Nav />
			<div
				className="content-body"
				style={{ minHeight: window.screen.height - 45 }}>
				<div className="container-fluid">
					<Outlet />
				</div>
			</div>
		</div>
	);
}

export default Markup;
