import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import ProductService from "../../../../../services/ProductService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import BaseService from "../../../../../services/BaseService";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw, ContentState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import { gradesOptions } from "../../../../Enums/Fixed";
import SubjectsService from "../../../../../services/SubjectsService";
import Loader from "../../../../common/Loader";
import "../style.scss";

const Digital = ({ isActive, setIsActive }) => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name_en: "",
		name_ar: "",
		subject: "",
		grade: "",
		price: "",
		discount: "",
		videos: [
			{
				name: "",
				duration: "",
			},
		],
		description: EditorState.createEmpty(),
		description_ar: EditorState.createEmpty(),
		add_on_group: false,
	});
	const [loading, setLoading] = useState(false);
	const [videos, setVideos] = useState([{ url: "", loading: false }]);
	const [id, setId] = useState(null);
	const [subjectOptions, setSubjectOptions] = useState([]);
	const navigate = useNavigate();
	const subjectsService = new SubjectsService();
	const productService = new ProductService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let promiseFun = [subjectsService.getList()];
		if (!!location?.state?.id)
			promiseFun?.push(productService.find(location?.state?.id));

		Promise.all(promiseFun).then((res) => {
			let subjects = [];
			if (res[0]?.status) {
				let info = res[0]?.data?.data?.data?.map((cat) => {
					return {
						label:
							lang === "en"
								? cat?.name
								: cat?.translations?.find((trans) => trans?.languageId === "ar")
										?.name,
						value: cat?.id,
					};
				});
				setSubjectOptions(info);
				subjects = info;
			}
			if (!!location?.state?.id) {
				if (res[1]?.status === 200) {
					const response = res[1]?.data?.data;
					setIsActive(response?.isActive);
					setVideos(
						response?.videos?.map((vid) => ({
							url: vid?.url,
							loading: false,
						}))
					);
					setId(location?.state?.id);
					setFormData({
						name_en: response?.name,
						name_ar: response?.translations?.find(
							(trans) => trans?.languageId === "ar"
						)?.name,
						subject: subjects?.find(
							(sub) => sub?.value === response?.subjectId
						),
						grade: {
							label: response?.grade[0],
							value: response?.grade[0],
						},
						price: response?.price,
						discount: response?.discount,
						videos: response?.videos?.map((vid) => ({
							name: vid?.name,
							duration: vid?.duration,
						})),
						description: EditorState.createWithContent(
							ContentState.createFromBlockArray(
								htmlToDraft(response?.description?.replaceAll("&lt;", "<"))
							)
						),
						description_ar: EditorState.createWithContent(
							ContentState.createFromBlockArray(
								htmlToDraft(
									response?.translations
										?.find((trans) => trans?.languageId === "ar")
										?.description?.replaceAll("&lt;", "<")
								)
							)
						),
						add_on_group: response?.addOnGroup,
					});
				}
			}
		});
	}, []);

	const fileHandler = (e, index) => {
		let files = e.target.files;
		const filesData = Object.values(files);

		if (!filesData?.length) {
			return;
		}
		setVideos((prevVideos) =>
			prevVideos.map((video, i) =>
				i === index ? { ...video, loading: true } : video
			)
		);
		setLoading(true);
		new BaseService()
			.postUploadVideo(filesData[0])
			.then((res) => {
				if (!!res?.status) {
					setVideos((prevVideos) =>
						prevVideos.map((video, i) =>
							i === index
								? {
										url: res?.data?.data?.uploadedFiles[0]?.url,
										loading: false,
								  }
								: video
						)
					);
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	const onDigitalSubmit = (e) => {
		e.preventDefault();
		let data = {
			type: "DIGITAL",
			name: formData?.name_en,
			price: Number(formData?.price),
			discount: Number(formData?.discount),
			description: draftToHtml(
				convertToRaw(formData?.description.getCurrentContent())
			),
			addOnGroup: formData.add_on_group,
			isActive: isActive,
			subjectId: formData?.subject?.value,
			grade: [formData?.grade?.value],
			numberOfVideos: videos?.filter((res) => !!res?.video?.url)?.length,
			videos: videos?.map((res, index) => {
				return {
					name: formData?.videos[index]?.name,
					url: res?.url,
					duration: formData?.videos[index]?.duration,
				};
			}),
			translations: [
				{
					languageId: "ar", // Arabic language ID
					name: formData?.name_ar,
					description: draftToHtml(
						convertToRaw(formData?.description_ar.getCurrentContent())
					),
				},
			],
		};

		if (!!id) {
			setLoading(true);
			productService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/products", { state: "DIGITAL" });
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			productService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/products", { state: "DIGITAL" });
				}
				setLoading(false);
			});
		}
	};

	return (
		<AvForm onValidSubmit={onDigitalSubmit}>
			<div className="row">
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.course_name} (${Translate[lang]?.english})`}
						name={`name_en`}
						type="text"
						value={formData?.name_en}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.course_name}
						onChange={(e) => {
							setFormData({ ...formData, name_en: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.course_name} (${Translate[lang]?.arabic})`}
						name={`name_ar`}
						type="text"
						value={formData?.name_ar}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.course_name}
						onChange={(e) => {
							setFormData({ ...formData, name_ar: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3">
					<label>{Translate[lang]?.subject}</label>
					<Select
						value={formData?.subject}
						options={subjectOptions}
						placeholder={`${Translate[lang]?.subject}`}
						onChange={(e) => setFormData({ ...formData, subject: e })}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3">
					<label>{Translate[lang]?.grade}</label>
					<Select
						value={formData?.grade}
						options={gradesOptions}
						placeholder={`${Translate[lang]?.grade}`}
						onChange={(e) => setFormData({ ...formData, grade: e })}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.price}
						name={`price`}
						type="number"
						value={formData?.price}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.price}
						onChange={(e) => {
							setFormData({ ...formData, price: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.discount} %`}
						name={`discount`}
						type="number"
						value={formData?.discount}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.percentage}
						onChange={(e) => {
							setFormData({ ...formData, discount: e.target.value });
						}}
					/>
				</div>
			</div>
			<div
				className="row pt-3 pb-2 m-0"
				style={{ boxShadow: "0 0 4px #dedede", borderRadius: "10px" }}>
				{formData?.videos?.map((video, index) => {
					return (
						<>
							<div className="col-lg-4 col-sm-4">
								<AvField
									label={`${Translate[lang]?.video_name}`}
									name={`video_name${index}`}
									type="text"
									value={video?.name}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={Translate[lang]?.video_name}
									onChange={(e) => {
										let update = formData?.videos?.map((vid, ind) => {
											if (index === ind) {
												return {
													...vid,
													name: e.target?.value,
												};
											} else {
												return vid;
											}
										});
										setFormData({ ...formData, videos: update });
									}}
								/>
							</div>
							<div className="col-lg-4 col-sm-4">
								<label className="mb-0">{Translate[lang].upload_video}</label>
								<div className="upload">
									<input
										type="file"
										name={`video${index}`}
										id={`video${index}`}
										required
										accept="video/*"
										onChange={(e) => fileHandler(e, index)}
									/>
									<p>{Translate[lang].upload_video}</p>
									{!videos[index]?.url ? (
										<svg
											width="24"
											height="24"
											viewBox="0 0 24 24"
											fill="none"
											xmlns="http://www.w3.org/2000/svg">
											<path
												d="M16.44 8.9C20.04 9.21 21.51 11.06 21.51 15.11V15.24C21.51 19.71 19.72 21.5 15.25 21.5H8.73998C4.26998 21.5 2.47998 19.71 2.47998 15.24V15.11C2.47998 11.09 3.92998 9.24 7.46998 8.91"
												stroke="#0F50A6"
												strokeWidth="1.5"
												strokeLinecap="round"
												strokeLinejoin="round"
											/>
											<path
												d="M12 15V3.62"
												stroke="#0F50A6"
												strokeWidth="1.5"
												strokeLinecap="round"
												strokeLinejoin="round"
											/>
											<path
												d="M15.35 5.85L12 2.5L8.65002 5.85"
												stroke="#0F50A6"
												strokeWidth="1.5"
												strokeLinecap="round"
												strokeLinejoin="round"
											/>
										</svg>
									) : (
										<svg
											width="24"
											height="24"
											viewBox="0 0 16 16"
											fill="none"
											xmlns="http://www.w3.org/2000/svg">
											<path
												d="M8 1.33333C4.32667 1.33333 1.33334 4.32667 1.33334 8C1.33334 11.6733 4.32667 14.6667 8 14.6667C11.6733 14.6667 14.6667 11.6733 14.6667 8C14.6667 4.32667 11.6733 1.33333 8 1.33333ZM11.1867 6.46667L7.40667 10.2467C7.31334 10.34 7.18667 10.3933 7.05334 10.3933C6.92 10.3933 6.79334 10.34 6.7 10.2467L4.81334 8.36C4.62 8.16667 4.62 7.84667 4.81334 7.65333C5.00667 7.46 5.32667 7.46 5.52 7.65333L7.05334 9.18667L10.48 5.76C10.6733 5.56667 10.9933 5.56667 11.1867 5.76C11.38 5.95333 11.38 6.26667 11.1867 6.46667Z"
												fill="#21AA37"
											/>
										</svg>
									)}
								</div>
								{videos[index]?.loading && (
									<div>
										<Loader />
									</div>
								)}
								{!!videos[index]?.url && !videos[index]?.loading && (
									<div className="about_image position-relative mt-3">
										<video className="w-100" controls>
											<source src={videos[index]?.url} type="video/mp4" />
											<source src={videos[index]?.url} type="video/ogg" />
										</video>
										<i
											style={{ top: "8px", right: "8px" }}
											className="la la-trash cursor-pointer fs-18 text-danger position-absolute"
											onClick={() =>
												setVideos(videos?.filter((_, ind) => index !== ind))
											}
										/>
									</div>
								)}
							</div>
							<div className="col-lg-3 col-sm-3">
								<AvField
									label={Translate[lang]?.duration_of_video}
									name={`duration${index}`}
									type="text"
									value={video?.duration}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={Translate[lang]?.duration}
									onChange={(e) => {
										let update = formData?.videos?.map((vid, ind) => {
											if (index === ind) {
												return {
													...vid,
													duration: e.target?.value,
												};
											} else {
												return vid;
											}
										});
										setFormData({ ...formData, videos: update });
									}}
								/>
							</div>
							{index === 0 && (
								<div className="col-1 mt-3">
									<i
										className="la la-plus-circle mt-3 text-primary cursor-pointer fs-32"
										onClick={() => {
											setFormData({
												...formData,
												videos: [
													...formData?.videos,
													{
														name: "",
														duration: "",
													},
												],
											});
											setVideos([...videos, { url: "", loading: false }]);
										}}></i>
								</div>
							)}
							{index > 0 && (
								<div className="col-1 mt-3">
									<i
										className="la la-times-circle mt-3 text-danger cursor-pointer fs-32"
										onClick={() => {
											setFormData({
												...formData,
												videos: formData?.videos?.filter(
													(_, ind) => ind !== index
												),
											});
											setVideos([...videos.filter((_, ind) => index !== ind)]);
										}}></i>
								</div>
							)}
						</>
					);
				})}
			</div>
			<div className="row mt-3">
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>{Translate[lang].english_description}</label>
					<Editor
						editorState={formData.description}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description: e })
						}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>{Translate[lang].arabic_description}</label>
					<Editor
						editorState={formData.description_ar}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description_ar: e })
						}
					/>
				</div>
				<div className="col-12">
					<hr />
				</div>
				<div className="col-12 mb-3">
					<label className="d-flex gap-8">
						<input
							type="checkbox"
							name="add_on_group"
							className="add-product-check"
							checked={formData?.add_on_group}
							onChange={(e) =>
								setFormData({ ...formData, add_on_group: e.target.checked })
							}
						/>
						{Translate[lang].add_on_group}
					</label>
				</div>

				{!!id && (
					<div className="col-lg-6 col-sm-6 mt-2 mb-5">
						<Button
							variant="secondary"
							type="button"
							className="w-100 fs-14"
							onClick={() => navigate("/products")}>
							{Translate[lang]?.cancel}
						</Button>
					</div>
				)}
				<div className="col-lg-6 col-sm-6 mt-2 mb-5">
					<Button
						variant="primary"
						type="submit"
						className="w-100 fs-14"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</div>
			</div>
		</AvForm>
	);
};

export default Digital;
