import React, { useEffect, useState } from "react";
import { Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Physical from "./Physical";
import Digital from "./Digital";
import "../style.scss";
import Service from "./Service";
import Packages from "./Packages";

const AddProduct = () => {
	const location = useLocation();
	const tabs = [
		"physical_product",
		"digital_product",
		"service_product",
		"packages_product",
	];
	const [selectedTab, setSelectedTab] = useState("");
	const [id, setId] = useState(null);
	const [isActive, setIsActive] = useState(true);
	const navigate = useNavigate();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;
		if (location?.pathname.includes("edit")) {
			setId(item?.id);
			if (item?.type === "PHYSICAL") setSelectedTab(tabs[0]);
			if (item?.type === "DIGITAL") setSelectedTab(tabs[1]);
			if (item?.type === "SERVICE") setSelectedTab(tabs[2]);
			if (item?.type === "PACKAGE") setSelectedTab(tabs[3]);
		} else {
			setId(null);
			setSelectedTab(tabs[0]);
		}
	}, [location]);

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{
						cursor: "pointer",
						transform: lang === "ar" ? "rotate(180deg)" : "",
					}}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/products")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{!!id ? Translate[lang]?.edit : Translate[lang]?.add}{" "}
					{Translate[lang]?.product}
				</label>

				<Form.Check
					type="switch"
					id="custom-switch"
					checked={isActive}
					onChange={(e) => setIsActive(e.target.checked)}
				/>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					{!id && (
						<div className="tabs">
							{tabs?.map((tab, index) => {
								return (
									<p
										key={index}
										onClick={() => setSelectedTab(tab)}
										className={`tab ${
											index === 1 || index === 2 ? "px-3" : ""
										} ${selectedTab === tab ? "active" : ""}`}>
										{Translate[lang][tab]}
									</p>
								);
							})}
						</div>
					)}

					{selectedTab === tabs[0] && (
						<Physical isActive={isActive} setIsActive={setIsActive} />
					)}
					{selectedTab === tabs[1] && (
						<Digital isActive={isActive} setIsActive={setIsActive} />
					)}
					{selectedTab === tabs[2] && (
						<Service isActive={isActive} setIsActive={setIsActive} />
					)}
					{selectedTab === tabs[3] && (
						<Packages isActive={isActive} setIsActive={setIsActive} />
					)}
				</Col>
			</Row>
		</>
	);
};

export default AddProduct;
