import React, { Fragment } from "react";
import { useState } from "react";
import { Row, Col, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import CardItem from "./CardItem";
import { Translate } from "../../../Enums/Tranlate";
import NoData from "../../../common/NoData";
import Loader from "../../../common/Loader";
import Pagination from "../../../common/Pagination/Pagination";
import BookingService from "../../../../services/BookingService";

const EducationBooking = () => {
	const tabs = ["at_home", "at_center", "online"];
	const [selectTab, setSelectTab] = useState("at_home");
	const [data, setData] = useState([]);
	const [hasData, setHasData] = useState(null);
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const bookingService = new BookingService();

	return (
		<Fragment>
			<div className="mb-3">
				<div className="tabs-div">
					{tabs?.map((tab, index) => {
						return (
							<p
								key={index}
								className={`${tab === selectTab ? "activeP" : ""}`}
								onClick={() => {
									setSelectTab(tab);
									setShouldUpdate((prev) => !prev);
								}}>
								{Translate[lang][tab]}
							</p>
						);
					})}
				</div>
			</div>
			<div className="mb-4 d-flex justify-content-between align-items-center">
				<div>
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang][selectTab]}
					</span>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "300px" }}>
									<Loader />
								</div>
							)}
							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>{Translate[lang]?.name}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.education_type}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.track}</strong>
											</th>
											{/* <th>
												<strong>{Translate[lang]?.platform}</strong>
											</th> */}
											<th>
												<strong>{Translate[lang]?.phone}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.email}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.civil_id}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.gender}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.grade}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.subjects}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.total_price}</strong>
											</th>
											{/* <th>
												<strong>{Translate[lang]?.start_date}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.end_date}</strong>
											</th> */}
											{/* <th>
												<strong>{Translate[lang]?.action}</strong>
											</th> */}
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							<Pagination
								setData={setData}
								service={bookingService}
								shouldUpdate={shouldUpdate}
								setHasData={setHasData}
								setLoading={setLoading}
								param={{ bookingType: selectTab.toLocaleUpperCase() }}
							/>
						</div>
					</Card>
				</Col>
			</Row>
		</Fragment>
	);
};

export default EducationBooking;
