import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/hall";
const apiEntertainmentHallsEndpoint =
	API_BASE_URL_ENV() + "/admin/hall/select/list?type=ENTERTAINMENT";
const apiEducationHallsEndpoint =
	API_BASE_URL_ENV() + "/admin/hall/select/list?type=EDUCATION";

export default class HallService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}
	getEntertainmentHalls(params) {
		return http.get(apiEntertainmentHallsEndpoint, { params });
	}
	getEducationHalls(params) {
		return http.get(apiEducationHallsEndpoint, { params });
	}
}
