import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/ecommerce/product-group";
const apiAssignEndpoint =
	API_BASE_URL_ENV() + "/admin/ecommerce/product-group/assign";

export default class AddOnGroupService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	postAsignedProduct(data) {
		return http.post(apiAssignEndpoint, data);
	}

	removeAssign(data) {
		return http.post(`${apiEndpoint}/remove`, data);
	}
}
