import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import Select from "react-select";
import PhoneInput from "react-phone-input-2";
import Admin2Service from "../../../../services/Admin2Service";
import RoleService from "../../../../services/RoleService";
import { Translate } from "../../../Enums/Tranlate";
import "../style.scss";

const AddAdminsModal = ({ addModal, setAddModal, item, setShouldUpdate }) => {
	const [formData, setFormData] = useState({
		name: "",
		phone: "",
		email: "",
		password: "",
		role: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [options, setOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const [rolesOptions, setRolesOptions] = useState([]);
	const lang = useSelector((state) => state.auth.lang);
	const admin2Service = new Admin2Service();
	const roleService = new RoleService();

	useEffect(() => {
		Promise.all([roleService.getList()]).then((res) => {
			if (res[0]?.status === 200) {
				let data = res[0]?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setRolesOptions(data);
			}

			if (Object.keys(item)?.length === 0) {
				setIsAdd(true);
			} else {
				setIsAdd(false);
				setFormData({
					id: item?.id,
					name: item?.name,
					isActive: item?.isActive,
					email: item?.email,
					phone: item?.phoneNumber,
					role: {
						label: item?.role.name,
						value: item?.role.id,
					},
				});
			}
		});
	}, []);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			email: formData?.email,
			password: formData?.password,
			phoneNumber: formData?.phone,
			roleId: formData?.role?.value,
			isActive: formData?.isActive,
		};

		setLoading(true);
		if (isAdd) {
			admin2Service
				.create(data)
				?.then((res) => {
					if (res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			admin2Service
				.update(formData?.id, data)
				?.then((res) => {
					if (res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.admin}
								</p>

								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>

						<Col md={12}>
							<AvField
								label={Translate[lang]?.name}
								type="text"
								placeholder={Translate[lang]?.name}
								bsSize="lg"
								name="name"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.name}
								onChange={(e) =>
									setFormData({
										...formData,
										name: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label>{Translate[lang]?.phone}</label>
							<PhoneInput
								country={"kw"}
								enableSearch={true}
								value={formData?.phone}
								onChange={(e) => setFormData({ ...formData, phone: e })}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.email}
								type="email"
								placeholder={Translate[lang]?.email}
								bsSize="lg"
								name="email"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.email}
								onChange={(e) =>
									setFormData({
										...formData,
										email: e.target.value,
									})
								}
							/>
						</Col>
						{isAdd && (
							<Col md={12}>
								<AvField
									label={Translate[lang]?.password}
									type="text"
									placeholder={Translate[lang]?.password}
									bsSize="lg"
									name="password"
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									value={formData.password}
									onChange={(e) =>
										setFormData({
											...formData,
											password: e.target.value,
										})
									}
								/>
							</Col>
						)}
						<Col md={12}>
							<label className="text-label">{Translate[lang]?.roles}</label>
							<Select
								value={formData?.role}
								name="role"
								placeholder={Translate[lang]?.select}
								options={rolesOptions}
								onChange={(e) => setFormData({ ...formData, role: e })}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.admin}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddAdminsModal;
