import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import PhoneInput from "react-phone-input-2";
import BranchesService from "../../../../../services/BranchesService";
import OrganizationService from "../../../../../services/OrganizationService";
import WorkShiftService from "../../../../../services/WorkShiftService";
import EmployeesService from "../../../../../services/EmployeesService";
import POSService from "../../../../../services/POSService";
import EffectiveUsersService from "../../../../../services/EffectiveUsersService";

const AddEffectiveUserModal = ({
	addModal,
	setAddModal,
	item,
	assign,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		isActive: true,
		employee: "",
		assign_employee: "",
		pos: "",
		email: "",
		phone: "",
		user_name: "",
		password: "",
		confirm_password: "",
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const effectiveUsersService = new EffectiveUsersService();
	const [showPassword, setShowPassword] = useState(false);
	const [showPassword2, setShowPassword2] = useState(false);
	const [employeesOptions, setEmployeesOptions] = useState([]);
	const [posOptions, setPosOptions] = useState([]);

	useEffect(() => {
		if (assign && !!formData?.pos?.value) {
			effectiveUsersService.getAssignList(formData?.pos?.value).then((res) => {
				if (res?.status) {
				}
			});
		}
	}, [formData]);

	useEffect(() => {
		if (!assign) {
			let promises = [
				new EmployeesService().getList(),
				new POSService().getList(),
			];
			Promise.all(promises).then((res) => {
				if (res[0]?.status === 200) {
					let data = res[0]?.data?.data?.data?.map((info) => {
						return {
							label: info?.name,
							value: info?.id,
						};
					});
					setEmployeesOptions(data);
				}
				if (res[1]?.status === 200) {
					let data = res[1]?.data?.data?.map((info) => {
						return {
							label: info?.name,
							value: info?.id,
						};
					});
					setPosOptions(data);
				}
			});
		}
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			new EffectiveUsersService().find(item?.id).then((res) => {
				if (res?.status) {
					let response = res?.data?.data;
					setFormData({
						id: item?.id,
						email: response?.email,
						phone: response?.phoneNumber,
						user_name: item?.username,
						isActive: item?.isActive,
						pos: {
							label: response?.posName,
							value: response?.posId,
						},
						employee: {
							label: response?.employeeName,
							value: response?.employeeId,
						},
						assign_employee: "",
					});
				}
			});
		}
	}, []);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			employeeId: formData?.employee?.value,
			posId: formData?.pos?.value,
			email: formData?.email,
			phoneNumber: formData?.phone,
			username: formData?.user_name,
			isActive: formData?.isActive,
		};

		setLoading(true);
		if (isAdd) {
			if (!!formData?.password) data["password"] = formData?.password;
			if (!!formData?.confirm_password)
				data["confirmPassword"] = formData?.confirm_password;
			effectiveUsersService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			effectiveUsersService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	const assignFun = (e) => {
		e.preventDefault();
		return;
		let data = {
			employeeId: formData?.employee?.value,
			posId: formData?.pos?.value,
			email: formData?.email,
			phoneNumber: formData?.phone,
			username: formData?.user_name,
			isActive: formData?.isActive,
		};

		setLoading(true);
		effectiveUsersService
			.update(formData?.id, data)
			?.then((res) => {
				if (res && res?.status === 200) {
					toast.success(res?.data?.message);
					setShouldUpdate((prev) => !prev);
					setAddModal();
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm
				className="form-horizontal"
				onValidSubmit={assign ? assignFun : submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.effective_user}
								</p>
								{!assign && (
									<Form.Check
										type="switch"
										id="custom-switch"
										checked={formData?.isActive}
										onChange={(e) =>
											setFormData({ ...formData, isActive: e.target.checked })
										}
									/>
								)}
							</div>
						</Col>
						{assign && (
							<Col md={12} className="mb-3">
								<label className="text-label">
									{Translate[lang]?.employee}
								</label>
								<Select
									value={formData?.employee}
									name="employee"
									placeholder={Translate[lang]?.select}
									options={employeesOptions}
									onChange={(e) => setFormData({ ...formData, employee: e })}
								/>
							</Col>
						)}
						{!assign && (
							<>
								<Col md={12} className="mb-3">
									<label className="text-label">
										{Translate[lang]?.employee}
									</label>
									<Select
										value={formData?.employee}
										name="employee"
										placeholder={Translate[lang]?.select}
										options={employeesOptions}
										onChange={(e) => setFormData({ ...formData, employee: e })}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label className="text-label">{Translate[lang]?.pos}</label>
									<Select
										value={formData?.pos}
										name="pos"
										placeholder={Translate[lang]?.select}
										options={posOptions}
										onChange={(e) => setFormData({ ...formData, pos: e })}
									/>
								</Col>
								<Col md={12}>
									<AvField
										label={Translate[lang]?.email}
										type="email"
										placeholder={Translate[lang]?.email}
										bsSize="lg"
										name="email"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.email}
										onChange={(e) =>
											setFormData({ ...formData, email: e.target.value })
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label>{Translate[lang]?.phone}</label>
									<PhoneInput
										country={"kw"}
										enableSearch={true}
										value={formData?.phone}
										onChange={(e) => setFormData({ ...formData, phone: e })}
									/>
								</Col>
								<Col md={12}>
									<AvField
										label={Translate[lang]?.user_name}
										type="text"
										placeholder={Translate[lang]?.user_name}
										bsSize="lg"
										name="user_name"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.user_name}
										onChange={(e) =>
											setFormData({ ...formData, user_name: e.target.value })
										}
									/>
								</Col>
								{isAdd && (
									<Col md={12}>
										<div className="position-relative">
											<AvField
												label={Translate[lang]?.password}
												type={showPassword ? "text" : "password"}
												placeholder={Translate[lang]?.password}
												bsSize="lg"
												name="password"
												validate={{
													required: {
														value: true,
														errorMessage: Translate[lang].field_required,
													},
												}}
												value={formData.password}
												onChange={(e) =>
													setFormData({ ...formData, password: e.target.value })
												}
											/>
											{showPassword ? (
												<i
													className="la la-eye-slash position-absolute cursor-pointer"
													style={{
														top: "44px",
														right: lang === "en" ? "8%" : "auto",
														left: lang === "ar" ? "8%" : "auto",
													}}
													onClick={() => setShowPassword(false)}></i>
											) : (
												<i
													className="la la-eye position-absolute cursor-pointer"
													style={{
														top: "44px",
														right: lang === "en" ? "8%" : "auto",
														left: lang === "ar" ? "8%" : "auto",
													}}
													onClick={() => setShowPassword(true)}></i>
											)}
										</div>
									</Col>
								)}
								{isAdd && (
									<Col md={12}>
										<div className="position-relative">
											<AvField
												label={Translate[lang]?.confirm_password}
												type={showPassword2 ? "text" : "password"}
												placeholder={Translate[lang]?.password}
												bsSize="lg"
												name="confirm_password"
												validate={{
													required: {
														value: true,
														errorMessage: Translate[lang].field_required,
													},
												}}
												value={formData.confirm_password}
												onChange={(e) =>
													setFormData({
														...formData,
														confirm_password: e.target.value,
													})
												}
											/>
											{showPassword2 ? (
												<i
													className="la la-eye-slash position-absolute cursor-pointer"
													style={{
														top: "44px",
														right: lang === "en" ? "8%" : "auto",
														left: lang === "ar" ? "8%" : "auto",
													}}
													onClick={() => setShowPassword2(false)}></i>
											) : (
												<i
													className="la la-eye position-absolute cursor-pointer"
													style={{
														top: "44px",
														right: lang === "en" ? "8%" : "auto",
														left: lang === "ar" ? "8%" : "auto",
													}}
													onClick={() => setShowPassword2(true)}></i>
											)}
										</div>
									</Col>
								)}
							</>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{assign ? "" : Translate[lang]?.effective_user}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddEffectiveUserModal;
