import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import SupervisorTypeService from "../../../../../services/SupervisorTypeService";

const AddSupervisorTypeModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		nameAr: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const supervisorTypeService = new SupervisorTypeService();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			supervisorTypeService.find(item?.id).then((res) => {
				if (res?.status) {
					const response = res?.data?.data;
					setFormData({
						id: item?.id,
						name: response?.name,
						nameAr: response?.translations?.find(
							(res) => res?.languageId === "ar"
						)?.name,
						isActive: response?.isActive,
					});
				}
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			isActive: formData?.isActive,
			translations: [
				{
					languageId: "ar",
					name: formData?.nameAr,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			supervisorTypeService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			supervisorTypeService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body className="pb-1">
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.supervisors_type}
								</p>
								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.name} (${Translate[lang].english})`}
								type="text"
								placeholder={Translate[lang]?.name}
								bsSize="lg"
								name="name"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.name}
								onChange={(e) =>
									setFormData({ ...formData, name: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.name} (${Translate[lang].arabic})`}
								type="text"
								placeholder={Translate[lang]?.name}
								bsSize="lg"
								name="nameAr"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.nameAr}
								onChange={(e) =>
									setFormData({ ...formData, nameAr: e.target.value })
								}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.supervisors_type}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddSupervisorTypeModal;
