import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/pos/effective-user";
const apiAssignEndpoint = API_BASE_URL_ENV() + "/admin/pos";

export default class EffectiveUsersService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	getAssignList(id) {
		return http.get(`${apiAssignEndpoint}/${id}/employees`);
	}
}
