import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import BaseService from "../../../../services/BaseService";
import "./style.scss";
import BannerService from "../../../../services/BannerService";
import JoinCardService from "../../../../services/JoinCardService";

const JoinCard = () => {
	const [formData, setFormData] = useState([
		{
			type: "ENTERTAINMENT",
			description: "",
			image: "",
			loading: false,
		},
		{
			type: "EDUCATION",
			description: "",
			image: "",
			loading: false,
		},
	]);
	const [loading, setLoading] = useState(false);
	const [deleteLoading, setDeleteLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const joinCardService = new JoinCardService();

	useEffect(() => {
		setLoading(true);
		joinCardService
			.getList()
			.then((res) => {
				if (res?.status) {
					if (!!res?.data?.data?.length) {
						let response = res?.data?.data;
						let data = formData?.map((details) => {
							if (response?.find((type) => type?.type === details?.type)?.id) {
								return response?.find((type) => type?.type === details?.type);
							} else {
								return details;
							}
						});
						setFormData(data);
					}
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	}, [shouldUpdate]);

	const onSubmit = (type) => {
		if (!type?.image && !type?.description) {
			return;
		}
		let data = {
			title: type?.type,
			description: type?.description,
			image: type?.image,
			type: type?.type,
		};

		if (!!type?.id) {
			setLoading(true);
			joinCardService.update(type?.id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(Translate[lang].updated_successfully);
					setShouldUpdate((prev) => !prev);
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			joinCardService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(
						`${Translate[lang].added} ${Translate[lang].successfully}`
					);
					setShouldUpdate((prev) => !prev);
				}
				setLoading(false);
			});
		}
	};

	const fileHandler = (e, index) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		if (filesData?.length) {
			setLoading(true);
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData((prevData) =>
							prevData.map((item, i) =>
								i === index
									? {
											...item,
											image:
												res?.data?.data?.uploadedFiles?.[0]?.url || item.image,
											loading: false,
									  }
									: item
							)
						);
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<div>
			{formData?.map((type, index) => {
				return (
					<div className="join mb-4">
						<div className="row w-100" key={index}>
							<div className="col-12">
								<label
									className="mb-3"
									style={{ fontSize: "20px", fontWeight: "600" }}>
									{Translate[lang][type?.type?.toLocaleLowerCase()]}
								</label>
							</div>
							<div className="col-lg-5 col-sm-12">
								<div className="form-group w-100">
									<div>
										<div className="join-img">
											{!type?.image && (
												<svg
													width="24"
													height="24"
													viewBox="0 0 24 24"
													fill="none"
													xmlns="http://www.w3.org/2000/svg">
													<path
														d="M9 22H15C20 22 22 20 22 15V9C22 4 20 2 15 2H9C4 2 2 4 2 9V15C2 20 4 22 9 22Z"
														stroke="#004094"
														strokeWidth="1.5"
														strokeLinecap="round"
														strokeLinejoin="round"
													/>
													<path
														d="M9 10C10.1046 10 11 9.10457 11 8C11 6.89543 10.1046 6 9 6C7.89543 6 7 6.89543 7 8C7 9.10457 7.89543 10 9 10Z"
														stroke="#004094"
														strokeWidth="1.5"
														strokeLinecap="round"
														strokeLinejoin="round"
													/>
													<path
														d="M2.66992 18.9486L7.59992 15.6386C8.38992 15.1086 9.52992 15.1686 10.2399 15.7786L10.5699 16.0686C11.3499 16.7386 12.6099 16.7386 13.3899 16.0686L17.5499 12.4986C18.3299 11.8286 19.5899 11.8286 20.3699 12.4986L21.9999 13.8986"
														stroke="#004094"
														strokeWidth="1.5"
														strokeLinecap="round"
														strokeLinejoin="round"
													/>
												</svg>
											)}
											{!!type?.image && (
												<a
													href={type?.image}
													target="_blank"
													rel="noreferrer"
													className="w-100 h-100">
													<img
														src={type?.image}
														alt="join"
														className="w-100 h-100"
													/>
												</a>
											)}
										</div>
										<div>
											<div
												className="d-flex mt-4"
												style={{
													gap: "24px",
												}}>
												<label
													className="custum-file-upload"
													for="file"
													style={{
														opacity: type?.loading || loading ? ".8" : "1",
													}}>
													<div className="icon">
														<svg
															width="25"
															height="25"
															viewBox="0 0 25 25"
															fill="none"
															xmlns="http://www.w3.org/2000/svg">
															<path
																d="M16.9405 9.39844C20.5405 9.70844 22.0105 11.5584 22.0105 15.6084V15.7384C22.0105 20.2084 20.2205 21.9984 15.7505 21.9984H9.24047C4.77047 21.9984 2.98047 20.2084 2.98047 15.7384V15.6084C2.98047 11.5884 4.43047 9.73844 7.97047 9.40844"
																stroke="#FDFDFE"
																strokeWidth="1.5"
																strokeLinecap="round"
																strokeLinejoin="round"
															/>
															<path
																d="M12.5 15.5011V4.12109"
																stroke="#FDFDFE"
																strokeWidth="1.5"
																strokeLinecap="round"
																strokeLinejoin="round"
															/>
															<path
																d="M15.8504 6.35L12.5004 3L9.15039 6.35"
																stroke="#FDFDFE"
																strokeWidth="1.5"
																strokeLinecap="round"
																strokeLinejoin="round"
															/>
														</svg>
													</div>

													<div>
														<span className="text">
															{Translate[lang].upload_image} ( 580*640 )
														</span>
													</div>
													{!type?.loading && (
														<input
															type="file"
															accept="image/*"
															disabled={loading}
															onChange={(e) => {
																if (!loading) fileHandler(e, index);
															}}
															className="upload"
															name="Upload"
														/>
													)}
												</label>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div className="col-lg-7 col-sm-12">
								<textarea
									onChange={(e) => {
										setFormData((prevData) =>
											prevData.map((item, i) =>
												i === index
													? {
															...item,
															description: e.target.value,
													  }
													: item
											)
										);
									}}
									placeholder={Translate[lang].description}
									value={type?.description}></textarea>
							</div>
							<div className="col-12 mt-3" style={{ textAlign: "end" }}>
								<Button
									variant="primary"
									disabled={type?.loading || loading}
									onClick={() => onSubmit(type)}>
									{Translate[lang].submit}
								</Button>
							</div>
						</div>
					</div>
				);
			})}
		</div>
	);
};

export default JoinCard;
