import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../../common/DeleteModal";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import { Translate } from "../../../../Enums/Tranlate";
import FacilitesService from "../../../../../services/FacilitesService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setViewModal,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const facilitesService = new FacilitesService();

	return (
		<tr key={index} className="text-center">
			<td>{item?.type}</td>
			<td>{item?.plan}</td>
			<td>{item?.scheduledTime}</td>
			<td>{item?.branches?.length}</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<img
						src={editIcon}
						onClick={() => {
							setItem(item);
							setAddModal(true);
						}}
						className="cursor-pointer"
						alt="edit"
						width={24}
					/>
					<img
						src={trashIcon}
						alt="trash"
						width={24}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.type}
					deletedItem={item}
					modelService={facilitesService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
