import { useEffect, useState } from "react";
import { Button, Col, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import "../style.scss";
import ProductService from "../../../../../services/ProductService";
import AddOnGroupService from "../../../../../services/AddOnGroupService";

const AssignModal = ({ addModal, setAddModal, item, setShouldUpdate }) => {
	const [selectProduct, setSelectProduct] = useState({});
	const [search, setSearch] = useState("");
	const [productsOptions, setProductsOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		let params = {};
		if (!!search) params = { search: search };
		new ProductService().getList(params).then((res) => {
			if (res?.status) {
				setProductsOptions(res?.data?.data);
			}
		});
	}, [search]);

	const submit = (e) => {
		e.preventDefault();
		if (!selectProduct?.id) {
			return;
		}
		let data = {
			groupProductId: item?.groupProductId,
			assignedProductId: selectProduct?.id,
		};

		setLoading(true);
		new AddOnGroupService()
			.postAsignedProduct(data)
			?.then((res) => {
				if (res && res?.status === 201) {
					toast.success(res?.data?.message);
					setShouldUpdate((prev) => !prev);
					setAddModal();
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{Translate[lang]?.assign} {Translate[lang]?.products}
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>

						<Col md={12}>
							<AvField
								type="text"
								placeholder={Translate[lang]?.search}
								bsSize="lg"
								name="search"
								value={search}
								onChange={(e) => setSearch(e.target.value)}
							/>
						</Col>
						<Col md={12}>
							<div
								className="products-menu mt-4"
								style={{ borderRadius: "5px", border: "1px solid #dedede" }}>
								{productsOptions?.map((product, index) => {
									return (
										<div
											key={product?.id}
											style={{
												borderBottom:
													productsOptions?.length - 1 > index
														? "1px solid #dedede"
														: "",
											}}
											className="d-flex align-items-center p-2 justify-content-between">
											<p className="mb-0 text-black fw-bold">{product?.name}</p>
											{product?.id !== selectProduct?.id ? (
												<Button
													variant="outline-primary"
													type="button"
													style={{ padding: "4px 24px" }}
													onClick={() => setSelectProduct(product)}>
													{Translate[lang].add}
												</Button>
											) : (
												<i
													className="la la-check-circle text-primary"
													style={{ fontSize: "24px", margin: "0 2rem" }}
												/>
											)}
										</div>
									);
								})}
							</div>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AssignModal;
