import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import PhoneInput from "react-phone-input-2";
import {
	convertDate,
	daysOptions,
	degreeOptions,
	educationTypeOptions,
	genderOptions,
	gradesOptions,
} from "../../../../Enums/Fixed";
import BaseService from "../../../../../services/BaseService";
import Loader from "../../../../common/Loader";
import SubjectsService from "../../../../../services/SubjectsService";
import ZoneService from "../../../../../services/ZoneService";
import TeacherManagementService from "../../../../../services/TeacherManagementService";
import ExecutivePlanService from "../../../../../services/ExecutivePlanService";
const countryCodes = require("country-codes-list");

const AddTeachers = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		isActive: true,
		name: "",
		nameAr: "",
		civil_id: "",
		nationality: "",
		gender: "",
		ownes_car: false,
		phone: "",
		zones: "",
		grades: "",
		subjects: "",
		academic_degree: "",
		specialization: "",
		university: "",
		teaching_certification: [],
		subjectGrades: [
			{
				subject: "",
				grade: "",
				price: "",
			},
		],
		years_of_experience: "",
		wamd_number: "",
		education_type: "",
		number_of_semester: "",
		academicYear: "",
		semesters: [],
		days: [],
	});
	const [type, setType] = useState("teacher_info"); //teacher_info
	const [selectSemester, setSelectSemester] = useState(0);
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const [zonesOptions, setZonesOptions] = useState([]);
	const [subjectsOptions, setSubjectsOptions] = useState([]);
	const [countriesOptions, setCountriesOptions] = useState([]);
	const [academicYearOptions, setAcademicYearOptions] = useState([]);
	const [customSubjects, setCustomSubjects] = useState([]);
	const navigate = useNavigate();
	const teacherManagementService = new TeacherManagementService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		const myCountryCodesObject = countryCodes.customList(
			"countryCode",
			"{countryNameEn}/{countryNameLocal}"
		);
		const entries = Object.entries(myCountryCodesObject)?.map((c) => {
			return {
				label: c[1].split("/")[0],
				value: c[1]?.split("/")[0]?.toLocaleLowerCase(),
			};
		});
		setCountriesOptions([
			{ label: "non Kuwaiti", value: "non_kuwaiti" },
			...entries,
		]);

		let promisesFun = [
			new ZoneService().getList(),
			new SubjectsService().getList(),
			new ExecutivePlanService().getList(),
		];
		if (location?.pathname.includes("edit"))
			promisesFun.push(teacherManagementService?.find(location.state?.id));
		let semestersOptions = [];
		Promise.all(promisesFun).then((res) => {
			if (res[0]?.status === 200) {
				let response1 = res[0]?.data?.data?.map((response) => {
					return {
						label:
							lang === "en"
								? response?.zoneGroupName
								: response?.zoneGroupNameAr,
						value: response?.id,
					};
				});
				setZonesOptions(response1);
			}
			if (res[1]?.status === 200) {
				let response2 = res[1]?.data?.data?.data?.map((response) => {
					return {
						label: response?.name,
						value: response?.id,
					};
				});
				setSubjectsOptions(response2);
			}
			if (res[2]?.status === 200) {
				let response3 = res[2]?.data?.data?.map((response) => {
					let info = {
						label: `${response?.educationType} / ${response?.year}`,
						year: response?.year,
						value: response?.id,
						semesters: response?.semesters,
						educationType: response?.educationType,
					};
					semestersOptions.push(info);
					return info;
				});
				setAcademicYearOptions(response3);
			}
			if (res[3]?.status === 200) {
				let info = res[3]?.data?.data;

				setFormData({
					id: info?.id,
					isActive: info?.isActive,
					name: info?.name,
					nameAr: info?.nameAr,
					civil_id: info?.civilId,
					gender: genderOptions?.find(
						(gen) => gen.value === info?.gender?.toLocaleLowerCase()
					),
					ownes_car: info?.ownsCar,
					phone: info?.phoneNumber,
					specialization: info?.specialization,
					university: info?.university,
					academic_degree: degreeOptions?.find(
						(deg) => deg?.value === info?.academicDegree
					),
					teaching_certification: info?.teachingCertifications?.map(
						(tech) => tech?.file
					),
					education_type: educationTypeOptions?.find(
						(edu) => edu?.value === info?.educationType
					),
					years_of_experience: info?.yearsOfExperience,
					wamd_number: info?.wamdNumber || "",
					number_of_semester: info?.academicYear?.numberOfSemesters,
					academicYear: {
						label: `${info?.academicYear?.educationType} / ${info?.academicYear?.year}`,
						value: info?.academicYear?.id,
					},
					grades: info?.grades?.map((grade) => ({
						label: grade,
						value: grade,
					})),
					nationality: entries?.find((ent) => ent?.value === info?.nationality),
					subjectGrades: info?.teacherSubjectGrades?.map((subGrade) => ({
						subject: {
							label: subGrade?.subject?.name,
							value: subGrade?.subject?.id,
						},
						grade: {
							label: subGrade?.grade,
							value: subGrade?.grade,
						},
						price: subGrade?.price,
					})),
					subjects: info?.teacherSubjectGrades?.map((subGrade) => ({
						label: subGrade?.subject?.name,
						value: subGrade?.subject?.id,
					})),
					zones: info?.zones?.map((zone) => {
						return {
							label: zone?.zoneToZoneGroup?.zoneGroup?.name,
							value: zone?.zoneToZoneGroup?.id,
						};
					}),
					semesters:
						semestersOptions
							?.find((sem) => sem?.value === info?.academicYear?.id)
							?.semesters?.map((res) => {
								return {
									...res,
									endDate: res?.endDate?.split("T")[0],
									examNightsEndDate: res?.examNightsEndDate?.split("T")[0],
									examNightsStartDate: res?.examNightsStartDate?.split("T")[0],
									finalRevisionEndDate:
										res?.finalRevisionEndDate?.split("T")[0],
									finalRevisionStartDate:
										res?.finalRevisionStartDate?.split("T")[0],
									startDate: res?.startDate?.split("T")[0],
								};
							}) || [],
					days: info?.teacherAvailability?.map((teacherAv) => ({
						semesterId: teacherAv?.semester?.id,
						day: teacherAv?.day,
						startTime: teacherAv?.startTime,
						endTime: teacherAv?.endTime,
					})),
				});
				setCustomSubjects(
					info?.teacherSubjectGrades?.map((subGrade) => ({
						label: subGrade?.subject?.name,
						value: subGrade?.subject?.id,
					}))
				);
			}
		});

		if (location?.pathname.includes("edit")) setId(location?.state?.id);
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		if (
			!formData?.grades ||
			!formData?.zones ||
			!formData?.nationality ||
			!formData?.teaching_certification?.length ||
			!formData?.subjectGrades?.filter(
				(res) => !!res?.grade?.value && !!res?.subject?.value
			)?.length
		) {
			toast.error("Complete All Phases");
			return;
		}
		let data = {
			name: formData?.name,
			nameAr: formData?.nameAr,
			civilId: formData?.civil_id,
			nationality: formData?.nationality?.value,
			gender: formData?.gender?.value?.toLocaleUpperCase(),
			ownsCar: formData?.ownes_car,
			phoneNumber: formData?.phone,
			academicDegree: formData?.academic_degree?.value,
			specialization: formData?.specialization,
			university: formData?.university,
			educationType: formData?.education_type?.value?.toLocaleUpperCase(),
			yearsOfExperience: Number(formData?.years_of_experience),
			isActive: formData?.isActive,
			academicYearId: formData?.academicYear?.value,
			grades: formData?.grades?.map((res) => res?.value),
			zoneToZoneGroupIds: formData?.zones?.map((res) => res?.value),
			availability: formData?.days,
			teachingCertifications: formData?.teaching_certification?.map((res) => {
				return {
					file: res,
				};
			}),
			subjectGrades: formData?.subjectGrades
				?.filter((res) => !!res?.grade?.value && !!res?.subject?.value)
				?.map((res) => {
					return {
						subjectId: res?.subject?.value,
						grade: res?.grade?.value,
						price: Number(res?.price),
					};
				}),
		};
		if (!!formData?.wamd_number) data["wamdNumber"] = formData?.wamd_number;

		if (!!id) {
			setLoading(true);
			teacherManagementService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response.data?.message);
					navigate("/teachers");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			teacherManagementService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response.data?.message);
					navigate("/teachers");
				}
				setLoading(false);
			});
		}
	};
	const onNextSubmit = (e) => {
		e.preventDefault();
		if (
			!formData?.grades ||
			!formData?.zones ||
			!formData?.nationality ||
			!formData?.teaching_certification?.length ||
			!formData?.subjectGrades?.filter(
				(res) => !!res?.grade?.value && !!res?.subject?.value
			)?.length
		) {
			toast.error("Complete All Phases");
			return;
		}
		setType("availability_time");
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUploadMulti(filesData)
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							teaching_certification: res?.data?.data?.uploadedFiles?.map(
								(img) => img?.url
							),
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "16px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/teachers")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{!!id ? Translate[lang]?.edit : Translate[lang]?.add}{" "}
					{Translate[lang]?.teacher}
				</label>

				{type === "teacher_info" && (
					<Form.Check
						type="switch"
						id="custom-switch"
						checked={formData?.isActive}
						onChange={(e) =>
							setFormData({ ...formData, isActive: e.target.checked })
						}
					/>
				)}
			</div>
			<Row className="add-organization">
				<div className="tabs-div">
					<p
						// onClick={() => setType("teacher_info")}
						className={type === "teacher_info" ? "activeP" : ""}>
						{Translate[lang].teacher_info}
					</p>
					<p
						// onClick={() => setType("availability_time")}
						className={type === "availability_time" ? "activeP" : ""}>
						{Translate[lang].availability_time}
					</p>
				</div>
				<Col md={10}>
					{type === "teacher_info" && (
						<AvForm onValidSubmit={onNextSubmit}>
							<div className="row">
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.english_name}
										name="name"
										type="text"
										value={formData?.name}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.english}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.arabic_name}
										name="nameAr"
										type="text"
										value={formData?.nameAr}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.arabic}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.civil_id}
										name="civil_id"
										type="number"
										value={formData?.civil_id}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={`${Translate[lang]?.civil_id}`}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<label className="text-label">
										{Translate[lang]?.nationality}
									</label>
									<Select
										value={formData?.nationality}
										name="nationality"
										placeholder={Translate[lang]?.select}
										options={countriesOptions}
										onChange={(e) =>
											setFormData({ ...formData, nationality: e })
										}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<label className="mb-2">{Translate[lang]?.gender}</label>
									<Select
										name="gender"
										className="h-44"
										options={genderOptions}
										value={formData?.gender}
										onChange={(e) => setFormData({ ...formData, gender: e })}
									/>
								</div>
								<div className="col-12 mb-3 mt-4">
									<div className="d-flex gap-16">
										<label className="mb-0 d-flex gap-8 cursor-pointer">
											<input
												type="radio"
												name="owns_car"
												checked={formData?.ownes_car}
												onChange={(e) =>
													setFormData({ ...formData, ownes_car: true })
												}
											/>
											{Translate[lang].owns_a_car}
										</label>
										<label className="mb-0 d-flex gap-8 cursor-pointer">
											<input
												type="radio"
												name="owns_car"
												checked={!formData?.ownes_car}
												onChange={(e) =>
													setFormData({ ...formData, ownes_car: false })
												}
											/>
											{Translate[lang].doesnot_own_a_car}
										</label>
									</div>
								</div>
								<div className="col-lg-6 col-sm-6 mb-3">
									<label>{Translate[lang]?.phone}</label>
									<PhoneInput
										country={"kw"}
										enableSearch={true}
										value={formData?.phone}
										onChange={(e) => setFormData({ ...formData, phone: e })}
									/>
								</div>
								<div className="col-lg-6 col-sm-6  mb-3">
									<label className="mb-2">
										{Translate[lang]?.zones} ({Translate[lang]?.multi_select})
									</label>
									<Select
										name="zones"
										className="h-44"
										isMulti
										options={zonesOptions}
										value={formData?.zones}
										onChange={(e) => setFormData({ ...formData, zones: e })}
									/>
								</div>
								<div className="col-lg-6 col-sm-6 mb-3">
									<label className="mb-2">
										{Translate[lang]?.grade} ({Translate[lang]?.multi_select})
									</label>
									<Select
										name="grades"
										className="h-44"
										isMulti
										options={gradesOptions}
										value={formData?.grades}
										onChange={(e) => setFormData({ ...formData, grades: e })}
									/>
								</div>
								<div className="col-lg-6 col-sm-6 mb-3">
									<label className="mb-2">
										{Translate[lang]?.subjects} ({Translate[lang]?.multi_select}
										)
									</label>
									<Select
										name="subjects"
										className="h-44"
										isMulti
										options={subjectsOptions}
										value={formData?.subjects}
										onChange={(e) => {
											setFormData({ ...formData, subjects: e });
											setCustomSubjects(e);
										}}
									/>
								</div>
								{formData?.subjectGrades?.map((subGrade, index) => {
									return (
										<React.Fragment key={index}>
											<div className="col-lg-3 col-sm-3 mb-3">
												<label className="mb-2">
													{Translate[lang]?.subject}
												</label>
												<Select
													name="subjects"
													className="h-44"
													options={customSubjects}
													value={subGrade?.subject}
													onChange={(e) => {
														let update = formData?.subjectGrades?.map(
															(sub, ind) => {
																if (ind === index) {
																	return {
																		...sub,
																		subject: e,
																	};
																} else {
																	return sub;
																}
															}
														);
														setFormData({ ...formData, subjectGrades: update });
													}}
												/>
											</div>

											<div className="col-lg-3 col-sm-3 mb-3">
												<label className="mb-2">{Translate[lang]?.grade}</label>
												<Select
													name="grade"
													className="h-44"
													options={gradesOptions}
													value={subGrade?.grade}
													onChange={(e) => {
														let update = formData?.subjectGrades?.map(
															(sub, ind) => {
																if (ind === index) {
																	return {
																		...sub,
																		grade: e,
																	};
																} else {
																	return sub;
																}
															}
														);
														setFormData({ ...formData, subjectGrades: update });
													}}
												/>
											</div>
											<div className="col-lg-5 col-sm-5">
												<AvField
													label={`${Translate[lang]?.price} / ${Translate[lang]?.class}`}
													name={`price${index}`}
													type="number"
													value={subGrade?.price}
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													placeholder={`${Translate[lang]?.price}`}
													onChange={(e) => {
														let update = formData?.subjectGrades?.map(
															(sub, ind) => {
																if (ind === index) {
																	return {
																		...sub,
																		price: e.target?.value,
																	};
																} else {
																	return sub;
																}
															}
														);
														setFormData({ ...formData, subjectGrades: update });
													}}
												/>
											</div>
											<div className="col-1 d-flex align-items-center">
												{index === 0 && (
													<i
														onClick={() =>
															setFormData({
																...formData,
																subjectGrades: [
																	...formData?.subjectGrades,
																	{
																		subject: "",
																		grade: "",
																		price: "",
																	},
																],
															})
														}
														className="la la-plus-circle text-primary fs-32 cursor-pointer"></i>
												)}
												{index > 0 && (
													<i
														onClick={() =>
															setFormData({
																...formData,
																subjectGrades: formData?.subjectGrades.filter(
																	(_, ind) => index !== ind
																),
															})
														}
														className="la la-times-circle text-danger fs-32 cursor-pointer"></i>
												)}
											</div>
										</React.Fragment>
									);
								})}
								<div className="col-lg-6 col-sm-6 mb-3">
									<label className="mb-2">
										{Translate[lang]?.academic_degree}
									</label>
									<Select
										name="academic_degree"
										className="h-44"
										options={degreeOptions}
										value={formData?.academic_degree}
										onChange={(e) =>
											setFormData({ ...formData, academic_degree: e })
										}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.specialization}
										name="specialization"
										type="text"
										value={formData?.specialization}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={`${Translate[lang]?.specialization}`}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.university}
										name="university"
										type="text"
										value={formData?.university}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={`${Translate[lang]?.university}`}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.years_of_experience}
										name="years_of_experience"
										type="number"
										value={formData?.years_of_experience}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={`${Translate[lang]?.years_of_experience}`}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-6 col-sm-6  mb-3">
									<label className="mb-2">
										{Translate[lang]?.academic_year}
									</label>
									<Select
										name="academicYear"
										className="h-44"
										options={academicYearOptions}
										value={formData?.academicYear}
										onChange={(e) => {
											setFormData({
												...formData,
												academicYear: e,
												semesters: e?.semesters?.map((res) => {
													return {
														...res,

														endDate: res?.endDate?.split("T")[0],
														examNightsEndDate:
															res?.examNightsEndDate?.split("T")[0],
														examNightsStartDate:
															res?.examNightsStartDate?.split("T")[0],
														finalRevisionEndDate:
															res?.finalRevisionEndDate?.split("T")[0],
														finalRevisionStartDate:
															res?.finalRevisionStartDate?.split("T")[0],
														startDate: res?.startDate?.split("T")[0],
													};
												}),
												education_type: {
													label:
														Translate[lang][
															e?.educationType?.toLocaleLowerCase()
														],
													value: e?.educationType?.toLocaleLowerCase(),
												},
												number_of_semester: e.numberOfSemesters,
											});
										}}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.wamd_number}
										name="wamd_number"
										type="number"
										value={formData?.wamd_number}
										placeholder={Translate[lang]?.wamd_number}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
								<div className="col-lg-12 col-sm-12 mb-3">
									<div className="form-group w-100 add-gallery">
										<label className="m-0">
											{Translate[lang]?.teaching_certification}
										</label>
										<div className="image-placeholder">
											<div className="avatar-edit">
												<input
													type="file"
													multiple
													onChange={(e) => fileHandler(e)}
													id={`imageUpload1`}
												/>
												<label htmlFor={`imageUpload1`} name=""></label>
											</div>
											<div className="avatar-preview2 m-auto">
												<div id={`imagePreview`} className="images">
													<>
														<i className="la la-plus-circle" />
														<span
															style={{ fontSize: "18px", fontWeight: "600" }}>
															{Translate[lang]?.add} {Translate[lang]?.image}
														</span>
														<span style={{ fontSize: "12px" }}>
															{Translate[lang]?.max_number} 3
														</span>
													</>
													{loading && <Loader />}
												</div>
											</div>
										</div>
										{!!formData?.teaching_certification?.length && (
											<div className="row">
												{formData?.teaching_certification
													?.slice(0, 3)
													?.map((res, index) => {
														return (
															<div className="col-md-3 mb-3">
																<div className="banner_image w-auto">
																	<img
																		src={res}
																		alt="bannerImage"
																		className="w-100"
																	/>
																	<a
																		href={res}
																		target="_blank"
																		rel="noreferrer">
																		<i className="la la-eye" />
																	</a>
																	<i
																		className="la la-trash"
																		onClick={() => {
																			let update = formData?.images?.filter(
																				(_, ind) => ind !== index
																			);
																			setFormData({
																				...formData,
																				images: update,
																			});
																		}}
																	/>
																</div>
															</div>
														);
													})}
											</div>
										)}
									</div>
								</div>
								<div className="col-lg-6 col-sm-6 mt-4 mb-5">
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.next}
									</Button>
								</div>
							</div>
						</AvForm>
					)}
					{type === "availability_time" && (
						<AvForm onValidSubmit={onSubmit}>
							<div className="row">
								<div className="col-lg-6 col-sm-6">
									<label className="text-label">
										{Translate[lang]?.education_type}
									</label>
									<Select
										isDisabled
										value={formData?.education_type}
										placeholder={Translate[lang]?.select}
										className="h-44"
										options={educationTypeOptions}
										// onChange={(e) =>
										// 	setFormData({ ...formData, education_type: e })
										// }
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.number_of_semester}
										name="number_of_semester"
										type="number"
										value={formData?.number_of_semester}
										disabled
										// validate={{
										// 	required: {
										// 		value: true,
										// 		errorMessage: Translate[lang].field_required,
										// 	},
										// }}
										placeholder={`${Translate[lang]?.number_of_semester}`}
										// onChange={(e) => {
										// 	if (e.target.value < 8) {
										// 		setFormData({
										// 			...formData,
										// 			number_of_semester: e.target.value,
										// 			availability: Array(Number(e.target.value))
										// 				.fill("")
										// 				?.map((_) => {
										// 					return {};
										// 				}),
										// 		});
										// 	}
										// }}
									/>
								</div>

								<div className="col-12 mb-3 mt-4">
									<p className="text-black fs-18 fw-500">
										{Translate[lang].availability_time_in_each_semester}
									</p>
									<div className="tabs-div">
										{formData?.semesters?.map((res, index) => {
											return (
												<p
													key={res}
													onClick={() => setSelectSemester(index)}
													className={
														index === selectSemester
															? "activeP text-capitalize"
															: "text-capitalize"
													}>
													{lang === "en" ? res?.nameEn : res?.nameAr}
												</p>
											);
										})}
									</div>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.start_date}
										name={`start_date${selectSemester}`}
										type="date"
										value={formData?.semesters[selectSemester]?.startDate}
										disabled
										validate={{
											required: {
												value: false,
											},
										}}
									/>
								</div>
								<div className="col-lg-6 col-sm-6">
									<AvField
										label={Translate[lang]?.end_date}
										name={`start_date${selectSemester}`}
										type="date"
										value={formData?.semesters[selectSemester]?.endDate}
										disabled
										validate={{
											required: {
												value: false,
											},
										}}
									/>
								</div>
								<div className="col-12 mb-3 mt-4">
									<p className="text-black fs-18 fw-500">
										{Translate[lang].select} {Translate[lang].days}
									</p>
									<div className="custom-days-tabs">
										{daysOptions?.map((day) => {
											return (
												<p
													key={day?.value}
													className={`${
														!!formData?.days?.find(
															(res) =>
																res?.semesterId ===
																	formData?.semesters[selectSemester]?.id &&
																res?.day ===
																	day?.label?.slice(0, 3)?.toLocaleUpperCase()
														)?.semesterId
															? "active"
															: ""
													}`}
													onClick={() => {
														if (
															!!formData?.days?.find(
																(res) =>
																	res?.semesterId ===
																		formData?.semesters[selectSemester]?.id &&
																	res?.day ===
																		day?.label?.slice(0, 3)?.toLocaleUpperCase()
															)?.semesterId
														) {
															let update = formData?.days?.filter(
																(item) =>
																	!(
																		item.semesterId ===
																			formData?.semesters[selectSemester]?.id &&
																		item.day ===
																			day?.label
																				?.slice(0, 3)
																				?.toLocaleUpperCase()
																	)
															);
															setFormData({ ...formData, days: update });
														} else {
															setFormData({
																...formData,
																days: [
																	...formData.days,
																	{
																		semesterId:
																			formData?.semesters[selectSemester].id,
																		day: day?.label
																			?.slice(0, 3)
																			?.toLocaleUpperCase(),
																		startTime: "",
																		endTime: "",
																	},
																],
															});
														}
													}}>
													{day?.label?.slice(0, 3)}
												</p>
											);
										})}
									</div>
								</div>
								<div className="col-6 mb-3 mt-4">
									<p className="text-black fs-18 fw-500">
										{Translate[lang].select} {Translate[lang].times}
									</p>

									{formData?.days
										?.filter(
											(res) =>
												res?.semesterId ===
												formData?.semesters[selectSemester]?.id
										)
										?.map((day, index) => {
											return (
												<div
													key={index}
													className="mb-3 d-flex align-items-center justify-content-between">
													<p className="mb-0 custom-times">{day?.day}</p>
													<i className="la la-arrow-right fs-20"></i>
													<input
														type="time"
														name={`start_time_${selectSemester}_${index}`}
														required
														value={day?.startTime}
														style={{
															padding: "4px 8px",
															border: "1px solid gray",
															borderRadius: "10px",
														}}
														onChange={(e) => {
															let update = formData?.days?.map((res, ind) => {
																if (
																	res?.semesterId === day?.semesterId &&
																	res?.day === day?.day
																) {
																	return {
																		...res,
																		startTime: e.target.value,
																	};
																} else {
																	return res;
																}
															});
															setFormData({ ...formData, days: update });
														}}
													/>
													<span className="mx-2 fs-20">-</span>
													<input
														type="time"
														name={`end_time_${selectSemester}_${index}`}
														required
														style={{
															padding: "4px 8px",
															border: "1px solid gray",
															borderRadius: "10px",
														}}
														value={day?.endTime}
														onChange={(e) => {
															let update = formData?.days?.map((res, ind) => {
																if (
																	res?.semesterId === day?.semesterId &&
																	res?.day === day?.day
																) {
																	return {
																		...res,
																		endTime: e.target.value,
																	};
																} else {
																	return res;
																}
															});
															setFormData({ ...formData, days: update });
														}}
													/>
												</div>
											);
										})}
								</div>

								<div className="col-6"></div>
								<div className="col-6 mb-5 mt-4">
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => setType("teacher_info")}
										disabled={loading}>
										{Translate[lang]?.back}
									</Button>
								</div>
								<div className="col-6 mb-5 mt-4">
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								</div>
							</div>
						</AvForm>
					)}
				</Col>
			</Row>
		</>
	);
};

export default AddTeachers;
