import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import GroupSettingsService from "../../../../services/GroupSettingsService";
import HallService from "../../../../services/HallService";
import { Translate } from "../../../Enums/Tranlate";

const GroupSettings = () => {
	let initial = {
		hall: "",
		group_capacity: "",
		first_sub: "",
		second_sub: "",
		supervisor_span: "",
	};
	const [formData, setFormData] = useState({});
	const [type, setType] = useState("education");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const [id, setId] = useState(null);
	const [hallsOptions, setHallsOptions] = useState([]);
	const groupSettingsService = new GroupSettingsService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let promiseFun = [
			type === "education"
				? new HallService().getEducationHalls()
				: new HallService().getEntertainmentHalls(),
			groupSettingsService.getList(type),
		];
		Promise.all(promiseFun).then((res) => {
			let hallOpt = [];
			if (res[0]?.status) {
				let info = res[0]?.data?.data?.map((sub) => {
					return {
						...sub,
						label: sub?.name,
						value: sub?.id,
					};
				});
				setHallsOptions(info);
				hallOpt = info;
			}
			if (res[1]?.status) {
				let response = res[1]?.data?.data;
				if (!!response?.length) {
					setId(response[0]?.id);
					let data = {
						hall: hallOpt?.find(
							(optio) => optio?.value === response[0].hall?.id
						),
						group_capacity: response[0].groupCapacity,
						first_sub: response[0].firstSubGroup || "",
						second_sub: response[0].secondSubGroup || "",
						supervisor_span: response[0].supervisorSpan,
					};
					setFormData(data);
				} else {
					setId(null);
				}
			}
		});
	}, [type]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();
		if (!formData?.hall?.value) {
			return;
		}
		let data = {
			hallId: formData?.hall?.value,
			groupCapacity: Number(formData?.hall?.capacity),
			// groupCapacity: Number(formData?.group_capacity),
			firstSubGroup: Number(formData?.first_sub),
			secondSubGroup: Number(formData?.second_sub),
		};
		if (type === "entertainment")
			data["supervisorSpan"] = Number(formData?.supervisor_span);

		if (!!id) {
			setLoading(true);
			groupSettingsService.update(type, id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(Translate[lang].admin_updated_successfully);
					setShouldUpdate((prev) => prev);
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			groupSettingsService.create(type, data).then((response) => {
				if (response?.status === 201) {
					toast.success(
						`${Translate[lang].added} ${Translate[lang].admin} ${Translate[lang].successfully}`
					);
					setShouldUpdate((prev) => prev);
				}
				setLoading(false);
			});
		}
	};

	return (
		<>
			<label className="mb-3" style={{ fontSize: "20px", fontWeight: "600" }}>
				{Translate[lang]?.group_settings}
			</label>
			<div className="tabs-div">
				<p
					onClick={() => {
						setType("education");
						setFormData(initial);
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "education" ? "activeP" : ""}>
					{Translate[lang].education}
				</p>
				<p
					onClick={() => {
						setType("entertainment");
						setFormData(initial);
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "entertainment" ? "activeP" : ""}>
					{Translate[lang].entertainment}
				</p>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">{Translate[lang]?.halls}</label>
								<Select
									value={formData?.hall}
									name="hall"
									placeholder={Translate[lang]?.select}
									options={hallsOptions}
									onChange={(e) => setFormData({ ...formData, hall: e })}
								/>
							</div>
							<div className="col-6"></div>

							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.group_capacity} ${
										type === "education"
											? `(30 ${Translate[lang]?.student})`
											: ""
									}`}
									placeholder={Translate[lang]?.group_capacity}
									name="group_capacity"
									type="number"
									value={formData?.hall?.capacity || ""}
									disabled
								/>
							</div>
							<div className="col-6"></div>
							<div className="col-lg-6 col-sm-6">
								<AvField
									label={`${Translate[lang]?.first_sub_group}`}
									name="first_sub"
									type="number"
									value={formData?.first_sub}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.first_sub_group}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.second_sub_group}`}
									name="second_sub"
									type="number"
									value={formData?.second_sub}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.second_sub_group}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							{type === "entertainment" && (
								<div className="col-lg-6 col-sm-6 ">
									<AvField
										label={`${Translate[lang]?.supervisor_span}`}
										name="supervisor_span"
										type="number"
										value={formData?.supervisor_span}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={`${Translate[lang]?.supervisor_span}`}
										onChange={(e) => inputHandler(e)}
									/>
								</div>
							)}
							{type === "entertainment" && <div className="col-6"></div>}

							{/* Buttons */}
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								<Button
									variant="primary"
									type="submit"
									className="w-100 fs-14"
									disabled={loading}>
									{Translate[lang]?.save}
								</Button>
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default GroupSettings;
