import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import "../style.scss";
import BaseService from "../../../../../services/BaseService";
import Loader from "../../../../common/Loader";

const AddCategoriesModal = ({
	addModal,
	setAddModal,
	item,
	assign,
	setShouldUpdate,
	type,
	Service,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		name_ar: "",
		image: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(true);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				name: item?.name,
				name_ar: item?.translations?.find((res) => res?.languageId === "ar")
					?.name,
				isActive: item?.isActive,
				image: item?.image,
				id: item?.id,
			});
		}
	}, []);

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							image: res?.data?.data?.uploadedFiles[0]?.url,
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};
	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			image: formData?.image,
			isActive: formData?.isActive,
			translations: [
				{
					languageId: "ar",
					name: formData?.name_ar,
				},
			],
		};

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.category}
								</p>

								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>

						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.category_name} (${Translate[lang]?.english})`}
								type="text"
								placeholder={Translate[lang]?.category_name}
								bsSize="lg"
								name="name"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.name}
								onChange={(e) =>
									setFormData({
										...formData,
										name: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.category_name} (${Translate[lang]?.arabic})`}
								type="text"
								placeholder={Translate[lang]?.category_name}
								bsSize="lg"
								name="name_ar"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.name_ar}
								onChange={(e) =>
									setFormData({
										...formData,
										name_ar: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={12} className="add-gallery">
							<label className="m-0">{Translate[lang]?.image}</label>
							<div className="image-placeholder">
								<div className="avatar-edit">
									<input
										type="file"
										multiple
										onChange={(e) => fileHandler(e)}
										id={`imageUpload1`}
									/>
									<label htmlFor={`imageUpload1`} name=""></label>
								</div>
								<div className="avatar-preview2 m-auto">
									<div id={`imagePreview`} className="images">
										{
											<>
												<i className="la la-plus-circle" />
												<span style={{ fontSize: "18px", fontWeight: "600" }}>
													{Translate[lang]?.add} {Translate[lang]?.image}
												</span>
												<span style={{ fontSize: "16px" }}>420*330</span>
											</>
										}
										{loading && <Loader />}
									</div>
								</div>
							</div>
							{!!formData?.image && (
								<div className="row">
									<div className="col-md-6 -col-12 mb-3">
										<div className="banner_image w-auto">
											<img
												src={formData?.image}
												alt="subjectImage"
												className="w-100"
											/>
											<a
												href={formData?.image}
												target="_blank"
												rel="noreferrer">
												<i className="la la-eye" />
											</a>
											<i
												className="la la-trash"
												onClick={() => {
													setFormData({
														...formData,
														image: "",
													});
												}}
											/>
										</div>
									</div>
								</div>
							)}
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.category}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddCategoriesModal;
