import { Col, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { Translate } from "../../../../Enums/Tranlate";
import AddOnGroupService from "../../../../../services/AddOnGroupService";
import RemoveAssignedModal from "../AssignModal/RemoveAssignedModal";

const ViewOnGroup = () => {
	const [item, setItem] = useState({});
	const [assign, setAssign] = useState({});
	const [loading, setLoading] = useState(false);
	const [deleteModal, setDeleteModal] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const location = useLocation();
	const addOnGroupService = new AddOnGroupService();

	useEffect(() => {
		setLoading(true);
		addOnGroupService
			?.find(location?.state?.groupProductId)
			.then((res) => {
				if (res?.status === 200) {
					setItem(res?.data?.data);
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	}, [shouldUpdate]);

	if (loading) {
		return;
	}
	return (
		<div className="department view-details">
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div className="d-flex align-items-center" style={{ gap: "8px" }}>
						<Link to="/add-on-group">
							<i
								className={
									lang === "en" ? "la la-arrow-left" : "la la-arrow-right"
								}
								style={{
									color: "#000",
									fontSize: "24px",
								}}></i>
						</Link>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].on_group}
						</span>
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<div
						style={{
							border: "1px solid #CDCED7",
							borderRadius: "10px",
							padding: "32px 24px",
						}}>
						<div className="d-flex mb-3" style={{ gap: "16px" }}>
							<label className="mb-0 fs-18">
								{Translate[lang].product_name}:
							</label>
							<span className="text-black fs-18" style={{ fontWeight: "500" }}>
								{item?.name}
							</span>
						</div>
						<div>
							<label className="d-block mb-4 fs-18">
								{Translate[lang].assigned_products}:
							</label>
							{item?.assignedProducts?.map((assigned, index) => {
								return (
									<div
										key={assigned?.assignmentId}
										className="d-flex justify-content-between"
										style={{
											borderBottom:
												item?.assignedProducts?.length - 1 > index
													? "1px solid #dedede"
													: "",
											marginBottom:
												item?.assignedProducts?.length - 1 > index
													? "1rem"
													: "",
											paddingBottom:
												item?.assignedProducts?.length - 1 > index
													? "1rem"
													: "",
										}}>
										<label className="mb-0 fs-18">{assigned?.name}</label>
										<i
											className="la la-trash cursor-pointer text-danger fs-18"
											onClick={() => {
												setDeleteModal(true);
												setAssign(assigned);
											}}
										/>
									</div>
								);
							})}
						</div>
					</div>
				</Col>
			</Row>
			{deleteModal && (
				<RemoveAssignedModal
					open={deleteModal}
					titleMsg={assign?.name}
					deletedItem={assign}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</div>
	);
};

export default ViewOnGroup;
