import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import VariantsService from "../../../../../services/VariantsService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import { TagsInput } from "react-tag-input-component";
import CategoriesService from "../../../../../services/CategoriesService";
import "../style.scss";

const AddVariant = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		category: "",
		variants: [],
		isActive: true,
	});
	const [variantsData, setVariantsData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [categoryOptions, setCategoryOptions] = useState([]);
	const navigate = useNavigate();
	const categoriesService = new CategoriesService();
	const variantsService = new VariantsService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;

		if (location?.pathname.includes("edit")) {
			setId(item?.id);
			setFormData({
				category: {
					label: item?.category?.name,
					value: item?.category?.id,
				},
				variants: item?.variantTypes,
				isActive: item?.isActive,
			});
			setVariantsData(
				Object.entries(item?.attributesByType).map(([key, items]) =>
					items.map((item) => ({ ...item, type: key }))
				)
			);
		} else {
			setId(null);
		}
	}, [location]);

	useEffect(() => {
		categoriesService.getList().then((res) => {
			if (res?.status) {
				let info = res?.data?.data
					?.filter((fil) => fil?.isActive)
					?.map((cat) => {
						return {
							label:
								lang === "en"
									? cat?.name
									: cat?.translations?.find(
											(trans) => trans?.languageId === "ar"
									  )?.name,
							value: cat?.id,
						};
					});
				setCategoryOptions(info);
			}
		});
	}, []);

	const onSubmit = (e) => {
		e.preventDefault();

		let data = {
			categoryId: formData?.category?.value,
			variantTypes: formData?.variants,
			isActive: formData?.isActive,
			attributes: variantsData?.flat()?.map((res) => {
				let info = {
					type: res?.type,
					name: res?.name,
					nameAr: res?.nameAr,
				};
				if (res?.type === "color" || res?.type === "colour")
					info["colorCode"] = res?.colorCode;
				return info;
			}),
		};

		if (!!id) {
			setLoading(true);
			variantsService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/variants");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			variantsService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/variants");
				}
				setLoading(false);
			});
		}
	};

	const handleVariants = (e) => {
		let update = e.map((res) => {
			if (
				variantsData.some((group) => group.some((item) => item.type === res))
			) {
				return variantsData.flatMap((group) =>
					group.filter((item) => item.type === res)
				);
			} else {
				let info = {
					type: res,
					name: "",
					nameAr: "",
				};
				if (res === "color" || res === "colour" || res === "اللون")
					info["colorCode"] = "#000";
				return ([res] = [info]);
			}
		});
		setFormData({ ...formData, variants: e });
		setVariantsData(update);
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{
						cursor: "pointer",
						transform: lang === "ar" ? "rotate(180deg)" : "",
					}}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/variants")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						strokeWidth="1.5"
						strokeMiterlimit="10"
						strokeLinecap="round"
						strokeLinejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{!!id ? Translate[lang]?.edit : Translate[lang]?.add}{" "}
					{Translate[lang]?.variant}
				</label>

				<Form.Check
					type="switch"
					id="custom-switch"
					checked={formData?.isActive}
					onChange={(e) =>
						setFormData({ ...formData, isActive: e.target.checked })
					}
				/>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6 mb-3">
								<label>{Translate[lang]?.category}</label>
								<Select
									value={formData?.category}
									options={categoryOptions}
									placeholder={`${Translate[lang]?.category}`}
									onChange={(e) => setFormData({ ...formData, category: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label>{Translate[lang]?.variants}</label>
								<TagsInput
									value={formData?.variants}
									onChange={(e) => {
										handleVariants(e);
									}}
									name="variants"
									placeHolder={`${Translate[lang]?.variants}`}
								/>
							</div>

							{!!variantsData?.length &&
								variantsData?.map((variant, index) => {
									if (
										variant[0]?.type === "color" ||
										variant[0]?.type === "colour"
									) {
										return variant?.map((res, ind) => {
											return (
												<React.Fragment key={ind}>
													{ind === 0 && (
														<div className="col-12 mb-1">
															<h4>{Translate[lang].color}</h4>
														</div>
													)}
													{ind > 0 && (
														<div
															className="col-12"
															style={{ textAlign: "end" }}>
															<i
																className="la la-times-circle fs-32 text-danger cursor-pointer"
																onClick={() => {
																	let update = variantsData?.map(
																		(upVar, indexVar) => {
																			if (indexVar === index) {
																				return [
																					...upVar.filter(
																						(_, filInd) => filInd !== ind
																					),
																				];
																			} else {
																				return upVar;
																			}
																		}
																	);
																	setVariantsData(update);
																}}></i>
														</div>
													)}
													<div className="col-lg-6 col-sm-6">
														<AvField
															label={`Color ${Translate[lang]?.name} (${
																Translate[lang]?.english
															}) ${ind + 1}`}
															name={`color_name_${ind} `}
															type="text"
															value={res?.name}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.name}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						name: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													<div className="col-lg-6 col-sm-6">
														<AvField
															label={`Color ${Translate[lang]?.name} (${
																Translate[lang]?.arabic
															}) ${ind + 1}`}
															name={`color_nameAr_${ind}`}
															type="text"
															value={res?.nameAr}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.name}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						nameAr: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													<div className="col-lg-6 col-sm-6">
														<AvField
															label={Translate[lang]?.color}
															name={`color_code_${ind}`}
															type="color"
															value={res?.colorCode}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.colorCode}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						colorCode: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													<div className="col-lg-6 col-sm-6">
														<AvField
															label={Translate[lang]?.color_code}
															name={`color_codeName_${ind}`}
															type="text"
															value={res?.colorCode}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.colorCode}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						colorCode: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													<div className="col-12">
														{variant?.length - 1 === ind && (
															<button
																className="btn btn-outline-primary mt-3 text-capitalize d-flex gap-8 align-items-center"
																type="button"
																onClick={() => {
																	let update = variantsData?.map(
																		(upVar, indexVar) => {
																			if (indexVar === index) {
																				return [
																					...upVar,
																					{
																						type: "color",
																						colorCode: "#000",
																						name: "",
																						nameAr: "",
																					},
																				];
																			} else {
																				return upVar;
																			}
																		}
																	);
																	setVariantsData(update);
																}}>
																<i className="la la-plus"></i>
																{Translate[lang].add} {Translate[lang].color}
															</button>
														)}
														<hr />
													</div>
												</React.Fragment>
											);
										});
									} else {
										return variant?.map((res, ind) => {
											return (
												<React.Fragment key={ind}>
													{ind === 0 && (
														<div className="col-12 mb-1">
															<h4>{res?.type}</h4>
														</div>
													)}
													<div className="col-lg-6 col-sm-6">
														<AvField
															label={`${res?.type} ${Translate[lang]?.name} (${
																Translate[lang]?.english
															}) ${ind + 1}`}
															name={`${res?.type}_name_${ind} `}
															type="text"
															value={res?.name}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.name}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						name: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													<div
														className={
															ind > 0
																? "col-lg-5 col-sm-5"
																: "col-lg-6 col-sm-6"
														}>
														<AvField
															label={`${res?.type} ${Translate[lang]?.name} (${
																Translate[lang]?.arabic
															}) ${ind + 1}`}
															name={`${res?.type}_nameAr_${ind}`}
															type="text"
															value={res?.nameAr}
															validate={{
																required: {
																	value: true,
																	errorMessage: Translate[lang].field_required,
																},
															}}
															placeholder={Translate[lang]?.name}
															onChange={(e) => {
																let update = variantsData?.map(
																	(upVar, indexVar) => {
																		if (indexVar === index) {
																			return upVar?.map((subVar, indSub) => {
																				if (indSub === ind) {
																					return {
																						...subVar,
																						nameAr: e.target.value,
																					};
																				} else {
																					return subVar;
																				}
																			});
																		} else {
																			return upVar;
																		}
																	}
																);
																setVariantsData(update);
															}}
														/>
													</div>
													{ind > 0 && (
														<div
															className="col-1 d-flex align-items-center"
															style={{ textAlign: "center" }}>
															<i
																className="la la-times-circle mt-2 fs-32 text-danger cursor-pointer"
																onClick={() => {
																	let update = variantsData?.map(
																		(upVar, indexVar) => {
																			if (indexVar === index) {
																				return [
																					...upVar.filter(
																						(_, filInd) => filInd !== ind
																					),
																				];
																			} else {
																				return upVar;
																			}
																		}
																	);
																	setVariantsData(update);
																}}></i>
														</div>
													)}
													{variant?.length - 1 === ind && (
														<div className="col-12">
															<button
																className="btn btn-outline-primary mt-3 text-capitalize d-flex gap-8 align-items-center"
																type="button"
																onClick={() => {
																	let update = variantsData?.map(
																		(upVar, indexVar) => {
																			if (indexVar === index) {
																				return [
																					...upVar,
																					{
																						type: res?.type,
																						name: "",
																						nameAr: "",
																					},
																				];
																			} else {
																				return upVar;
																			}
																		}
																	);
																	setVariantsData(update);
																}}>
																<i className="la la-plus"></i>
																{Translate[lang].add} {res?.type}
															</button>
															<hr />
														</div>
													)}
												</React.Fragment>
											);
										});
									}
								})}

							{!!id && (
								<div className="col-lg-6 col-sm-6 mt-2 mb-5">
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/variants")}>
										{Translate[lang]?.cancel}
									</Button>
								</div>
							)}
							<div className="col-lg-6 col-sm-6 mt-2 mb-5">
								<Button
									variant="primary"
									type="submit"
									className="w-100 fs-14"
									disabled={loading}>
									{Translate[lang]?.save}
								</Button>
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddVariant;
