import { useEffect, useState } from "react";
import { Badge, Dropdown, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import DeleteModal from "../../../common/DeleteModal";
import { useSelector } from "react-redux";
import { customRules } from "../../../Enums/Rules";
import { toast } from "react-toastify";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import Admin2Service from "../../../../services/Admin2Service";

const CardItem = ({ item, index, setShouldUpdate, setItem, setAddModal }) => {
	const [status, setStatus] = useState(null);
	const [deleteModal, setDeleteModal] = useState(false);
	const navigate = useNavigate();
	const adminService = new Admin2Service();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		setStatus(!item.isBlocked);
	}, [item]);

	const changeStatusToggle = (e) => {
		let data = {
			isBlocked: status,
		};
		adminService.toggleStatus(item.id, data).then((res) => {
			if (res?.status === 200) {
				toast.success(Translate[lang].updated_status_successfully);
				setStatus(!status);
			}
		});
	};

	return (
		<tr key={index}>
			<td>{item?.name}</td>
			<td>{item?.phoneNumber}</td>
			<td>{item?.email}</td>
			<td>{item?.role?.name}</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item.isActive ? "success" : "danger"} light`}>
					{item?.isActive ? Translate[lang].active : Translate[lang].deactive}
				</Badge>
			</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<img
						src={editIcon}
						onClick={() => {
							setItem(item);
							setAddModal(true);
						}}
						className="cursor-pointer"
						alt="edit"
						width={24}
					/>
					<img
						src={trashIcon}
						onClick={() => setDeleteModal(true)}
						className="cursor-pointer"
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={adminService}
					setShouldUpdate={setShouldUpdate}
					onCloseModal={setDeleteModal}
				/>
			)}
		</tr>
	);
};
export default CardItem;
