import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import ProductService from "../../../../../services/ProductService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import CategoriesService from "../../../../../services/CategoriesService";
import "../style.scss";
import BaseService from "../../../../../services/BaseService";
import Loader from "../../../../common/Loader";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw, ContentState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import VariantsService from "../../../../../services/VariantsService";
import { update } from "draft-js/lib/DefaultDraftBlockRenderMap";

const Physical = ({ isActive }) => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name_en: "",
		name_ar: "",
		category: "",
		price: "",
		discount: "",
		stock: "",
		sku: "",
		variants: [],
		images: [],
		description: EditorState.createEmpty(),
		description_ar: EditorState.createEmpty(),
		hasVariant: false,
		addOnGroup: false,
		availableForDelivery: false,
	});
	const [loadingImages, setLoadingImages] = useState(false);
	const [loading, setLoading] = useState(false);
	const [view, setView] = useState(false);
	const [openVariants, setOpenVariants] = useState(false);
	const [id, setId] = useState(null);
	const [categoryOptions, setCategoryOptions] = useState([]);
	const [variantsOptions, setVariantsOptions] = useState([]);
	const navigate = useNavigate();
	const categoriesService = new CategoriesService();
	const productService = new ProductService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;

		if (location?.pathname.includes("edit")) {
			setId(item?.id);
			// EditorState.createWithContent(
			// 	ContentState.createFromBlockArray(
			// 		htmlToDraft(response?.title?.replaceAll("&lt;", "<"))
			// 	)
			// )
			setFormData({
				category: {
					label: item?.category?.name,
					value: item?.category?.id,
				},
				variants: item?.variantTypes,
				isActive: item?.isActive,
			});
		} else {
			setId(null);
		}
	}, [location]);

	useEffect(() => {
		let promiseFun = [categoriesService.getList()];
		if (!!location?.state?.id)
			promiseFun?.push(productService.find(location?.state?.id));
		Promise.all(promiseFun).then((res) => {
			let categoriesOptions = [];
			if (res[0]?.status) {
				let info = res[0]?.data?.data
					?.filter((fil) => fil?.isActive)
					?.map((cat) => {
						return {
							label:
								lang === "en"
									? cat?.name
									: cat?.translations?.find(
											(trans) => trans?.languageId === "ar"
									  )?.name,
							value: cat?.id,
						};
					});
				setCategoryOptions(info);
				categoriesOptions = info;
			}
			if (!!location?.state?.id) {
				if (res[1]?.status) {
					let response = res[1]?.data?.data;
					setFormData({
						name_en: response?.name,
						name_ar: response?.translations?.find(
							(lan) => lan?.languageId === "ar"
						)?.name,
						category: categoriesOptions?.find(
							(opt) => opt?.value === response?.categoryId
						),
						discount: response?.discount,
						sku: response?.sku,
						images: response?.images?.map((img) => img?.image),
						description: EditorState.createWithContent(
							ContentState.createFromBlockArray(
								htmlToDraft(response?.description?.replaceAll("&lt;", "<"))
							)
						),
						description_ar: EditorState.createWithContent(
							ContentState.createFromBlockArray(
								htmlToDraft(
									response?.translations
										?.find((trans) => trans?.languageId === "ar")
										?.description?.replaceAll("&lt;", "<")
								)
							)
						),
						addOnGroup: response?.addOnGroup,
						availableForDelivery: response?.availableForDelivery,
						hasVariant: response?.hasVariants,
						variants: response?.variantCombinations?.map((vari) => {
							const updatedObject = {}; // Copy existing object

							vari?.attributes.forEach((item) => {
								const key = item.type.toLowerCase();
								updatedObject[key] = {
									...item,
									label: item.name,
									value: item.variantAttributeId,
								};
							});
							return {
								...updatedObject,
								stock: vari?.stock,
								amount: vari?.amount,
								cost: vari?.cost,
								price: vari?.price,
							};
						}),
					});
				}
			}
		});
	}, [location]);

	useEffect(() => {
		if (!!formData?.category?.value && formData?.hasVariant) {
			new VariantsService()
				.getVariantsByCategory(formData?.category?.value)
				.then((res) => {
					if (res?.status) {
						let info = res?.data?.data
							?.filter((fil) => fil?.isActive)
							?.map((varia) => varia?.attributesByType)
							?.reduce((acc, item) => {
								Object.keys(item).forEach((key) => {
									acc[key] = item[key]?.map((varOpt) => ({
										...varOpt,
										label: lang === "en" ? varOpt?.name : varOpt?.nameAr,
										value: varOpt?.id,
									}));
								});
								return acc;
							}, {});
						setVariantsOptions(info);
						if (!location?.state?.id || openVariants) {
							const keys = Object.keys(info);
							const emptyObject = {};
							keys.forEach((key) => {
								emptyObject[key.toLowerCase()?.replaceAll("-", "_")] = "";
							});
							setFormData({
								...formData,
								variants: [
									{
										...emptyObject,
										stock: "",
										amount: "",
										cost: "",
										price: "",
									},
								],
							});
						}
					}
				});
		}
	}, [formData?.category, formData?.hasVariant]);

	const onPhysicalSubmit = (e) => {
		e.preventDefault();

		let data = {
			type: "PHYSICAL",
			name: formData?.name_en,
			// price: Number(formData?.price),
			// stock: formData?.stock,
			discount: Number(formData?.discount),
			description: draftToHtml(
				convertToRaw(formData?.description.getCurrentContent())
			),
			addOnGroup: formData?.addOnGroup,
			isActive: isActive,
			categoryId: formData?.category?.value,
			sku: formData?.sku,
			hasVariants: formData?.hasVariant,
			availableForDelivery: formData?.availableForDelivery,
			images: formData?.images?.map((res) => {
				return {
					image: res,
				};
			}),
			translations: [
				{
					languageId: "ar", // Arabic language ID
					name: formData?.name_ar,
					description: draftToHtml(
						convertToRaw(formData?.description_ar.getCurrentContent())
					),
				},
			],
		};

		if (formData?.hasVariant)
			data["variantCombinations"] = formData?.variants?.map((res) => {
				return {
					attributes: Object.keys(variantsOptions)?.map((key) => {
						return { variantAttributeId: res[key?.toLocaleLowerCase()]?.value };
					}),
					stock: Number(res?.stock),
					amount: Number(res?.amount),
					cost: Number(res?.cost),
					price: Number(res?.price),
				};
			});
		if (!!id) {
			setLoading(true);
			productService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			productService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/products");
				}
				setLoading(false);
			});
		}
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);

		if (!filesData?.length) {
			return;
		}
		setLoadingImages(true);
		new BaseService()
			.postUploadMulti(filesData)
			.then((res) => {
				if (res?.status === 201) {
					let imgsUrls = res?.data?.data?.uploadedFiles?.map((img) => img?.url);
					setFormData({
						...formData,
						images: [...formData?.images, ...imgsUrls],
					});
				}
				setLoadingImages(false);
			})
			.catch(() => setLoadingImages(false));
	};

	return (
		<AvForm onValidSubmit={onPhysicalSubmit}>
			<div className="row">
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.product_name} (${Translate[lang]?.english})`}
						name={`name_en`}
						type="text"
						value={formData?.name_en}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.product_name}
						onChange={(e) => {
							setFormData({ ...formData, name_en: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.product_name} (${Translate[lang]?.arabic})`}
						name={`name_ar`}
						type="text"
						value={formData?.name_ar}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.product_name}
						onChange={(e) => {
							setFormData({ ...formData, name_ar: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-6 col-sm-6 mb-3">
					<label>{Translate[lang]?.category}</label>
					<Select
						value={formData?.category}
						options={categoryOptions}
						placeholder={`${Translate[lang]?.category}`}
						onChange={(e) => setFormData({ ...formData, category: e })}
					/>
				</div>
				{/* <div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.price}
						name={`price`}
						type="number"
						value={formData?.price}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.price}
						onChange={(e) => {
							setFormData({ ...formData, price: e.target.value });
						}}
					/>
				</div> */}
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={`${Translate[lang]?.discount} %`}
						name={`discount`}
						type="number"
						value={formData?.discount}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.percentage}
						onChange={(e) => {
							setFormData({ ...formData, discount: e.target.value });
						}}
					/>
				</div>
				{/* <div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.stock}
						name={`stock`}
						type="number"
						value={formData?.stock}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.stock}
						onChange={(e) => {
							setFormData({ ...formData, stock: e.target.value });
						}}
					/>
				</div> */}
				<div className="col-lg-6 col-sm-6">
					<AvField
						label={Translate[lang]?.sku}
						name={`sku`}
						type="text"
						value={formData?.sku}
						validate={{
							required: {
								value: true,
								errorMessage: Translate[lang].field_required,
							},
						}}
						placeholder={Translate[lang]?.sku}
						onChange={(e) => {
							setFormData({ ...formData, sku: e.target.value });
						}}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3 add-gallery">
					<div className="form-group w-100">
						<label className="m-0">{Translate[lang]?.images}</label>
						<div className="image-placeholder">
							{!view && (
								<div className="avatar-edit">
									{(!loadingImages || formData?.images?.length <= 5) && (
										<input
											accept="image/*"
											type="file"
											multiple
											onChange={(e) => fileHandler(e)}
											id={`imageUpload2`}
										/>
									)}
									<label htmlFor={`imageUpload2`} name=""></label>
								</div>
							)}
							<div className="avatar-preview2 m-auto">
								<div id={`imagePreview`} className="images">
									<>
										<i className="la la-plus-circle" />
										<span style={{ fontSize: "18px", fontWeight: "600" }}>
											{Translate[lang]?.add} {Translate[lang]?.images}
										</span>
										<span style={{ fontSize: "12px" }}>
											{Translate[lang]?.max_number} 5 {Translate[lang]?.images}
										</span>
									</>
									{loadingImages && <Loader />}
								</div>
							</div>
						</div>
						{!!formData?.images?.length && (
							<div className="row">
								{formData?.images?.slice(0, 10)?.map((res, index) => {
									return (
										<div className="col-md-3 mb-3">
											<div className="banner_image w-auto">
												<img src={res} alt="bannerImage" className="w-100" />
												<a href={res} target="_blank" rel="noreferrer">
													<i className="la la-eye" />
												</a>
												{!view && (
													<i
														className="la la-trash"
														onClick={() => {
															let update = formData?.images?.filter(
																(_, ind) => ind !== index
															);
															setFormData({
																...formData,
																images: update,
															});
														}}
													/>
												)}
											</div>
										</div>
									);
								})}
							</div>
						)}
					</div>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>{Translate[lang].english_description}</label>
					<Editor
						editorState={formData.description}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description: e })
						}
					/>
				</div>
				<div className="col-lg-12 col-sm-12 mb-3">
					<label>{Translate[lang].arabic_description}</label>
					<Editor
						editorState={formData.description_ar}
						toolbarClassName="toolbarClassName"
						wrapperClassName="wrapperClassName"
						editorClassName="editorClassName"
						onEditorStateChange={(e) =>
							setFormData({ ...formData, description_ar: e })
						}
					/>
				</div>
				<div className="col-12">
					<hr />
				</div>
				<div
					className={!!formData?.variants?.length ? "col-12 mb-3" : "col-12"}>
					<label className="d-flex gap-8 mb-0">
						<input
							type="checkbox"
							name="hasVariant"
							className="add-product-check"
							checked={formData?.hasVariant}
							onChange={(e) => {
								setOpenVariants(true);
								setFormData({ ...formData, hasVariant: e.target.checked });
							}}
						/>
						{Translate[lang].has_variant}
					</label>
				</div>
				{!!formData?.variants?.length &&
					formData?.hasVariant &&
					formData?.variants?.map((variant, index) => {
						return (
							<React.Fragment key={index}>
								{Object.keys(variantsOptions).map((key) => (
									<div className="col-lg-3 col-sm-3">
										<label style={{ textTransform: "capitalize" }}>{key}</label>
										<Select
											value={variant[key?.toLocaleLowerCase()]}
											name={`${key}_${index}`}
											options={variantsOptions[key]}
											onChange={(e) => {
												let update = formData?.variants?.map((res, ind) => {
													if (index === ind) {
														return {
															...res,
															[key?.toLocaleLowerCase()]: e,
														};
													} else {
														return res;
													}
												});
												setFormData({ ...formData, variants: update });
											}}
										/>
									</div>
								))}
								<div className="col-lg-3 col-sm-3">
									<AvField
										label={Translate[lang]?.price}
										name={`price${index}`}
										type="number"
										value={variant?.price}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.price}
										onChange={(e) => {
											let update = formData?.variants?.map((res, ind) => {
												if (index === ind) {
													return {
														...res,
														price: e.target?.value,
													};
												} else {
													return res;
												}
											});
											setFormData({ ...formData, variants: update });
										}}
									/>
								</div>
								<div className="col-lg-3 col-sm-3">
									<AvField
										label={Translate[lang]?.cost}
										name={`cost${index}`}
										type="number"
										value={variant?.cost}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.cost}
										onChange={(e) => {
											let update = formData?.variants?.map((res, ind) => {
												if (index === ind) {
													return {
														...res,
														cost: e.target?.value,
													};
												} else {
													return res;
												}
											});
											setFormData({ ...formData, variants: update });
										}}
									/>
								</div>
								<div className="col-lg-3 col-sm-3">
									<AvField
										label={Translate[lang]?.amount}
										name={`amount${index}`}
										type="number"
										value={variant?.amount}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.amount}
										onChange={(e) => {
											let update = formData?.variants?.map((res, ind) => {
												if (index === ind) {
													return {
														...res,
														amount: e.target?.value,
													};
												} else {
													return res;
												}
											});
											setFormData({ ...formData, variants: update });
										}}
									/>
								</div>
								<div className="col-lg-3 col-sm-3">
									<AvField
										label={Translate[lang]?.stock}
										name={`stock${index}`}
										type="number"
										value={variant?.stock}
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										placeholder={Translate[lang]?.stock}
										onChange={(e) => {
											let update = formData?.variants?.map((res, ind) => {
												if (index === ind) {
													return {
														...res,
														stock: e.target?.value,
													};
												} else {
													return res;
												}
											});
											setFormData({ ...formData, variants: update });
										}}
									/>
								</div>
								<div className="col-2 pt-2">
									{index + 1 === formData?.variants?.length && (
										<i
											className="la la-plus-circle mt-4 cursor-pointer"
											onClick={() =>
												setFormData({
													...formData,
													variants: [
														...formData?.variants,
														{ price: "", cost: "", amount: "", stock: "" },
													],
												})
											}
											style={{
												fontSize: "44px",
												color: "rgba(15, 80, 166, 1)",
											}}
										/>
									)}
									{index + 1 !== formData?.variants?.length && (
										<i
											className="la la-times-circle mt-4 cursor-pointer text-danger"
											onClick={() => {
												let update = formData?.variants?.filter(
													(_, ind) => ind !== index
												);
												setFormData({
													...formData,
													variants: update,
												});
											}}
											style={{
												fontSize: "44px",
											}}
										/>
									)}
								</div>
								{formData?.variants?.length - 1 !== index && (
									<div className="col-12">
										<hr />
									</div>
								)}
							</React.Fragment>
						);
					})}
				<div className="col-12">
					<hr />
				</div>
				<div className="col-12 mb-3">
					<label className="d-flex gap-8 mb-0">
						<input
							type="checkbox"
							name="add_on_group"
							className="add-product-check"
							checked={formData?.addOnGroup}
							onChange={(e) =>
								setFormData({ ...formData, addOnGroup: e.target.checked })
							}
						/>
						{Translate[lang].add_on_group}
					</label>
				</div>
				<div className="col-12">
					<label className="d-flex gap-8 mb-0">
						<input
							type="checkbox"
							name="available_for_delivery"
							className="add-product-check"
							checked={formData?.availableForDelivery}
							onChange={(e) =>
								setFormData({
									...formData,
									availableForDelivery: e.target.checked,
								})
							}
						/>
						{Translate[lang].available_for_delivery}
					</label>
				</div>

				{!!id && (
					<div className="col-lg-6 col-sm-6 mt-5 mb-5">
						<Button
							variant="secondary"
							type="button"
							className="w-100 fs-14"
							onClick={() => navigate("/products")}>
							{Translate[lang]?.cancel}
						</Button>
					</div>
				)}
				<div className="col-lg-6 col-sm-6 mt-5 mb-5">
					<Button
						variant="primary"
						type="submit"
						className="w-100 fs-14"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</div>
			</div>
		</AvForm>
	);
};

export default Physical;
