import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddSubjectsModal from "./AddSubjectsModal";
import "./style.scss";
import { useLocation, useNavigate } from "react-router-dom";
import SubjectsService from "../../../services/SubjectsService";
import ProgramsService from "../../../services/ProgramsService";

const Education = () => {
	const [data, setData] = useState([]);
	const [addModal, setAddModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(null);
	const [type, setType] = useState("");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const subjectsService = new SubjectsService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const navigate = useNavigate();
	const location = useLocation();

	useEffect(() => {
		if (!!location?.state) {
			setType(location?.state);
		} else {
			setType("subjects");
		}
	}, [location]);

	// useEffect(() => {
	// 	if (type === "subjects") {
	// 		setLoading(true);
	// 		subjectsService.getList().then((res) => {
	// 			if (res?.status) {
	// 				setData(res?.data?.data?.data);
	// 				if (!!res?.data?.data?.data?.length) {
	// 					setHasData(1);
	// 				} else {
	// 					setHasData(0);
	// 				}
	// 			}
	// 			setLoading(false);
	// 		});
	// 	}
	// }, [shouldUpdate, type]);

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => {
						setType("subjects");
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "subjects" ? "activeP" : ""}>
					{Translate[lang].subjects}
				</p>
				<p
					onClick={() => {
						setType("programs");
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "programs" ? "activeP" : ""}>
					{Translate[lang].programs}
				</p>
			</div>
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div className="add-department">
						{
							<Button
								variant="primary"
								className="me-2 h-75"
								onClick={() => {
									if (type === "subjects") {
										setItem({});
										setAddModal(true);
									} else {
										setType(null);
										navigate("/education/add-program");
									}
								}}>
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add}{" "}
								{type === "subjects"
									? Translate[lang]?.subject
									: Translate[lang]?.program}
							</Button>
						}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>
													{type === "subjects"
														? Translate[lang]?.subjects
														: Translate[lang]?.program_name}
												</strong>
											</th>
											<th>
												<strong>
													{type === "subjects"
														? Translate[lang]?.education_type
														: Translate[lang]?.type}
												</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.grade}</strong>
											</th>
											{type === "programs" && (
												<th>
													<strong>{Translate[lang]?.subjects}</strong>
												</th>
											)}
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setItem={setItem}
													type={type}
													setAddModal={setAddModal}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}

							{!!type && (
								<Pagination
									setData={setData}
									service={
										type === "programs"
											? new ProgramsService()
											: subjectsService
									}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
								/>
							)}
						</div>
					</Card>
				</Col>
			</Row>

			{addModal && (
				<AddSubjectsModal
					item={item}
					addModal={addModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddModal(false)}
					Service={subjectsService}
				/>
			)}
		</div>
	);
};

export default Education;
