import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import PhoneInput from "react-phone-input-2";
import BranchesService from "../../../../../services/BranchesService";
import OrganizationService from "../../../../../services/OrganizationService";
import WorkShiftService from "../../../../../services/WorkShiftService";
import EmployeesService from "../../../../../services/EmployeesService";

const AddBranchModal = ({
	addModal,
	setAddModal,
	item,
	assign,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		isActive: true,
		name: "",
		manager: "",
		branch_id: "",
		branch_no: "",
		organization: "",
		email: "",
		phone: "",
		address: "",
		work_shift: "",
		gates_number: "",
		gates: [],
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const branchesService = new BranchesService();
	const [organizationsOptions, setOrganizationsOptions] = useState([]);
	const [workShiftOptions, setWorkShiftOptions] = useState([]);
	const [managerOptions, setManagerOptions] = useState([]);

	useEffect(() => {
		let promises = assign
			? [new EmployeesService().getManager("MANAGER")]
			: [
					new EmployeesService().getManager("MANAGER"),
					new OrganizationService().getList(),
					new WorkShiftService().getList(),
			  ];
		Promise.all(promises).then((res) => {
			let managersOptions = [];
			let wshOptions = [];
			let orgOptions = [];
			if (res[0]?.status === 200) {
				let data = res[0]?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				managersOptions = data;
				setManagerOptions(data);
			}
			if (!assign && res[1]?.status === 200) {
				let data = res[1]?.data?.data?.map((info) => {
					return {
						label: info?.legalName,
						value: info?.id,
					};
				});
				orgOptions = data;
				setOrganizationsOptions(data);
			}
			if (!assign && res[2]?.status === 200) {
				let data = res[2]?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				wshOptions = data;
				setWorkShiftOptions(data);
			}

			if (Object.keys(item)?.length === 0) {
				setIsAdd(true);
			} else {
				setIsAdd(false);
				setFormData({
					id: item?.id,
					name: item?.name,
					isActive: item?.isActive,
					branch_no: item?.branchNo,
					email: item?.email,
					phone: item?.phoneNumber,
					address: item?.address,
					manager:
						managersOptions?.find((res) => res?.value === item?.manager?.id) ||
						"",
					organization: orgOptions?.find(
						(res) => res?.value === item?.organization?.id
					),
					work_shift: wshOptions?.find(
						(res) => res?.value === item?.workShift?.id
					),
					gates_number: { label: item?.noOfGates, value: item?.noOfGates },
					gates: item?.gates?.map((res) => {
						return {
							gate_name: res?.gateName,
							gate_number: res?.gateNumber,
						};
					}),
				});
			}
		});
	}, []);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			address: formData?.address,
			email: formData?.email,
			phoneNumber: formData?.phone,
			noOfGates: formData?.gates_number?.value,
			organizationId: formData?.organization?.value,
			workShiftId: formData?.work_shift?.value,
			gates: formData?.gates?.map((res) => {
				return {
					gateNumber: res?.gate_number,
					gateName: res?.gate_name,
				};
			}),
			isActive: formData?.isActive,
		};

		setLoading(true);
		if (isAdd) {
			branchesService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			if (!!formData?.manager?.value)
				data["managerId"] = formData?.manager?.value;
			branchesService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{assign
										? Translate[lang]?.assign_manager
										: isAdd
										? Translate[lang]?.add
										: Translate[lang]?.edit}{" "}
									{Translate[lang]?.branch}
								</p>
								{!assign && (
									<Form.Check
										type="switch"
										id="custom-switch"
										checked={formData?.isActive}
										onChange={(e) =>
											setFormData({ ...formData, isActive: e.target.checked })
										}
									/>
								)}
							</div>
						</Col>
						{assign && (
							<Col md={12} className="mb-3">
								<label className="text-label">
									{Translate[lang]?.branch_manager}
								</label>
								<Select
									value={formData?.manager}
									name="manager"
									placeholder={Translate[lang]?.select}
									options={managerOptions}
									onChange={(e) => setFormData({ ...formData, manager: e })}
								/>
							</Col>
						)}
						{!assign && (
							<>
								<Col md={12}>
									<AvField
										label={Translate[lang]?.branch_name}
										type="text"
										placeholder={Translate[lang]?.branch_name}
										bsSize="lg"
										name="name"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.name}
										onChange={(e) =>
											setFormData({ ...formData, name: e.target.value })
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label className="text-label">
										{Translate[lang]?.organization}
									</label>
									<Select
										value={formData?.organization}
										name="organization"
										placeholder={Translate[lang]?.select}
										options={organizationsOptions}
										onChange={(e) =>
											setFormData({ ...formData, organization: e })
										}
									/>
								</Col>
								{!isAdd && (
									<Col md={12} className="mb-3">
										<label className="text-label">
											{Translate[lang]?.branch_manager}
										</label>
										<Select
											value={formData?.manager}
											name="manager"
											placeholder={Translate[lang]?.select}
											options={managerOptions}
											onChange={(e) => setFormData({ ...formData, manager: e })}
										/>
									</Col>
								)}
								<Col md={12}>
									<AvField
										label={Translate[lang]?.email}
										type="email"
										placeholder={Translate[lang]?.email}
										bsSize="lg"
										name="email"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.email}
										onChange={(e) =>
											setFormData({ ...formData, email: e.target.value })
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label>{Translate[lang]?.phone}</label>
									<PhoneInput
										country={"kw"}
										enableSearch={true}
										value={formData?.phone}
										onChange={(e) => setFormData({ ...formData, phone: e })}
									/>
								</Col>
								<Col md={12}>
									<AvField
										label={Translate[lang]?.address}
										type="text"
										placeholder={Translate[lang]?.address}
										bsSize="lg"
										name="address"
										validate={{
											required: {
												value: true,
												errorMessage: Translate[lang].field_required,
											},
										}}
										value={formData.address}
										onChange={(e) =>
											setFormData({ ...formData, address: e.target.value })
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label className="text-label">
										{Translate[lang]?.work_shift}
									</label>
									<Select
										value={formData?.work_shift}
										name="work_shift"
										placeholder={Translate[lang]?.select}
										options={workShiftOptions}
										onChange={(e) =>
											setFormData({ ...formData, work_shift: e })
										}
									/>
								</Col>
								<Col md={12} className="mb-3">
									<label className="text-label">
										{Translate[lang]?.gates_no}
									</label>
									<Select
										value={formData?.gates_number}
										name="gates_number"
										placeholder={Translate[lang]?.select}
										options={[
											{ label: "1", value: 1 },
											{ label: "2", value: 2 },
											{ label: "3", value: 3 },
											{ label: "4", value: 4 },
											{ label: "5", value: 5 },
											{ label: "6", value: 6 },
											{ label: "7", value: 7 },
											{ label: "8", value: 8 },
											{ label: "9", value: 9 },
											{ label: "10", value: 10 },
										]}
										onChange={(e) => {
											setFormData({
												...formData,
												gates: Array(e.value).fill({
													gate_number: "",
													gate_name: "",
												}),
												gates_number: e,
											});
										}}
									/>
								</Col>
								{formData?.gates?.map((gate, index) => {
									return (
										<>
											<Col md={6} key={index}>
												<AvField
													label={Translate[lang]?.gate_no}
													type="number"
													placeholder={Translate[lang]?.gate_no}
													bsSize="lg"
													name="gate_no"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={gate?.gate_number}
													onChange={(e) => {
														let update = formData?.gates?.map((res, ind) => {
															if (index === ind) {
																return {
																	...res,
																	gate_number: e.target?.value,
																};
															} else {
																return res;
															}
														});
														setFormData({ ...formData, gates: update });
													}}
												/>
											</Col>
											<Col md={6} key={index}>
												<AvField
													label={Translate[lang]?.gate_name}
													type="text"
													placeholder={Translate[lang]?.gate_name}
													bsSize="lg"
													name="gate_name"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={gate?.gate_name}
													onChange={(e) => {
														let update = formData?.gates?.map((res, ind) => {
															if (index === ind) {
																return {
																	...res,
																	gate_name: e.target?.value,
																};
															} else {
																return res;
															}
														});
														setFormData({ ...formData, gates: update });
													}}
												/>
											</Col>
										</>
									);
								})}
							</>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{assign
							? Translate[lang]?.assign_manager
							: isAdd
							? Translate[lang]?.add
							: Translate[lang]?.edit}{" "}
						{assign ? "" : Translate[lang]?.branch}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddBranchModal;
