export default function adjustClassTimes(
	classSchedule,
	breakAfter,
	breakDuration
) {
	function timeToMinutes(time) {
		const [hours, minutes] = time.split(":").map(Number);
		return hours * 60 + minutes;
	}

	function minutesToTime(minutes) {
		const hours = Math.floor(minutes / 60);
		const mins = minutes % 60;
		return `${String(hours).padStart(2, "0")}:${String(mins).padStart(2, "0")}`;
	}

	let classCount = classSchedule.length;

	// Calculate total reduction per class before the break
	let reductionPerClass = Math.floor(breakDuration / classCount);

	let result = [];
	let previousEnd = null;

	for (let i = 0; i < classCount; i++) {
		let { from, to } = classSchedule[i];

		let startMinutes = timeToMinutes(from);
		let endMinutes = timeToMinutes(to);

		let adjustedDuration = endMinutes - startMinutes;

		// Reduce class time for those before the break
		adjustedDuration -= reductionPerClass;

		let newEndMinutes = startMinutes + adjustedDuration;

		// Ensure correct order if the previous class had an adjusted end time
		if (previousEnd !== null && previousEnd < startMinutes) {
			startMinutes = previousEnd;
			newEndMinutes = startMinutes + adjustedDuration;
		}
		if (previousEnd !== null && previousEnd > startMinutes) {
			startMinutes = previousEnd;
			newEndMinutes = startMinutes + adjustedDuration;
		}

		result.push({
			classNumber: i + 1,
			timeFrom: minutesToTime(startMinutes),
			timeTo: minutesToTime(newEndMinutes),
		});

		previousEnd = newEndMinutes;

		// Insert break after the specified class
		if (i + 1 === breakAfter) {
			previousEnd += breakDuration;
		}
	}

	return result;
}
