import BaseService from "./BaseService";
import { API_BASE_URL_ENV } from "../jsx/common/common";
import http from "./HttpService";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/teacher/zone-to-zone-group";
const apiAllZoneEndpoint = API_BASE_URL_ENV() + "/admin/teacher/zone";

export default class ZoneService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	getZonesList() {
		return http.get(apiAllZoneEndpoint);
	}
}
