import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../../Enums/Tranlate";
import Select from "react-select";
import SupervisorService from "../../../../../services/SupervisorService";
import SupervisorTypeService from "../../../../../services/SupervisorTypeService";
import EmployeesService from "../../../../../services/EmployeesService";

const AddSupervisorModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
	type,
}) => {
	const [formData, setFormData] = useState({
		supervisor: "",
		type: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [typeOptions, setTypeOptions] = useState([]);
	const [supervisorsOptions, setSupervisorsOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const supervisorService = new SupervisorService();
	const supervisorTypesService = new SupervisorTypeService();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		supervisorTypesService.getList().then((res) => {
			if (res?.status) {
				const response = res?.data?.data
					?.filter((info) => info?.isActive)
					?.map((info) => {
						return {
							label: info?.name,
							value: info?.id,
						};
					});
				setTypeOptions(response);
			}
		});
		new EmployeesService().getManager("SUPERVISOR").then((res) => {
			if (res?.status) {
				const response = res?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.id,
					};
				});
				setSupervisorsOptions(response);
			}
		});
	}, []);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			supervisorService?.find(item?.id).then((res) => {
				if (res?.status) {
					const response = res?.data?.data;
					setFormData({
						id: item?.id,
						supervisor: {
							label: response?.employeeName,
							value: response?.employeeId,
						},
						type: {
							label: response?.supervisorTypeName,
							value: response?.supervisorTypeId,
						},
						isActive: item?.isActive,
					});
				}
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			employeeId: formData?.supervisor?.value,
			supervisorTypeId: formData?.type?.value,
			isActive: formData?.isActive,
		};

		setLoading(true);
		if (isAdd) {
			supervisorService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			supervisorService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.supervisor}
								</p>
								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">
								{Translate[lang]?.supervisor}
							</label>
							<Select
								value={formData?.supervisor}
								name="supervisor"
								placeholder={Translate[lang]?.select}
								options={supervisorsOptions}
								onChange={(e) => setFormData({ ...formData, supervisor: e })}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">{Translate[lang]?.type}</label>
							<Select
								value={formData?.type}
								name="type"
								placeholder={Translate[lang]?.select}
								options={typeOptions}
								onChange={(e) => setFormData({ ...formData, type: e })}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.job_title}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddSupervisorModal;
