import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddSubjectsModal from "./AddSubjectsModal";
import DepartmentService from "../../../services/DepartmentService";
import "./style.scss";
import { useLocation, useNavigate } from "react-router-dom";
import EntertainmentSubjectsService from "../../../services/EntertainmentSubjectsService";
import AddCategoriesModal from "./AddCategoriesModal";
import EntertainmentCategoriesService from "../../../services/EntertainmentCategoriesService";
import EntertainmentProgramsService from "../../../services/EntertainmentProgramsService";
import TableModal from "./TableModal";

const Entertainment = () => {
	const [data, setData] = useState([]);
	const [addModal, setAddModal] = useState(false);
	const [tableModal, setTableModal] = useState(false);
	const [addCategoryModal, setAddCategoryModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(1);
	const [type, setType] = useState("");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const entertainmentSubjectsService = new EntertainmentSubjectsService();
	const entertainmentCategoriesService = new EntertainmentCategoriesService();
	const entertainmentProgramsService = new EntertainmentProgramsService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const navigate = useNavigate();
	const location = useLocation();

	useEffect(() => {
		if (!!location?.state) {
			setType(location?.state);
		} else if (!type) {
			setType("subjects");
		}
	}, []);

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => {
						setType("subjects");
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "subjects" ? "activeP" : ""}>
					{Translate[lang].subjects}
				</p>
				<p
					onClick={() => {
						setType("categories");
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "categories" ? "activeP" : ""}>
					{Translate[lang].categories}
				</p>
				<p
					onClick={() => {
						setType("programs");
						setShouldUpdate((prev) => !prev);
					}}
					className={type === "programs" ? "activeP" : ""}>
					{Translate[lang].programs}
				</p>
			</div>
			{type === "categories" && (
				<div className="note">
					<p className="mb-0">
						<span className="mx-2">{Translate[lang].note}:</span>
						{Translate[lang].subjects_note}
					</p>
				</div>
			)}
			{type === "programs" && (
				<div className="note">
					<p className="mb-0">
						<span className="mx-2">{Translate[lang].note}:</span>
						{Translate[lang].programs_note}
					</p>
				</div>
			)}
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div className="add-department">
						{
							<Button
								variant="primary"
								className="me-2 h-75"
								onClick={() => {
									if (type !== "programs") {
										setItem({});
										if (type === "subjects") setAddModal(true);
										if (type === "categories") setAddCategoryModal(true);
									} else {
										navigate("/entertainment/add-program");
									}
								}}>
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add} {Translate[lang][type]}
							</Button>
						}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											{type === "categories" && (
												<th>
													<strong>{Translate[lang]?.category}</strong>
												</th>
											)}
											{type === "programs" && (
												<>
													<th>
														<strong>{Translate[lang]?.program_name}</strong>
													</th>
													<th>
														<strong>{Translate[lang]?.branch}</strong>
													</th>
													{/* <th>
														<strong>{Translate[lang]?.hall}</strong>
													</th> */}
													{/* <th>
														<strong>{Translate[lang]?.subjects}</strong>
													</th> */}
												</>
											)}
											{type !== "programs" && (
												<th>
													<strong>{Translate[lang]?.subjects}</strong>
												</th>
											)}
											{type !== "categories" && (
												<th>
													<strong>{Translate[lang]?.age_group}</strong>
												</th>
											)}
											{type === "programs" && (
												<>
													<th>
														<strong>
															{Translate[lang]?.program_start_date}
														</strong>
													</th>
													<th>
														<strong>{Translate[lang]?.program_end_date}</strong>
													</th>
												</>
											)}
											{type === "subjects" && (
												<>
													<th>
														<strong>{Translate[lang]?.gender}</strong>
													</th>
													<th>
														<strong>{Translate[lang]?.status}</strong>
													</th>
												</>
											)}
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setTableModal={setTableModal}
													setItem={setItem}
													type={type}
													setAddModal={setAddModal}
													setAddCategoryModal={setAddCategoryModal}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{!!type && (
								<Pagination
									setData={setData}
									service={
										type === "subjects"
											? entertainmentSubjectsService
											: type === "categories"
											? entertainmentCategoriesService
											: entertainmentProgramsService
									}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
								/>
							)}
						</div>
					</Card>
				</Col>
			</Row>

			{addModal && (
				<AddSubjectsModal
					item={item}
					addModal={addModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddModal(false)}
				/>
			)}
			{tableModal && (
				<TableModal
					item={item}
					addModal={tableModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setTableModal(false)}
				/>
			)}
			{addCategoryModal && (
				<AddCategoriesModal
					item={item}
					addModal={addCategoryModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddCategoryModal(false)}
				/>
			)}
		</div>
	);
};

export default Entertainment;
